export const createItemData = (data, disabled, id = null) => {
    console.log(data, "data at edit");
    return data.map((item, index) => {
        return {
            type: item.type,
            registerName: item.registerName,
            labelName: item.labelName,
            required: item.required,
            className: item.className,
            errorMessages: {
                required: item.errorMessages.required,
                pattern: item.errorMessages.pattern,
            },
            id: item.id ?? id,
            disabled,
        };
    });
};

export const organizeData = (data, index) => {
    const { company_name, job_title, description, start_date, end_date, id } = data;
    return {
        [`company_name_${index}`]: company_name,
        [`job_title_${index}`]: job_title,
        [`start_date_${index}`]: start_date,
        [`end_date_${index}`]: end_date,
        [`description_${index}`]: description,
        // id,
    };
};
export const organizeEducationData = (data, index) => {
    const { institution_name, qualification, description, start_date, end_date } = data;
    return {
        [`institution_name_${index}`]: institution_name,
        [`qualification_${index}`]: qualification,
        [`start_date_${index}`]: start_date,
        [`end_date_${index}`]: end_date,
        [`description_${index}`]: description,
    };
};
export const createEducationDefaultData = (data = []) => {
    const tempData = [];
    for (let i = 0; i < data.length; i++) {
        tempData.push(organizeEducationData(data[i], i));
    }
    return tempData;
};
export const createDefaultData = (data = []) => {
    const tempData = [];
    console.log(data.length, "length");
    if (data.length) {
        for (let i = 0; i < data.length; i++) {
            tempData.push(organizeData(data[i], i));
        }
    }
    return tempData;
};

export const createJobHistoryList = (length, jobData) => {
    const tempArr = [];
    if (jobData.length) {
        for (let i = 0; i < length; i++) {
            tempArr.push(addJob(i, jobData[i].id, true));
        }
    } else {
        tempArr.push(addJob(0));
    }
    return tempArr;
};

export const createEducationHistoryList = (length, jobData) => {
    const tempArr = [];

    for (let i = 0; i < length; i++) {
        tempArr.push(addEducationDetails(i, jobData[i].id, true));
    }

    return tempArr;
};

