export const createProfileVerification = (data, computerSystems) => {
    console.log(data,"data on profileVerification");
    const cs_data = [];
    computerSystems.map((item, index) => {
        if (item.value) {
            cs_data.push({
                id: item.id,
            });
        }
    });
    const { reg_number, valid_till,rtw_valid_till } = data;
    return {
        reg_number,
        valid_till,
        rtw_valid_till,
        cs: JSON.stringify(cs_data),
    };
};
