export const structureAdminDocData = (documents = []) => {
    console.log(documents);
    if (!documents.length) {
        return PHARMACY_ADMIN_DOCS;
    } else {
        let data = [];
        let tempData = []
        let id = 2;
        documents.forEach((doc) => {
            if (doc.document_type == DOCUMENT_TYPE_NAME.OTHER) {
                data.push({
                    id: id,
                    title: doc.name || DOCUMENT_TYPE_NAME_ORG[doc.document_type],
                    uploadedFile: doc.document,
                    docId: doc.id,
                    document_type: doc.document_type,
                    disabled:true
                });               
                id++;
            }
        });
        PHARMACY_ADMIN_DOCS.forEach((item) => {
            const index = documents.findIndex((doc) => doc.document_type == item.document_type)
            if (index !== -1) {
                let newData = { ...item };
                newData.uploadedFile = documents[index].document;
                newData.docId = documents[index].id;
                newData.validTill= documents[index].valid_till || ''
                tempData.push(newData)
            }else{
                tempData.push(item)
            }
        });
        return [...tempData,...data];
    }
};

