export const getSelectedData = (allData = [], selectedData = []) => {
    let finalData = [];
    allData.forEach((item) => {
        let csInUseItem = selectedData.find((cs) => cs.id == item.id);
        if (csInUseItem) {
            finalData.push({ ...item, value: true });
        } else {
            finalData.push(item);
        }
    });
    return finalData;
};

