import React from "react";
import { useSelector } from "react-redux";
import { USER_TYPE } from "shared/Constants";
import PharmacyProfile from "../Profile/PharmacyProfile";
import Profile from "../Profile/Profile";

const ProfileRenderer = () => {
    const profile = useSelector((state) => state.common.profileData);
    console.log(profile, "profile Data", profile);
    const renderer = () => {
        switch (profile?.user_type) {
            case USER_TYPE.LOCUM: {
                return <Profile />;
            }
            case USER_TYPE.PHARMACY: {
                return <PharmacyProfile />;
            }
            default:
                return <div>Something went Wrong!</div>;
        }
    };
    return renderer();
};

export default ProfileRenderer;
