import CustomTextInput from "components/CustomTextInput";
import React, { useState } from "react";
import { INPUT_TYPES, STRINGS } from "shared/Constants";

function ApplyJobform({
    notesValue = "",
    priceValue = "",
    onChangeNote = () => {},
    onChangePrice = () => {},
    data = {},
    errorMessages = {},
    sendRequest = () => {},
}) {
    const [addNote, setAddNote] = useState(false);

    const disableApplyButton = () => {
        if (priceValue < 0 || priceValue >= 1000) {
            // If price value is outside of range, show error message
            return true;
        } else if (!(priceValue?.length || priceValue)) {
            return true;
        } else {
            return false;
        }
    };

    return (
        <form autoComplete="off">
            <div className="position-relative">
                <div className="position-relative">
                    {/* <em>£</em> */}

                    <div className="row align-items-center gx-2 mb-3">
                        <div className="col-md-4">
                            <label className="text-primary mb-0">Proposed Rate:</label>
                        </div>
                        <div className="col-md-8">
                            <div className="input-group">
                                <span class="input-group-text">£</span>
                                <input
                                    className="form-control"
                                    autoComplete="false"
                                    onChange={onChangePrice}
                                    value={priceValue || ""}
                                    titleText={"Proposed Rate:"}
                                    pattern={"[0-9]+"}
                                    placeholder={"Enter Proposed Rate..."}
                                    type={INPUT_TYPES.NUMBER}
                                    min={data?.start_rate}
                                    max={data?.end_rate}
                                    title={`Please enter a valid rate between ${data?.start_rate} and ${data?.end_rate} with up to two decimal places (e.g. 1234.56).`}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                {errorMessages ? (
                    <span className="error">{errorMessages}</span>
                ) : (
                    <span className="error">{""}</span>
                )}
            </div>
{/*
            <CustomTextInput
                onChange={onChangePrice}
                value={priceValue || ""}
                titleText={"Proposed Rate:"}
                pattern={"^([1-9]\\d{0,3}|0)(\\.\\d{1,2})?$"}
                placeholder={"Enter Proposed Rate..."}
                type={INPUT_TYPES.NUMBER}
                min={data?.start_rate}
                max={data?.end_rate}
                errorMessage={errorMessages}
                title={`Please enter a valid rate between ${data?.start_rate} and ${data?.end_rate} with up to two decimal places (e.g. 1234.56).`}
            /> */}
            <div className="checkbox mb-3">
                <input
                    checked={addNote}
                    type={INPUT_TYPES.CHECKBOX}
                    onChange={() => setAddNote((prev) => !prev)}
                />
                <label>{"Add a Note"}</label>
            </div>
            {addNote ? (
                <CustomTextInput
                    titleText={"Note"}
                    ShowTextArea={true}
                    value={notesValue || ""}
                    onChange={onChangeNote}
                    placeholder={STRINGS.ENTER_HERE}
                    className="form-control mb-3"
                />
            ) : null}
            <p className="text-primary ">
                <strong>Please Note:</strong> Requests for extra expense detailed in notes will be
                ignored.
            </p>
            <div className="text-center mt-4">
                <button
                    className={`btn btn-md btn-secondary ${disableApplyButton() ? "disabled" : ""}`}
                    onClick={sendRequest}
                >
                    Send Request
                </button>
            </div>
        </form>
    );
}

export default ApplyJobform;
