// profile verification api for document and available fields updates
function* getProfileVerification({ success = () => {}, fail = () => {} }) {
    try {
        yield put(startLoading());

        const { status, data = {} } = yield getRequest({
            API: API_URLS.PROFILE_VERIFICATION,
        });
        console.log(data, "data at api call");
        success(data);
    } catch (error) {
        console.log(error, "error");
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
        // console.log(error);
        // callback(JSON.stringify(error));
    } finally {
        yield put(stopLoading());
    }
}
