function* watchAuth() {
    yield all([
        takeLatest(REGISTER, register),
        takeLatest(LOGIN, login),
        takeLatest(GET_PROFILE_VERIFICATION, getProfileVerification),
        takeLatest(OTP_VERIFICATION, otpVerification),
        takeLatest(RESEND_OTP, resendOtp),
        takeLatest(FORGOT_PASSWORD, forgotPassword),
        takeLatest(GET_REGISTRATION_DETAILS, getRegistrationDetails),
        takeLatest(SAVE_REGISTRATION_DETAILS, saveRegistrationDetails),
        takeLatest(GET_ACCOUNT_DETAILS, getAccountDetails),
        takeLatest(SAVE_ACCOUNT_DETAILS, saveAccountDetails),
        takeLatest(SAVE_PROFILE_VERIFICATION, saveProfileVerification),
        takeLatest(GET_CS_IN_USE, getCsInUse),
        takeLatest(GET_ALL_DETAILS, getProfileDetails),
        takeLatest(GET_PHARMACY_ADMIN_DATA, getPharmacyAdminData),
        takeLatest(LOGOUT, logout),
        takeLatest(CONFIRM_PASSWORD, confirmPassword),
        takeLatest(RESET_PASSWORD, resetPassword),
        takeLatest(CONTACT_US, contactUs),
        takeLatest(CONTACT_US_DETAILS, contactUsDetails),
        takeLatest(ABOUT_US, aboutUs),
        takeLatest(PRIVACY_POLICY, privacyPolicy),
        takeLatest(COOKIE_POLICY, cookiePolicy),
        //
        takeLatest(FOOTER, footer),
        takeLatest(HEADER, header),
        takeLatest(TERMS_AND_CONDITIONS, termsAndConditions),
        takeLatest(GET_CONTACT_US, getContactUs),
        // getContactUs

        takeLatest(FETCH_JOB_LISTING, fetchJobListing),
        takeLatest(VIEW_JOB, viewJob),
        takeLatest(DELETE_JOB_POSTED, deleteJob),
        takeLatest(APPLY_JOB, applyJob),
    ]);
}

export default watchAuth;
