import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { SNACK_VARIANT } from "shared/Constants";
import {
    accountDetailsDefaultData,
    createInvoicePayload,
    getInititalValueInvoice,
    INVOICE_DETAILS_DATA,
} from "views/authenticated/constants";
import {
    getAccountDetails,
    locumProfileDetails,
    saveAccountDetails,
} from "../../../../redux/Actions/Auth";
import { STRINGS } from "../../../../shared/Constants";

function Invoice({}) {
    const dispatch = useDispatch();
    const profileDetails = useSelector((state) => state.common.locumProfileDetails);
    const accountDetails = useSelector((state) => state.common.locumProfileDetails?.invoiceDetails);
    const [defaultValues, setDefaultValues] = useState(accountDetailsDefaultData(accountDetails));
    const [msg, sendNotifications] = useNotification();
    console.log(accountDetails, "account details");
    const onSubmit = (data) => {
        const payload = createInvoicePayload(data);
        setDefaultValues(data);
        dispatch(
            locumProfileDetails({
                ...profileDetails,
                invoiceDetails: { ...getInititalValueInvoice(data) },
            }),
        );
        dispatch(
            saveAccountDetails(
                payload,
                (response) => {
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    useEffect(() => {
        setDefaultValues(accountDetailsDefaultData(accountDetails));
    }, [accountDetails]);
    return (
        <div className="tab_content">
            <CustomForm
                onSubmit={onSubmit}
                reCaptcha={false}
                title={STRINGS.SAVE}
                formData={INVOICE_DETAILS_DATA}
                defaultValues={{ ...defaultValues }}
            />
        </div>
    );
}

export default Invoice;
