import React from "react";
import './style.scss';

function TabNavigation({ tabsData = [], activeTab, onClick = () => { } }) {
    return (
        <ul className="d-flex list-unstyled m-0 p-0 nav-tabs">
            {tabsData.map((item) => (
                <li
                    key={item.id}
                    id={item.id}
                    onClick={onClick}
                    className={`show ${item.id === activeTab && "active"} `}
                >
                    {item.title}
                </li>
            ))}
        </ul>
    );
}

export default React.memo(TabNavigation);
