import { SELECTED_TAB, SET_TEMPLATE_DATA } from "redux/Actions/PharmacyAdminProfile";

const initialState = {
    templateData: {},
    selectedTab: null
};
const pharmacyAdminReducer = (state = initialState, action) => {
    switch (action.type) {
       case SET_TEMPLATE_DATA: {
        return {
            ...state,
            templateData: action.data
        }
       }
       case SELECTED_TAB: {
        return {
            ...state,
            selectedTab: action.data
        }
       }
        default: {
            return state;
        }
    }
};

export default pharmacyAdminReducer;