import React, { useLayoutEffect, useEffect, useState } from "react";
import Footer from "components/Footer/Footer";
import Header from "components/Header";
import HeroBanner from "components/HeroBanner";
import Newsletter from "components/Newsletter";
import { HEADER_DATA } from "./constants";
import { Images } from "shared/Images";
import "./styles.scss";
import RegisterUser from "components/RegisterUser";
import Register from "components/OurTeam";
import FunctionalTable from "components/CustomFunctionalTable";
import { useHistory } from "react-router-dom";
import { ROUTES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { useDispatch, useSelector } from "react-redux";
import {
    fromVerificationCode,
    getAllDetails,
    getHomePageData,
    getPharmacyAdminData,
    otpVerification,
    setHomepage,
} from "redux/Actions/Auth";
import { store } from "redux/Store";
import useNotification from "hooks/Notifications";
import { getDocsUrl } from "shared/Utilities";
import ReactHtmlParser from "react-html-parser";
import ContactUs from "views/ContactUs";
import ContactForm from "views/ContactUs/contactForm";

function HomePage(props) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const verificationCode = useSelector((state) => state.common.fromVerficationCode);
    const profile = useSelector((state) => state.common.profileData);
    const token = useSelector((state) => state.auth.token);
    const homepageData = useSelector((state) => state.dynamic.homepage);
    const getData = () => {
        dispatch(
            getHomePageData(
                (response) => {
                    dispatch(setHomepage(response.data));
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    useEffect(() => {
        if (store.getState().common.fromVerficationCode) {
            history.replace(ROUTES.PROFILE);
            dispatch(fromVerificationCode(false));
        }
        if (!Object.keys(homepageData).length) {
            getData();
        }
        if(token){
            dispatch(getAllDetails());
            dispatch(getPharmacyAdminData())
        }
        getData();
    }, []);

    return (
        <main className="main">
            <Header headerList={HEADER_DATA} />
            <HeroBanner bannerSection={homepageData?.sections?.[0]} />

            {!Object.keys(profile).length && (
                <section className="user_login_sec text-center">
                    <RegisterUser />
                </section>
            )}

            <section className="about_sec">
                <div className="container px-xl-5">
                    <div className="row align-items-center">
                        <div className="col-md-4">
                            <figure className="mb-md-0 about_pic">
                                <img
                                    src={
                                        getDocsUrl(homepageData?.sections?.[1]?.files?.[0]?.file) ||
                                        Images.AboutPic
                                    }
                                    alt="About"
                                    className="img-fluid"
                                />
                            </figure>
                        </div>
                        <div className="col-md-8 ps-lg-5">
                            <div className="about_summary">
                                <h2 className="h2 mb-3">
                                    {homepageData?.sections?.[1]?.section_name
                                        ? ReactHtmlParser(homepageData?.sections?.[1]?.section_name)
                                        : STRINGS.ABOUT_US}
                                </h2>
                                {homepageData?.sections?.[1]?.section_content ? (
                                    ReactHtmlParser(homepageData?.sections?.[1]?.section_content)
                                ) : (
                                    <p> {STRINGS.ABOUT_US_CONTENT}</p>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section className="teamSec">
                <Register ourTeamData={homepageData?.sections?.[2]} />
            </section>

            <ContactForm
                getInTouch={homepageData?.sections?.[3]?.section_name}
                content={homepageData?.sections?.[3]?.section_content}
            />

            <Newsletter
                section_name={homepageData?.sections?.[4]?.section_name}
                section_content={homepageData?.sections?.[4]?.section_content}
            />

            <Footer />
        </main>
    );
}

export default HomePage;
