import React from "react";
import { useSelector } from "react-redux";
import { USER_TYPE } from "shared/Constants";
import AdminManageProfile from "../AdminManageProfile";
import ManageProfile from "../ManageProfile";

const EditProfileRenderer = () => {
    const profile = useSelector((state) => state.common.profileData);
    console.log(profile, "profile");
    const renderer = () => {
        switch (profile?.user_type) {
            case USER_TYPE.LOCUM: {
                return <ManageProfile />;
            }
            case USER_TYPE.PHARMACY: {
                return <AdminManageProfile />;
            }
            default:
                return <></>;
        }
    };
    return renderer();
};

export default EditProfileRenderer;
