import { all, put, takeLatest } from "redux-saga/effects";
import { clearProfileData, setToken } from "redux/Actions/Auth";
import { NOTIFICATION_LISTING } from "redux/Actions/LocumAdmin";
import { API_URLS, STATUS_CODES } from "services/Api/Constants";
import { postRequest, updateAuthToken } from "shared/Axios";
import { objectToFormData } from "shared/utils";

function* fetchNotificationListing({ payload, success = () => {}, fail = () => {} }) {
    try {
        // yield put(startLoading());
        const formDataObj = objectToFormData(payload);

        const { status, data = {} } = yield postRequest({
            API: API_URLS.NOTIFICATION_LISTING,
            DATA: formDataObj,
        });
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        // yield put(stopLoading());
    }
}

function* locumAdminSaga() {
    yield all([takeLatest(NOTIFICATION_LISTING, fetchNotificationListing)]);
}

export default locumAdminSaga;
