import { ROUTES, SNACK_VARIANT, STRINGS, USER_TYPE } from "shared/Constants";

import CustomForm from "components/CustomForm";
import React, { useState } from "react";
import {
    payloadForRegister,
    pharmacyRegisterData,
    pharmacyRegisterDefault,
} from "views/authentication/constants";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { register } from "redux/Actions/Auth";
import useNotification from "hooks/Notifications";
import { STATUS_CODES } from "services/Api/Constants";

const PharmacyRegisterForm = (props) => {
    const dispatch = useDispatch();
    const history = useHistory();
    const [recaptchaToken, setRecaptchaToken] = useState("");
    const [captchaError, setCaptchaError] = useState(false);
    const [msg, sendNotification] = useNotification();
    const [reCaptchaVal, setRecaptchaVal] = useState(false)
    const onSubmit = (data) => {
        
        // if (recaptchaToken) {
            setCaptchaError(false);
            const payload = payloadForRegister(data, USER_TYPE.PHARMACY);
            console.log(data, "main data")
            dispatch(
                register(
                    payload,
                    (response) => {
                        sendNotification({
                            msg: response.data.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                        history.push(ROUTES.VERIFICATION_CODE, {
                            email: data.email,
                            isSignUp: true,
                        });
                    },
                    (error) => {
                        setRecaptchaVal(!reCaptchaVal)
                        sendNotification({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        // } else {
        //     setCaptchaError(true);
        // }
    };
    const recaptchaRes = (data) => {
        setRecaptchaToken(data);
    };
    
    return (
        <div className="pharamacy_register">
            <CustomForm
                key={"pharmacy-register-form"}
                onSubmit={onSubmit}
                formData={pharmacyRegisterData}
                defaultValues={pharmacyRegisterDefault}
                privacyPolicy={true}
                reCaptcha={false}
                onRecaptchaChange={recaptchaRes}
                captchaError={captchaError}
                size={"md"}
                reCaptchaVal={reCaptchaVal}
                title={STRINGS.SIGNUP}
            />
            
        </div>
    );
};

export default PharmacyRegisterForm;
