// libs
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useHistory } from "react-router-dom";

// widgets
import StaffDetails from "../components/StaffDetails";

// hooks
import useNotification from "hooks/Notifications";

// consts
import { ADMIN_ROUTES, ROUTES_TITLES, SNACK_MESSAGES, SNACK_VARIANT, STRINGS, initialDaysData } from "shared/Constants";

// actions
import { createStaff, getBranchListing, getStaffByEmail } from "redux/Actions/PharmacyAdminDashboard";
import { checkJobTimingDataValid, structureBranchData, validJobTimingDataForStartEnd } from "shared/Utilities";
import { deepCopy } from "shared/utils";

const CreateStaff = () => {
    const dispatch = useDispatch();
    const history = useHistory();
    const profile = useSelector((state) => state.common.profileData);
    const [msg, sendNotification] = useNotification();
    const [staffDetails, setStaffDetails] = useState({ locumDetails: { email: null, username: null, phone: null }, branch: null, status: 1, isSelectAll: true, timings: null });
    const [branchesData, setBranchesData] = useState([]);
    const [searchedData, setSearchedData] = useState([])
    const [jobTimings, setJobTimings] = useState(initialDaysData || []);
    const [showBranches, setShowBranches] = useState(false);
    const [isAllSelected, setIsAllSelected] = useState(false);
    const [branchKeyWord, setBranchKeyword] = useState(null)

    useEffect(() => {
        fetchBranchListingCall();
    }, []);

    const fetchBranchListingCall = () => {
        let payload = {
            isJobTimingsRequired: 1
        };
        dispatch(
            getBranchListing(
                payload,
                (res = {}) => {
                    setBranchesData(structureBranchData(res.data, "branch_name"));
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };

    const fetchStaffDetailsByEmail = () => {
        let payload = {
            email: staffDetails?.locumDetails?.email || null,
            isDebounceCall : true
        };
        dispatch(
            getStaffByEmail(
                payload,
                (res = {}) => {
                    setStaffDetails((prev) => {
                        return { ...prev, locumDetails: res?.data || {} }
                    });

                },
                (err) => {
                    setStaffDetails((prev) => {
                        return { ...prev, locumDetails: { ...prev.locumDetails, id: "", username: "", phone: "" } }
                    });
                    // sendNotification({
                    //     msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                    //     variant: SNACK_VARIANT.ERROR,
                    // });
                },
            ),
        );
    };

    const createStaffCall = () => {
        if (!staffDetails?.locumDetails?.email?.trim() || !staffDetails?.locumDetails?.id || !staffDetails?.branch?.length) {
            sendNotification({
                msg: "please enter valid staff email & branch ",
                variant: SNACK_VARIANT.ERROR,
            });
            return;
        };

        const payload = {
            locum_id: staffDetails?.locumDetails?.id,
            branch_id: JSON.stringify(staffDetails?.branch),
            status: 1,
            // days: transformedJobTimings
        }
        dispatch(
            createStaff(payload,
                (res = {}, type) => {
                    sendNotification({
                        msg: res?.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                    history.push(ADMIN_ROUTES.MANAGE_STAFF);
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };

    const handleStaffInputsChange = ({ target: { name = "", value = "" } }) => {
        if (name) {
            setStaffDetails((prev) => {
                return {
                    ...prev, locumDetails: {
                        ...prev.locumDetails,
                        [name]: value
                    }
                }
            });
        }
    };

    const handleBranchInputChange = (value, item) => {
        setStaffDetails((prev) => {
            if(value.target.checked){
                return {
                    ...prev,
                    branch: prev.branch ? [...prev.branch, item.id] : [item.id],
                }
            }else{
                return {
                    ...prev,
                    branch: prev.branch.filter(branchId => branchId != item.id)
                }
            }
        });
        setBranchesData(prevState => {
            let copyPrevState = deepCopy(prevState);
            let index = copyPrevState.findIndex(branch => branch.id == item.id);
            copyPrevState[index].value = value.target.checked;
            return copyPrevState;
        })
    };

    const handleSetIsSelectedAll = (val) => {
        setStaffDetails((prev) => {
            return { ...prev, isSelectAll: val }
        });
    };

    const handleCreateStaff = () => {
        createStaffCall();
    };

    const handleAllSelected = (e) => {
        setIsAllSelected(e.target.checked);
        setBranchesData(prevState => {
            let copyPrevState = deepCopy(prevState);
            copyPrevState.forEach(item => item.value = e.target.checked);
            return copyPrevState;
        })
        setStaffDetails((prev) => {
            if(e.target.checked){
                return {
                    ...prev,
                    branch: branchesData.map(item => item.id),
                }
            }else{
                return {
                    ...prev,
                    branch: []
                }
            }
        });
    
    }

    const handleBranchChangeKeyword = (e) => {
        setBranchKeyword(e.target.value)
    }

    const handleBranchSearchData = () => {
        let key = branchKeyWord.toLowerCase();

        // Use filter() method to create a new array with matching objects
        if (key?.length) {
        const searchResults = branchesData.filter((branch) => {
            // Extract the values from the object and convert them to lowercase
            const name = branch.title.toLowerCase();

            // Check if the keyword is present in the name or category
            return name.includes(key);
        });
        // Return the search results
        setSearchedData(searchResults);
        } else {
            setSearchedData([])
        }       
    }

    const handleRemoveSelectedBranch = (branchToRemove) => {
        setStaffDetails((prev) => {           
            return {
                ...prev,
                branch: prev.branch.filter(branchId => branchId != branchToRemove.id)
            }            
        });
        setBranchesData(prevState => {
            let copyPrevState = deepCopy(prevState);
            let index = copyPrevState.findIndex(branch => branch.id == branchToRemove.id);
            copyPrevState[index].value = false;
            return copyPrevState;
        })
    }

    useEffect(() => {
        if(branchesData.filter(item => item.value).length == branchesData.length){
            setIsAllSelected(true);
        }else{
            setIsAllSelected(false);
        }
    }, [branchesData])

    useEffect(() => {
        setBranchKeyword("")
    }, [showBranches])

    return (
        <>           
            <nav aria-label="breadcrumb" className="breadcrumb_nav">
                <ol class="breadcrumb">
                    <li className="breadcrumb-item"><Link to={ADMIN_ROUTES.MANAGE_STAFF}>{ROUTES_TITLES.MANAGE_STAFF}</Link></li>
                    <li class="breadcrumb-item active">{STRINGS.ADD_NEW_STAFF}</li>
                </ol>
            </nav>

            <hr className="divider" />
            <StaffDetails
                fetchStaffDetailsByEmail={fetchStaffDetailsByEmail}
                staffDetails={staffDetails}
                handleStaffInputsChange={handleStaffInputsChange}
                profile={profile}
                branchesData={branchesData}
                handleBranchInputChange={handleBranchInputChange}
                handleSetIsSelectedAll={handleSetIsSelectedAll}
                setJobTimings={setJobTimings}
                jobTimingsData={staffDetails?.branch?.details?.job_timings || []}
                disabledInputs={{
                    username: true,
                    phone: true,
                }}
                branchKeyWord={branchKeyWord}
                handleBranchChangeKeyword={handleBranchChangeKeyword}
                handleBranchSearchData={handleBranchSearchData}
                handleAllSelected={handleAllSelected}
                isAllSelected={isAllSelected}
                showBranches={showBranches}
                setShowBranches={setShowBranches}
                searchedData={searchedData}
                handleRemoveSelectedBranch={handleRemoveSelectedBranch}
            />
            <div className="text-center">
                <button
                    className="btn btn-md btn-primary"
                    onClick={handleCreateStaff}
                >
                    {STRINGS.SAVE}
                </button>
            </div>
        </>
    );
};

export default CreateStaff;
