import {
    ERROR_MESSAGES,
    ERROR_TYPES_GENERATION,
    INPUT_TYPES,
    LABEL,
    PASSWORD_TYPES,
    STRINGS,
    THEME,
} from "shared/Constants";
import React, { useEffect, useRef, useState } from "react";
import { getMaxDate, minDate, typeSelection, validateConfirmPassword } from "shared/Utilities";
import "react-phone-input-2/lib/style.css";
import CustomTextInput from "components/CustomTextInput";
import { Images } from "shared/Images";
import { useForm } from "react-hook-form";
const CustomJobForm = ({
    onSubmit = () => {},
    renderSubmitButton = true,
    id = "hook-form",
    formData = [],
    formClass = "col-md-6",
    defaultValues = {},
    setDefaultValues = () => {},
    onDeleteClick = () => {},
    onDocChange = () => {},
}) => {
    const [typeToggle, setTypeToggle] = useState({
        password: INPUT_TYPES.PASSWORD,
        [STRINGS.CONFIRM_PASSWORD]: INPUT_TYPES.PASSWORD,
        old_password: INPUT_TYPES.PASSWORD,
    });
    const {
        register,
        handleSubmit,
        watch,
        reset,
        formState,
        formState: { errors, isSubmitSuccessful },
    } = useForm({ defaultValues: { ...defaultValues } });

    useEffect(() => {
        if (Object.keys(defaultValues).length) {
            reset({ ...defaultValues });
        } else {
            reset({
                company_name_0: "",
                job_title_0: "",
                start_date_0: "",
                end_date_0: "",
                description_0: "",
            });
        }
    }, [defaultValues]);

    // React.useEffect(() => {
    //     if (isSubmitSuccessful) {
    //         reset({ ...defaultValues });
    //     }
    // }, [formState, reset]);
    return (
        <form
            id={id}
            onSubmit={handleSubmit((data, event) => {
                onSubmit(data, event);
            })}
        >
            <div className="card mb-4">
                <div className="card-body">
                    {formData.map((formItem, formIndex) => {
                        return (
                            <div className="d-flex align-items-center box_clone position-relative mb-3">
                                <div className="bg-white p-4 rounded">
                                    <div
                                        // className={`${formClass}`}
                                        className="row"
                                    >
                                        {formItem.map((formNestedItem, formNestedIndex) => {
                                            return (
                                                <div
                                                    className={`${formClass} mb-4`}
                                                    key={`${formNestedItem.registerName} + ${formNestedIndex}`}
                                                >
                                                    <CustomTextInput
                                                        readOnly={formNestedItem?.disabled}
                                                        id={formNestedItem?.registerName}
                                                        placeholder={formNestedItem?.placeholder}
                                                        cursor={
                                                            formNestedItem?.cursor
                                                                ? formNestedItem?.cursor
                                                                : ""
                                                        }
                                                        accept={
                                                            formNestedItem.type === "file"
                                                                ? "application/pdf"
                                                                : "*"
                                                        }
                                                        type={
                                                            PASSWORD_TYPES.includes(
                                                                formNestedItem.registerName,
                                                            )
                                                                ? typeToggle[
                                                                      formNestedItem.registerName
                                                                  ]
                                                                : formNestedItem.type
                                                        }
                                                        icon={
                                                            PASSWORD_TYPES.includes(
                                                                formNestedItem.registerName,
                                                            )
                                                                ? typeToggle[
                                                                      formNestedItem.registerName
                                                                  ] === INPUT_TYPES.PASSWORD
                                                                    ? Images.HidePassword
                                                                    : Images.ShowPassword
                                                                : null
                                                        }
                                                        onIconClick={(data) =>
                                                            typeSelection(data, setTypeToggle)
                                                        }
                                                        iconStyle={"formIcon"}
                                                        titleText={formNestedItem.labelName}
                                                        {...register(formNestedItem.registerName, {
                                                            pattern: formNestedItem.pattern,
                                                            required: formNestedItem.required,
                                                            validate: (val) =>
                                                                validateConfirmPassword(
                                                                    val,
                                                                    formNestedItem.registerName,
                                                                    watch,
                                                                ),
                                                        })}
                                                    />
                                                    {ERROR_TYPES_GENERATION.includes(
                                                        errors[formNestedItem.registerName]?.type,
                                                    ) && (
                                                        <span className="error">
                                                            {
                                                                formNestedItem.errorMessages[
                                                                    errors[
                                                                        formNestedItem.registerName
                                                                    ]?.type
                                                                ]
                                                            }
                                                        </span>
                                                    )}
                                                </div>
                                            );
                                        })}
                                    </div>
                                </div>
                                {formData.length != 1 && (
                                    <button
                                        type="button"
                                        className="job_action_btn btn-xs btn btn-danger"
                                        onClick={() => onDeleteClick(formItem[0].registerName)}
                                    >
                                        <img src={Images.TrashIcon} width="16" />
                                    </button>
                                )}
                            </div>
                        );
                    })}
                </div>
            </div>
        </form>
    );
};

export default CustomJobForm;
