import { all, put, takeLatest } from "redux-saga/effects";
import {
    clearProfileData,
    CV_MANAGE,
    DELETE_DOCUMENTS,
    DELETE_JOB_DETAILS,
    GET_CV_DATA,
    GET_EDUCATION_DETAILS,
    GET_JOB_DETAILS,
    GET_PROFILE,
    SAVE_EDUCATION_DETAILS,
    SAVE_JOB_DETAILS,
    setToken,
    startLoading,
    stopLoading,
    UPDATE_PROFILE,
    UPLOAD_DOCUMENTS,
} from "redux/Actions/Auth";
import { API_URLS, STATUS_CODES } from "services/Api/Constants";
import { deleteRequest, postRequest, updateAuthToken } from "shared/Axios";
import { objectToFormData } from "shared/utils";
import { getRequest } from "../../shared/Axios";
import { DELETE_EDUCATION_DETAILS, LOGOUT, setProfileData } from "../Actions/Auth";

// upload documents api
function* sendDocuments({ payload, success = () => {}, fail = () => {} }) {
    try {
        yield put(startLoading());
        const formDataObj = objectToFormData({
            document_type: payload.document_type,
            name: payload.name,
            document: payload.document,
        });
        const { status, data = {} } = yield postRequest({
            API: API_URLS.UPLOAD_DOCUMENTS,
            DATA: formDataObj,
        });
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
}
