// libs
import React, { useState } from 'react';
import PhoneInput from 'react-phone-input-2';

// wigets
import CustomTextInput from 'components/CustomTextInput';
import JobTiming from 'components/JobTimings';
import ReactSelect from 'components/ReactSelect';
import SearchBar from 'components/SearchBar';
import ListingModal from 'components/ListingModal';
import { INPUT_TYPES, LABEL, STRINGS } from 'shared/Constants';
import { Images } from 'shared/Images';

const StaffDetails = ({
  fetchStaffDetailsByEmail = () => {},
  staffDetails = {},
  handleStaffInputsChange = () => {},
  profile = {},
  branchesData = {},
  handleBranchInputChange = () => {},
  setJobTimings = () => {},
  handleSetIsSelectedAll = () => {},
  jobTimingsData = [],
  disabledInputs = {},
  isView = false,
  isEdit = false,
  handleDisabledHours = () => {},
  handleDisabledMinutes = () => {},
  showBranches = false,
  setShowBranches = () => {},
  isAllSelected = false,
  setIsAllSelected = () => {},
  handleAllSelected = () => {},
  branchKeyWord = null,
  handleBranchChangeKeyword = () => {},
  handleBranchSearchData = () => {},
  searchedData = [],
  handleRemoveSelectedBranch = () => {},
}) => {
  const enabledDays = jobTimingsData?.map((day) => day?.day);
  return (
    <div className="pt-2 admin_view">
      <div className="field_white mb-3">
        <h6 className="label_title mb-4">
          <span>User Details</span>
        </h6>
        <div className="row">
          <div className="col-md-4 mb-4">
            <label>Staff Email</label>
            {isEdit ? (
              <CustomTextInput
                type="text"
                name="email"
                placeholder={'Enter Here...'}
                value={staffDetails?.locumDetails?.email}
                disabled={disabledInputs?.email}
              />
            ) : (
              <SearchBar
                searchAPI={fetchStaffDetailsByEmail}
                keyword={staffDetails?.locumDetails?.email}
                handleChangeKeyword={handleStaffInputsChange}
                name={'email'}
                placeholder="Enter Here..."
                isSearchBarIcon={false}
                debounceTime={500}
                disabled={disabledInputs?.email}
              />
            )}
          </div>
          <div className="col-md-4 mb-4">
            <label>Staff Username</label>
            <CustomTextInput
              type="text"
              name="username"
              placeholder={'Enter Here...'}
              onChange={handleStaffInputsChange}
              value={staffDetails?.locumDetails?.username}
              disabled={disabledInputs?.username}
            />
          </div>
          <div className="col-md-4 mb-4">
            <label>Phone Number</label>
            <div className="country_dropdown">
              <PhoneInput
                inputProps={{
                  name: 'phone',
                }}
                value={staffDetails?.locumDetails?.phone || null}
                name="phone"
                onChange={handleStaffInputsChange}
                disabled={disabledInputs?.phone}
              />
            </div>
          </div>
        </div>
      </div>

      <div className="field_white mb-3">
        <h6 className="label_title mb-4">
          <span>Pharmacy Details</span>
        </h6>
        <div className="row">
          <div className="col-md-4 mb-4">
            <label>Pharmacy</label>
            <CustomTextInput
              type="text"
              placeholder={'Pharmacy Name'}
              value={profile?.pharmacy_info?.pharmacy_name || ''}
              disabled={true}
            />
          </div>
          <div className="col-md-4 mb-4 arrow_direction_right">
            <label>Branch</label>
            {isView ? (
              <CustomTextInput
                type="text"
                placeholder={'Branch'}
                value={staffDetails?.branch || null}
                disabled={true}
              />
            ) : (
              <>
                <div className="branch_selection_view">
                <div className="tag_list">
                  {branchesData
                    .filter((item) => item.value)
                    .slice(0, 3)
                    .map((branch) => {
                      return (
                        <>
                          <div className="branch_item">
                            <span>{branch.title}</span>
                            <em>
                              <img
                                width="7"
                                height={7}
                                src={Images.greenCrossIcon}
                                alt="deleteicon"
                                onClick={() =>
                                  handleRemoveSelectedBranch(branch)
                                }
                              />
                            </em>
                          </div>
                        </>
                      );
                    })}
                </div>
                  <div className="more_item">
                    {branchesData.filter((item) => item.value).length > 3
                      ? `+${
                          branchesData.filter((item) => item.value).slice(3)
                            .length
                        } branch`
                      : ''}
                  </div>
                  <em
                    onClick={() => {
                      setShowBranches(true);
                    }}
                    className="show_hide_pwd"
                  >
                    <img width="12" src={Images.ArrowDown} alt="" />
                  </em>
                </div>
                <ListingModal
                  data={branchKeyWord ? searchedData : branchesData}
                  setShow={setShowBranches}
                  show={showBranches}
                  handleSelection={handleBranchInputChange}
                  heading={STRINGS.SELECT_BRANCH_MESSAGE}
                  multipleSelection={true}
                  buttonType={INPUT_TYPES.CHECKBOX}
                  keyword={branchKeyWord}
                  handleChangeKeyword={handleBranchChangeKeyword}
                  handleSearchData={handleBranchSearchData}
                  handleSelectAll={handleAllSelected}
                  isAllSelect={isAllSelected}
                />
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default StaffDetails;
