import { LOGIN, SET_AUTH_TOKEN, SET_TOKEN } from "../Actions/Auth";
import { ACTION_STATES } from "../Actions/ActionStates";

const initialState = {
    token: null,
    authToken: null,
};
const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_TOKEN: {
            return {
                ...state,
                authToken: action.token,
            };
        }
        case SET_TOKEN: {
            return {
                ...state,
                token: action.token,
            };
        }
        default: {
            return state;
        }
    }
};

export default authReducer;
