import useNotification from "hooks/Notifications";
import React, { useEffect } from "react";
import { useState } from "react";
import { Document, Page } from "react-pdf/dist/esm/entry.webpack";
import { useDispatch } from "react-redux";
import { startLoading, stopLoading } from "redux/Actions/Auth";
import { SNACK_VARIANT, STRINGS } from "shared/Constants";
import "./styles.scss";
// install npm i react-pdf
// if you face issue then install npm i react-pdf@5.0.0 , for clarity check https://github.com/wojtekmaj/react-pdf/issues/1043
// if you have webpack version less than 4 install(npm i file-loader)

function DocumentViewer({ viewDoc, setViewDoc, isVerified, onLoadSuccess = () => {} }) {
    const [allPages, setAllPages] = useState([]);
    const [msg, sendNotifications] = useNotification();
    const dispatch = useDispatch();
    const onDocumentLoadSuccess = ({ numPages }) => {
        const pages = []
        for (let p = 1; p <= numPages; p++){
            pages.push(p);
        }
        setAllPages(pages);
        dispatch(stopLoading());
        onLoadSuccess();
    };
    useEffect(() => {
        dispatch(startLoading());
    }, []);
    return (
        <>
            <Document
                file={viewDoc}
                onLoadSuccess={onDocumentLoadSuccess}
                onLoadError={() => {
                    dispatch(stopLoading());
                    sendNotifications({
                        msg: STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                    setViewDoc(false);
                }}
                className="doc_view_div"
            >
                {
                    allPages.map((pageItem,pageIndex) => <Page key={`page-${pageItem}`} pageNumber={pageItem} wrap={true} />)
                }
                
            </Document>
        </>
    );
}

export default DocumentViewer;
