import { ROUTES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import React, { useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { forgotPassword } from "redux/Actions/Auth";
import CustomForm from "components/CustomForm";
import { forgotPasswordData } from "../constants";
import useNotification from "hooks/Notifications";
import "./styles.scss";

function ForgotPassword(props) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [email, setEmail] = useState("");
    const emailDefault = { email: "" };
    const onLoginClick = () => {
        history.push(ROUTES.LOGIN);
    };
    const onSubmit = (data) => {
        const payload = {
            email: data.email,
        };
        dispatch(
            forgotPassword(
                payload,
                (response) => {
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    return (
        <div className="overflow-hidden front_form min-vh-100">
            <div className="ls_form forgot_sec">
                <div className="text-center d-block mb-4">
                    <h1 className="h5 text-capitalize mb-2">{STRINGS.FORGET_PASSWORD}</h1>
                    <p className="text-mute">{STRINGS.RECOVER_PASSWORD}</p>
                </div>
                <CustomForm
                    formData={forgotPasswordData}
                    reCaptcha={false}
                    onSubmit={onSubmit}
                    defaultValues={emailDefault}
                    title={STRINGS.SEND}
                />
                <div className="text-center mt-3">
                    <span>
                        Back to <span className="link_text" onClick={onLoginClick}>{STRINGS.LOGIN}</span>
                    </span>
                </div>
            </div>
        </div>
    );
}

export default ForgotPassword;
