import ActivateDeactivate from "components/ActivateDeactivate";
import CommonTable from "components/CommonTable";
import CustomButton from "components/CustomButton";
import DeleteModal from "components/DeleteModal";
import { Pagination } from "components/Pagination";
import SearchBar from "components/SearchBar";
import useNotification from "hooks/Notifications";
import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { deleteBranches, getBranchListing, postBranchStatus } from "redux/Actions/PharmacyAdminProfile";
import { ADMIN_ROUTES, ROUTES, SNACK_MESSAGES, SNACK_VARIANT, SORTKEYS, STRINGS } from "shared/Constants";
import TableView from "./TableView";

const PharmacyBranches = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotification] = useNotification();
    const profile = useSelector((state) => state.common.profileData);
    const [multipleSelectedData, setMultipleSelectedData] = useState([]);
    const [data, setData] = useState([]);
    const [totalItems, setTotalItems] = useState(0);
    const [itemsPerPage, setItemsPerPage] = useState(15);
    const [activePage, setActivePage] = useState(1)
    const [keyword, setKeyword] = useState(null);
    const [showDeleteModal, setShowDeleteModal] = useState(false)
    const [deleteBranchData, setDeleteBranchData] = useState({})
    const [toggle, setToggle] = useState({
        key: "",
        direction: SORTKEYS["ascending"],
    })

    const handleToggle = (key, direction) => {
        setToggle({ key, direction})
    }

    const handleAddNewBranchClick = () => {
        history.push({
            pathname: ADMIN_ROUTES.MANAGE_BRANCH,
            state: { addNewBranch: true, pharmacyName: profile?.pharmacy_info?.pharmacy_name },
        });
    };

    const toggleDeleteModal = (value) => {
        setShowDeleteModal(prevState => !prevState);
        console.log(value, showDeleteModal)
        if (value && !showDeleteModal) {
            setDeleteBranchData(value)
        } else {
            setDeleteBranchData({})
        }
    }

    const fetchData = useCallback(({ currentPageForPayload = activePage, itemsCountPerPageForPayload = itemsPerPage, sorting = toggle.key ? 7 : "", sort_type = toggle.direction } = {}) => {
        let payload = {
            page: currentPageForPayload,
            search: keyword ? keyword : "",
            limit: itemsCountPerPageForPayload,
            sorting: sorting ? sorting : "",
            sort_type: sort_type
        };
        dispatch(
            getBranchListing(
                payload,
                (res, type) => {
                    setData(res.data);
                    setTotalItems(res.count);
                    setMultipleSelectedData([])
                },
                (err) => {
                    sendNotification({
                        msg: err?.data?.message
                            ? err.data.message
                            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    }, [dispatch, itemsPerPage, activePage, keyword, sendNotification]);

    const handleChangeKeyword = (e) => {
        setKeyword(e.target.value);
    };

    const handleStatusChange = (id, status) => {
        let arr = [];
        if (multipleSelectedData.length && !id) {
            arr = multipleSelectedData.map((i, idx) => {
                return {
                    id: i,
                    status: status,
                };
            });
        } else {
            arr = [
                {
                    id: id,
                    status: status ? 1 : 0,
                },
            ];
        }

        let payload = {
            branches_status: arr
        }

        dispatch(
            postBranchStatus(
                payload,
                (response) => {
                    fetchData();
                    sendNotification({
                        msg: response?.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (err) => {
                    console.log(err, "err")
                    sendNotification({
                        msg: err
                            ? err
                            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
        console.log(arr, "final arrr")
    };

    const handleDelete = () => {
        console.log(deleteBranchData, "delete branch data");

        let arr = [];
        if (multipleSelectedData.length && !deleteBranchData.id) {
            arr = multipleSelectedData.map((i, idx) => {
                return {
                    id: i,
                };
            });
        } else {
            arr = [
                {
                    id: deleteBranchData.id,
                },
            ];
        }

        let payload = {
            branches: arr
        }

        dispatch(deleteBranches(
            payload,
            (response) => {
                fetchData();
                sendNotification({
                    msg: response?.message,
                    variant: SNACK_VARIANT.SUCCESS,
                });
                toggleDeleteModal();
            },
            (err) => {
                sendNotification({
                    msg: err
                        ? err
                        : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
                toggleDeleteModal();
            },
        ))
        console.log(arr, "final arrr")
    }

    const restPageCountAndCallAPI = (itemsCountPerPageForPayload = itemsPerPage) => {
        fetchData({ currentPageForPayload: 1, itemsCountPerPageForPayload })
        setActivePage(1);
    }

    const handleItemsPerPageChange = (i) => {
        setItemsPerPage(i);
        restPageCountAndCallAPI(i)
    }

    const handlePageChange = (i) => {
        setActivePage(i?.selected + 1);
        fetchData({ currentPageForPayload: i?.selected + 1 })
    }

    const handleSearchData = () => {
        //settign active page to 1st page on search same doing for calling API
        restPageCountAndCallAPI()
    }

    const fetchDataAccordingToSort = (key, direction) => {
        handleToggle(key, direction);
        fetchData({currentPageForPayload: 1, sorting : 7 , sort_type: direction})
        setActivePage(1);
    }

    useEffect(() => {
        fetchData();
    }, []);

    return (
        <>
            <hr className="divider mt-0" />
            <div className="d-flex justify-content-end admin_action flex-wrap">
                {multipleSelectedData.length ? (
                    <>
                        <ActivateDeactivate
                            statusChange={handleStatusChange}
                            title={"Select Action"}
                        />
                        <button
                            className="btn btn-sm btn-danger ms-2"
                            onClick={() => { toggleDeleteModal() }}
                        >
                            {STRINGS.DELETE}
                        </button>
                    </>
                ) : null}
                <SearchBar
                    searchAPI={handleSearchData}
                    keyword={keyword}
                    handleChangeKeyword={handleChangeKeyword}
                />
                <CustomButton
                    className="ms-2 btn btn-sm btn-secondary"
                    type="button"
                    title={STRINGS.ADD_MORE_BRANCH}
                    onClick={handleAddNewBranchClick}
                />
            </div>
            <div className="tab_content pt-0">
                <CommonTable>
                    <TableView
                        data={data}
                        multipleSelectedData={multipleSelectedData}
                        setMultipleSelectedData={setMultipleSelectedData}
                        handleStatusChange={handleStatusChange}
                        toggleDeleteModal={toggleDeleteModal}
                        toggle={toggle}
                        fetchDataAccordingToSort={fetchDataAccordingToSort}
                    />
                </CommonTable>
                <Pagination
                    itemsCount={totalItems}
                    itemsPerPage={itemsPerPage}
                    handlePageChange={handlePageChange}
                    handleItemsPerPageChange={handleItemsPerPageChange}
                    activePage={activePage}
                />
            </div>

            <DeleteModal
                show={showDeleteModal}
                toggleModal={toggleDeleteModal}
                onClick={handleDelete}
                medium={true}
            />
        </>
    );
};

export default PharmacyBranches;
