const PharmacyInfo = ({ handlePharmacyDetailsChange, pharmacyInfoData }) => {
  const dispatch = useDispatch();
  const [msg, sendNotifications] = useNotification();
  const profile = useSelector((state) => state.common.profileData);
  const [country, setCountry] = useState('');
  const [countryError, setCountryError] = useState(false);
  const [CSInUse, setCSInUse] = useState([]);
  const [viewDoc, setViewDoc] = useState(false);
  const [documentUrl, setDocumentUrl] = useState('');
  const [modalOpacity, setModalOpacity] = useState(false);
  const [tableData, setTableData] = useState(
    structureAdminDocData(profile?.pharmacy_info?.documents)
  );
  const onSubmit = (values) => {
    let payload = { ...values };
    payload.license_valid_till = tableData[0].validTill
    payload.insurance_valid_till = tableData[1].validTill
    payload.csInUse = payload.csInUse.map((item) => ({ id: item.id }));
    dispatch(
      postPharmacyInfo(
        payload,
        (response) => {
          sendNotifications({
            msg: response.message,
            variant: SNACK_VARIANT.SUCCESS,
          });
          dispatch(getPharmacyAdminData());
        },
        (error) => {
          sendNotifications({
            msg: error,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
    console.log(values);
  };
  const selectCountry = (val) => {
    setCountry(val);
  };

  const onCheckboxSelect = (e) => {
    setCSInUse((prevState) => {
      let copyPrevState = [...prevState];
      let index = copyPrevState.findIndex((item) => item.id == e.target.id);
      copyPrevState[index].value = e.target.checked;
      return copyPrevState;
    });
  };

  const handleUpload = (event) => {
    if (event.target.files[0].size < FIVE_MB_SIZE) {
      const title = tableData[event.target.id].title;
      const payload = createUploadPayload(
        event.target.files[0],
        tableData[event.target.id].document_type,
        title,
        true
      );
      dispatch(
        uploadPharmacyDocument(
          payload,
          (response) => {
            const tempArr = [...tableData];
            const tempData = tempArr[event.target.id];
            tempData.uploadedFile = response.data.document;
            tempData.docId = response.data.id;
            tempData.document_type = response.data.document_type;
            tempData.disabled = true;
            tempArr[event.target.id] = tempData;
            sendNotifications({
              msg: response.message,
              variant: SNACK_VARIANT.SUCCESS,
            });
            profile.pharmacy_info.documents = [
              ...getInititalValueDocuments(tempArr),
            ];
            dispatch(setProfileData(profile));
            setTableData(tempArr);
            event.target.value = null;
          },
          (error) => {
            sendNotifications({
              msg: error || STRINGS.SOMETHING_WENT_WRONG,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        )
      );
    } else {
      sendNotifications({
        msg: ERROR_MESSAGES.SIZE,
        variant: SNACK_VARIANT.ERROR,
      });
    }
    event.target.value = null;
  };
  const handleDeleteClick = (itemId,splice=false) => {
    dispatch(
        deletePharmacyDocument(
            { id: tableData[itemId].docId },
            (response) => {
                const tempArr = [...tableData];
                const tempData = tempArr[itemId];
              tempData.uploadedFile = null;
              tempData.validTill = ''
                if (tableData[itemId].document_type == DOCUMENT_TYPE_NAME.OTHER) {
                    tempData.disabled = false
                }
                tempArr[itemId] = tempData;
                if (splice) {
                    tempArr.splice(itemId,1)
                }
                sendNotifications({
                    msg: response.message,
                    variant: SNACK_VARIANT.SUCCESS,
                });
                profile.pharmacy_info.documents = [...getInititalValueDocuments(tempArr)]
                dispatch(setProfileData(profile))
                setTableData(tempArr);
            },
            (error) => {
                sendNotifications({
                    msg: error || STRINGS.SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
            },
        ),
    );
  };
  const onDeleteClick = (event) => {
      const itemId = event.target.id;
      handleDeleteClick(itemId);
  };
  const onRowDeleteClick = (index) => {
      if (tableData[index].uploadedFile) {
          handleDeleteClick(index,true);
      } else {
          tableData.splice(index, 1);
          setTableData([...tableData]);
      }
  };
  const onRowClick = (index) => {
    if (tableData[index].uploadedFile) {
      setModalOpacity(true);
      setDocumentUrl(getDocsUrl(tableData[index].uploadedFile));
      setViewDoc(true);
    }
  };
  const onAddServiceClick = () => {
    window.scrollTo(0, document.body.scrollHeight);
    setTableData((prev) => [
      ...prev,
      {
        id: prev.length + 1,
        title: SERVICES.OTHER,
        uploadedFile: null,
        document_type: 4,
        disabled: false,
      },
    ]);
  };
  const onInputChange = (index, event) => {
    tableData[index].title = event.target.value;
    setTableData([...tableData]);
  };
  const onValidTillChange = (index, event) => {
    console.log(event.target.value, index, "eventntr")
    let newData = [...tableData]
    newData[index].validTill = event.target.value;
    setTableData([...newData])
}
console.log(tableData, "tableData")
  useEffect(() => {
    handlePharmacyDetailsChange(
      'csInUse',
      CSInUse.filter((item) => item.value)
    );
  }, [CSInUse]);

  useEffect(() => {
    dispatch(
      getPharmacyCSInUse((response) => {
        let data = structureCSInUseData(response.data);
        let updatedCSInUse = [];
        data.forEach((item) => {
          let csInUseItem = pharmacyInfoData?.csInUse?.find(
            (cs) => cs.id == item.id
          );
          if (csInUseItem) {
            updatedCSInUse.push({ ...item, value: true });
          } else {
            updatedCSInUse.push(item);
          }
        });
        setCSInUse(updatedCSInUse);
      })
    );
  }, []);
  return (
    <div className="tab_content">
      <CustomForm
        id={'edit-pharmacy'}
        onSubmit={onSubmit}
        reCaptcha={false}
        formData={REGISTRATION_DETAILS_DATA}
        renderSubmitButton={false}
        selectCountry={selectCountry}
        country={pharmacyInfoData.country}
        countryError={countryError}
        defaultValues={{ ...pharmacyInfoData }}
        handleStateDataChange={handlePharmacyDetailsChange}
      />
      <div className="col-12 mb-3">
        <label className="mb-3">{LABEL.COMPUTER_SYSTEMS}</label>
        <div className="row">
          <CustomCheckBox
            checkBoxData={CSInUse}
            onCheckboxSelect={onCheckboxSelect}
          />
        </div>
      </div>
      <div className="col-12">
        <div className="d-flex align-items-center justify-content-between mb-2">
          <label className="mb-0">{LABEL.PROFILE_DOCUMENTS}</label>
          <CustomButton
            title={STRINGS.ADD_MORE_SERVICES}
            onClick={onAddServiceClick}
            className="btn btn-sm btn-outline-secondary add_btn"
            Icon={Images.PlusIcon}
          />
        </div>
      </div>
      <div className="col-12 mb-4">
        <div className="table-responsive">
          <CustomTable
            data={tableData}
            activeTab={STRINGS.PROFILE_TABLE}
            handleUpload={handleUpload}
            onClick={onRowClick}
            onDeleteClick={onDeleteClick}
            onRowDeleteClick={onRowDeleteClick}
            onInputChange={onInputChange}
            onValidTillChange={onValidTillChange}
            tableDefaultRows={2}
          />
        </div>
        {viewDoc && (
          <DocumentViewerModal
            viewDoc={viewDoc}
            modalOpacity={modalOpacity}
            documentUrl={documentUrl}
            setViewDoc={setViewDoc}
            onLoadSuccess={() => {
              setModalOpacity(false);
            }}
          />
        )}
      </div>
      <div className="text-center mb-4">
        <CustomButton
          form={'edit-pharmacy'}
          className="btn btn-md btn-primary"
          type="submit"
          title={STRINGS.UPDATE}
        />
      </div>
    </div>
  );
};

export default PharmacyInfo;
