import { ACTION_STATES } from "../Actions/ActionStates";
import { LOGOUT } from "../Actions/Auth";
import authReducer from "./Auth";
import { combineReducers } from "redux";
import commonReducer from "./CommonReducer";
import errorReducer from "./Api/ErrorReducer";
import loadingReducer from "./Api/LoadingReducer";
import dynamicContentReducer from "./DynamicContentReducer";
import pharmacyAdminReducer from "./PharmacyAdminReducer";
import locumAdminReducer from "./LocumAdminReducer";

const appReducer = combineReducers({
    auth: authReducer,
    loading: loadingReducer,
    common: commonReducer,
    dynamic: dynamicContentReducer,
    error: errorReducer,
    pharmacyAdmin: pharmacyAdminReducer,
    locumAdmin: locumAdminReducer,

});

const rootReducer = (state, action) => {
    if (action.type === LOGOUT + ACTION_STATES.SUCCESS) {
        state = undefined;
    }
    return appReducer(state, action);
};

export default rootReducer;
