// libs
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useHistory } from "react-router-dom";

// widgets
import CustomTextInput from "components/CustomTextInput";
import PermisionsTable from "../components/PermissionsTable";

// hooks
import useNotification from "hooks/Notifications";

// consts
import { ADMIN_ROUTES, ROUTES_TITLES, SNACK_MESSAGES, SNACK_VARIANT, STRINGS } from "shared/Constants";

// actions
import { createGroup, fetchPermissions } from "redux/Actions/PharmacyAdminDashboard";

const CreateGroup = () => {
    const dispatch = useDispatch();
    const history = useHistory();
    const [msg, sendNotification] = useNotification();
    const [headerCheckboxData, setHeaderCheckboxData] = useState({can_view: false, can_edit: false, can_create: false, can_delete: false});
    const [groupDetails, setGroupDetails] = useState({ group_name: "", group_discription: "", status: 0, permissions: [] });

    useEffect(() => {
        getPermissionsCall();
    }, []);


    useEffect(() => {
        if(groupDetails?.permissions?.length){
            checkSelectedAll(groupDetails?.permissions);
        }
    }, [groupDetails?.permissions]);

    const getPermissionsCall = () => {
        dispatch(
            fetchPermissions(
                (res = {}, type) => {
                    setGroupDetails((data) => {
                        return {
                            ...data, permissions: res.data
                        }
                    })
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };

    const createGroupCall = () => {
        if (!groupDetails?.group_discription?.trim() || !groupDetails?.group_name?.trim()) {
            sendNotification({
                msg: "please enter group name and group description",
                variant: SNACK_VARIANT.ERROR,
            });
            return;
        }
        dispatch(
            createGroup(groupDetails,
                (res = {}, type) => {
                    sendNotification({
                        msg: res?.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                    history.push(ADMIN_ROUTES.GROUP_PERMISSIONS);
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };

    const checkSelectedAll = (currentPermissions) => {
        // TODO refactor and make it concise
        const canViewCheckbox = currentPermissions?.filter(perm => perm["can_view"] == true)?.length == currentPermissions?.length;
        const canCreateCheckbox = currentPermissions?.filter(perm => perm["can_create"] == true)?.length == currentPermissions?.length;
        const canEditCheckbox = currentPermissions?.filter(perm => perm["can_edit"] == true)?.length == currentPermissions?.length;
        const canDeleteCheckbox = currentPermissions?.filter(perm => perm["can_delete"] == true)?.length == currentPermissions?.length;
    
        const transformedAllCheckboxData = {
            "can_view": canViewCheckbox ,
            "can_create": canCreateCheckbox,
            "can_edit": canEditCheckbox,
            "can_delete": canDeleteCheckbox
        };
        setHeaderCheckboxData(transformedAllCheckboxData);
    };

    const handleInputChange = ({ target: { name, value = "", checked = false, type } }, permissionId = "") => {
        if (type == "text") {
            setGroupDetails(details => { return { ...details, [name]: value } })
        } else {
            setGroupDetails(details => {
                const updatedPermissions = details?.permissions?.map((permission) => {
                    if (permission?.id == permissionId) {
                        return {
                            ...permission,
                            [name]: checked
                        }
                    }
                    return permission;
                });
                return { ...details, permissions: updatedPermissions };
            });
        }
    };

    const handleCreateGroup = () => {
        createGroupCall();
    };

    const handleMultipleSelect = ({ target: { checked } }, type) => {
        // TODO refactor
        setHeaderCheckboxData((prev) => {
            return {
                ...prev,
                [type]: checked
            }
        });

        setGroupDetails(details => {
            const updatedPermissions = details?.permissions?.map((permission) => {
                return {
                    ...permission,
                    [type]: checked
                }
            });
            return { ...details, permissions: updatedPermissions };
        });
    };

    return (
        <>
            <nav aria-label="breadcrumb" className="breadcrumb_nav">
                <ol class="breadcrumb">
                    <li className="breadcrumb-item"><Link to={ADMIN_ROUTES.GROUP_PERMISSIONS}>{ROUTES_TITLES.GROUP_PERMISSIONS}</Link></li>
                    <li class="breadcrumb-item active">{STRINGS.ADD_NEW_GROUP}</li>
                </ol>
            </nav>

            <hr className="divider" />

            <div className="card rounded mb-4">
                <div className="card-body bg-white">
                    <h6 className="h6 mb-0">Group Details:</h6>
                    <div className="row">
                        <div className="col-md-4 mt-4">
                            <label>Group Name</label>
                            <CustomTextInput
                                type="text"
                                name="group_name"
                                placeholder={"Group Name"}
                                value={groupDetails?.group_name || ""}
                                onChange={handleInputChange}
                            />
                        </div>
                        <div className="col-md-4 mt-4">
                            <label>Description</label>
                            <CustomTextInput
                                type="text"
                                name="group_discription"
                                placeholder={"Descriptions"}
                                onChange={handleInputChange}
                                value={groupDetails?.group_discription || ""}
                            />
                        </div>
                    </div>
                </div>
            </div>

            <PermisionsTable
                permissionsData={groupDetails?.permissions || []}
                onChangeInput={handleInputChange}
                handleMultipleSelect={handleMultipleSelect}
                headerCheckboxData={headerCheckboxData}
            />

            <div className="text-center">
                <button
                    className="btn btn-md btn-primary"
                    onClick={handleCreateGroup}
                >
                    {STRINGS.SAVE}
                </button>
            </div>
        </>
    );
};

export default CreateGroup;
