import Accordion from "components/Accordion";
import BasicTable from "components/CustomBasicTable";
import Footer from "components/Footer/Footer";
import Header from "components/Header";
import ProfileAttributes from "components/ProfileAttributes";
import ProfileBankDetails from "components/ProfileBankDetails";
import ProfileDescription from "components/ProfileDescription";
import ProfileDetails from "components/ProfileDetails";
import { ROUTES, SNACK_VARIANT, STRINGS, USER_TYPES } from "shared/Constants";
import React, { useEffect, useState } from "react";
import FunctionalTable from "components/CustomFunctionalTable";
import { Images } from "shared/Images";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getAllDetails, locumProfileDetails } from "../../../redux/Actions/Auth";
import useNotification from "hooks/Notifications";
import {
    bankDetails,
    educationHistoryDetails,
    jobHistoryDetails,
    profileDetailsData,
    uploadedDocuments,
} from "../constants";
import { getDocsUrl } from "shared/Utilities";
import DocumentViewerModal from "components/DocumentViewer";

const imageURL =
    "https://images.unsplash.com/photo-1575936123452-b67c3203c357?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8aW1hZ2V8ZW58MHx8MHx8&w=1000&q=80";

const tableHeader = ["#", "Name", "Qualification", "Start Date", "End Date", "Description"];

const tableHeaderFunc = ["#", "Document Name", "Uploaded Document", "Status", "Action"];

const handleActionClick = (a, b) => {
    console.log(a, b);
};

function Profile({
    profileData = {},
    personalData = {},
    hideCSinUse = false,
    hideBankDetails = false,
    isEditable = true,
    isLocumUserProfile = true,
}) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    // const profileDetails = useSelector((state) => state.common.locumProfileDetails);
    const [profileDetail, setProfileDetails] = useState(Object.keys(profileData).length ? profileData : {});

    const personal_information = useSelector((state) => state.common.locumProfileDetails?.profile);
    const [personal_info, setPersonal_info] = useState(
        Object.keys(personalData)?.length ? personalData : personal_information,
    );

    const [viewDoc, setViewDoc] = useState(false);
    const [documentUrl, setDocumentUrl] = useState("");
    const [modalOpacity, setModalOpacity] = useState(false);
    const [documents, setDocuments] = useState([]);

    console.log(profileDetail," DATTATATATAT")
    useEffect(() => {
        if(!isLocumUserProfile){
            setProfileDetails(profileData)
        }
    }, [profileData])

    useEffect(() => {
        if(isLocumUserProfile){
            dispatch(
                getAllDetails(
                    (response) => {
                        dispatch(locumProfileDetails(response.data));
                        setProfileDetails(response.data)
                        sendNotifications({
                            msg: response.data.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        }
    }, []);

    useEffect(() => {
        setDocuments(profileDetail?.profileVerification?.documents);
    }, [JSON.stringify(profileDetail)]);
    
    const handleDocumentClick = (a, b) => {
        if (a.document) {
            setDocumentUrl(getDocsUrl(a.document));
            setModalOpacity(true);
            setViewDoc(true);
        }
    };
    return (
        <main className="main">
            {/* <Header /> */}

            <div className="inner_content view_profile_sec">
                <div className="container">
                    <div className="d-flex align-items-center justify-content-between">
                        <div className="title_h1 mb-4">
                            <h1 className="h6">View Profile</h1>
                        </div>
                        {isEditable ? (
                            <button
                                className="btn btn-xs btn-primary mb-4"
                                onClick={() => {
                                    history.push(ROUTES.MANAGE_PROFILE);
                                }}
                            >
                                <img src={Images.EditIcon} alt="" width={20} />
                            </button>
                        ) : null}
                    </div>
                    <ProfileDetails
                        data={profileDetailsData(profileDetail)}
                        imageURL={getDocsUrl(personal_info?.photo)}
                        rating={3}
                        verifiedName={profileDetail?.verified_document}
                        profileCompleteness={profileDetail?.profile_completeness}
                        totalExp={profileDetail?.experience}
                    />
                    {hideCSinUse ? null : (
                        <ProfileAttributes
                            title={"Computer Systems Familiar With:"}
                            attributes={profileDetail?.profileVerification?.cs}
                        />
                    )}
                    {hideBankDetails ? null : (
                        <ProfileBankDetails
                            title={"Bank Details"}
                            data={bankDetails(profileDetail?.invoiceDetails)}
                        />
                    )}
                    <ProfileDescription
                        title={"Descriptions"}
                        description={profileDetail?.cvManage?.personal_description || ""}
                    />

                    <section className="accordin_sec">
                        <Accordion title={"Education"}>
                            <BasicTable
                                theadData={tableHeader}
                                tbodyData={educationHistoryDetails(
                                    profileDetail?.cvManage?.edu_history,
                                )}
                            />
                        </Accordion>
                        <Accordion title={"Experience"}>
                            <BasicTable
                                theadData={tableHeader}
                                tbodyData={jobHistoryDetails(profileDetail?.cvManage?.job_history)}
                            />
                        </Accordion>
                    </section>

                    <div className="card">
                        <div className="card-body">
                            <h2 className="h6 text-captialize">Uploaded Documents</h2>

                            <FunctionalTable
                                theadData={tableHeaderFunc}
                                tbodyData={uploadedDocuments(
                                    documents,
                                    // profileDetail?.profileVerification?.documents,
                                    // getUniqueDocumentTypes(data.documents),
                                )}
                                handleDocumentClick={handleDocumentClick}
                                handleActionClick={handleActionClick}
                                type={USER_TYPES.USER}
                            />
                        </div>
                    </div>
                </div>
            </div>
            {viewDoc && (
                <DocumentViewerModal
                    viewDoc={viewDoc}
                    documentUrl={documentUrl}
                    setViewDoc={setViewDoc}
                    modalOpacity={modalOpacity}
                    onLoadSuccess={() => {
                        setModalOpacity(false);
                    }}
                />
            )}
            <Footer />
        </main>
    );
}

export default Profile;
