import useNotification from "hooks/Notifications";
import React, { useState } from "react";
import {
    STRINGS,
    ROUTES,
    SNACK_VARIANT,
    INPUT_TYPES,
    LABEL,
    REGEX,
    ERROR_MESSAGES,
} from "../../../shared/Constants";
import { useHistory, useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import CustomForm from "components/CustomForm";
import CustomTextInput from "../../../components/CustomTextInput/index";
import { Images } from "../../../shared/Images";
import CustomButton from "components/CustomButton";
import { forgotPassword, confirmPassword } from "../../../redux/Actions/Auth";

function ResetPassword({}) {
    const history = useHistory();
    const dispatch = useDispatch();
    const params = useParams();
    const [msg, sendNotifications] = useNotification();
    const [showNewPassword, setShowNewPassword] = useState(false);
    const [confirmNewPassword, setConfirmNewPassword] = useState(false);
    const [errorMessage, setErrorMessage] = useState({
        confirm_password: "",
        password: "",
    });
    const [password, setPassword] = useState({
        newPassword: "",
        confirmNewPassword: "",
    });
    // const onLoginClick = () => {
    //     history.push(ROUTES.LOGIN);
    // };
    const onSubmit = (event) => {
        if (!password.newPassword || !password.confirmNewPassword || errorMessage.password || errorMessage.confirm_password) {
            // !password.newPassword && setErrorMessage(prev => {
            //     return {
            //         ...prev,
            //         password:ERROR_MESSAGES.PASSWORD_VALID
            //     }
            // })
            // !password.confirmNewPassword && setErrorMessage(prev => {
            //     return {
            //         ...prev,
            //         confirm_password:ERROR_MESSAGES.PASSWORD_VALID
            //     }
            // })
            sendNotifications({
                msg: ERROR_MESSAGES.CHECK_PASSWORD,
                variant: SNACK_VARIANT.ERROR
            })
            return 
        }
        if (!errorMessage.password && !errorMessage.confirm_password) {
            let data = {
                uid: params.uid,
                token: params.token,
                password: password.confirmNewPassword,
            };
            dispatch(
                confirmPassword(
                    data,
                    (response) => {
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                        history.push(ROUTES.LOGIN);
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        }
    };
    // let data = {
    //     uid: params.uid,
    //     token: params.token,
    //     password: newPass,
    //   };
    const onChange = (event) => {
        if (event.target.id === "password") {
            if (!event.target.value.length) {
                setErrorMessage((prev) => {
                    return {
                        ...prev,
                        password: "",
                    };
                });
            }
            if (REGEX.PASSWORD.test(event.target.value)) {
                if (event.target.value === password.confirmNewPassword) {
                    setErrorMessage((prev) => {
                        return {
                            ...prev,
                            password: "",
                        };
                    });    
                }
                setPassword((prev) => {
                    return {
                        ...prev,
                        newPassword: event.target.value,
                    };
                });
                setErrorMessage((prev) => {
                    return {
                        ...prev,
                        password: "",
                    };
                });
            } else {
                setErrorMessage((prev) => {
                    return {
                        ...prev,
                        password: ERROR_MESSAGES.CONFIRM_PASSWORD_VALID,
                    };
                });
            }
        } else {
            // if (REGEX.PASSWORD.test(event.target.value)) {
            if (!event.target.value.length) {
                setErrorMessage((prev) => {
                    return {
                        ...prev,
                        confirm_password: "",
                    };
                });
            }
            console.log(event.target.value,password.newPassword,"value");
            if (event.target.value == password.newPassword) {
                setPassword((prev) => {
                    return {
                        ...prev,
                        confirmNewPassword: event.target.value,
                    };
                });
                setErrorMessage((prev) => {
                    return {
                        ...prev,
                        confirm_password: "",
                    };
                });
            } else {
                setErrorMessage((prev) => {
                    return {
                        ...prev,
                        confirm_password: ERROR_MESSAGES.CONFIRM_PASSWORD_MATCHED,
                    };
                });
            }
            // } else {
            //     setErrorMessage((prev)=>{return {
            //         ...prev,
            //         confirm_password: ERROR_MESSAGES.CONFIRM_PASSWORD_VALID
            //     }})
            // }
        }
        // setUserData({ ...userData, [event.target.id]: event.target.value });
    };
    return (
        <div className="overflow-hidden front_form min-vh-100">
            <div className="ls_form forgot_sec">
                <div className="text-center d-block mb-4">
                    <h1 className="h5 text-capitalize mb-2">{STRINGS.RESET_PASSWORD}</h1>
                    <p className="text-mute">{STRINGS.NEW_PASSWORD}</p>
                </div>
                <div className="mb-4">
                    <CustomTextInput
                        id={"password"}
                        // icon={Images.USER}
                        // iconStyle={"iconStyle"}
                        onIconClick={() => {
                            setShowNewPassword(!showNewPassword);
                        }}
                        icon={showNewPassword ? Images.ShowPassword : Images.HidePassword}
                        type={showNewPassword ? INPUT_TYPES.TEXT : INPUT_TYPES.PASSWORD}
                        // type={showPassword ? STRINGS.PASSWORD_ID : INPUT_TYPES.TEXT}
                        errorMessage={errorMessage.password}
                        titleText={LABEL.PASSWORD}
                        placeholder={STRINGS.ENTER_PASSWORD}
                        onChange={onChange}
                    />
                </div>
                <div className="mb-4">
                    <CustomTextInput
                        id={"confirm_password"}
                        onIconClick={() => {
                            setConfirmNewPassword(!confirmNewPassword);
                        }}
                        type={confirmNewPassword ? INPUT_TYPES.TEXT : INPUT_TYPES.PASSWORD}
                        icon={confirmNewPassword ? Images.ShowPassword : Images.HidePassword}
                        titleText={LABEL.CONFIRM_PASSWORD}
                        errorMessage={errorMessage.confirm_password}
                        placeholder={STRINGS.ENTER_PASSWORD}
                        onChange={onChange}
                    />
                </div>
                <div className="text-center">
                    <CustomButton
                        className="btn btn-md btn-primary"
                        onClick={onSubmit}
                        title={STRINGS.PASSWORD_RESET}
                    />
                </div>
            </div>
        </div>
    );
}

export default ResetPassword;
