// fetch staff listing api used in manage staff
function* fetchStaffListing({ payload = {}, success = () => {}, fail = () => {} }) {
    const { page = 1, limit, search = "", orderBy, filterType, branch = ""} = payload; 
    let apiUrl = `${API_URLS.FETCH_STAFF_LISTING}?page=${page}&limit=${limit}&search=${search}&orderBy=${orderBy ?? 1}`;
    if(filterType){
        apiUrl += `&filterType=${filterType}`;
    };
    if(branch){
        apiUrl += `&branch=${branch}`;
    }
    try {
        yield put(startLoading());
        const { status, data = {} } = yield getRequest({
            API: apiUrl,
        });
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
};

