// create vacancy api
function* updateVacancy({ payload, success = () => {}, fail = () => {} }) {
    try {
        yield put(startLoading());
        let api = payload?.vacancyId ? `${API_URLS.CREATE_VACANCY}${payload?.vacancyId}/` : API_URLS.CREATE_VACANCY;
        if(payload?.vacancyId) {
            const { status, data = {} } = yield putRequest({
                API: api,
                DATA: payload.data
            });
            success(data);
        }else{
            const { status, data = {} } = yield postRequest({
                API: api,
                DATA: payload.data
            });
            success(data);
        }
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
};

