import "./styles.scss";

import {
    checkLoginMsg,
    ERROR_MESSAGES,
    INPUT_TYPES,
    ROUTES,
    SNACK_VARIANT,
    STRINGS,
} from "shared/Constants";
import React, { useState } from "react";
import { loginClick, renderFacebookButton } from "shared/Utilities";

import CustomButton from "components/CustomButton";
import CustomModal from "components/CustomModal";
import CustomTextInput from "components/CustomTextInput";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import { Images } from "shared/Images";
import { useGoogle } from "hooks/GoogleLogin";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { login } from "redux/Actions/Auth";
import useNotification from "hooks/Notifications";

function Login(props) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotification] = useNotification();
    const { googleResponse, googleError, GoogleSignin } = useGoogle();
    const [userData, setUserData] = useState({
        email: "",
        password: "",
    });
    const [errorMessage, setErrorMessage] = useState({
        email: "",
        password: "",
    });
    const [visible, setVisible] = useState(false);
    const [showPassword, setShowPassword] = useState(true);
    const [checkbox, setCheckbox] = useState(false);

    const onChange = (event) => {
        setUserData({ ...userData, [event.target.id]: event.target.value });
    };
    console.log("Reached Here")
    const onSubmit = () => {
        const { emailMsg, passwordMsg } = loginClick(userData.email, userData.password);
        setErrorMessage({
            email: emailMsg,
            password: passwordMsg,
        });
        if (!checkLoginMsg(emailMsg, passwordMsg)) {
            const payload = { ...userData };
            dispatch(
                login(
                    payload,
                    (data) => {
                        console.log(data, "data data");
                        if (data.verified) {
                            sendNotification({
                                msg: data.message || "login",
                                variant: SNACK_VARIANT.SUCCESS,
                            });
                            history.push(ROUTES.HOMEPAGE);
                        } else {
                            sendNotification({
                                msg: data.message,
                                variant: SNACK_VARIANT.ERROR,
                            });
                            history.push(ROUTES.VERIFICATION_CODE, { email: userData.email });
                        }
                    },
                    (error) => {
                        console.log(error, "error")
                        sendNotification({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        }
        // setVisible(true);
        // setVerificationContainer(true);
    };

    const onIconClick = () => {
        setShowPassword(!showPassword);
    };

    const onCheckboxPress = () => {
        setCheckbox(!checkbox);
    };

    const onRegisterNow = () => {
        history.push(ROUTES.SIGNUP);
    };
    const responseFacebook = (data) => {
        console.log(data);
    };
    const renderFacebook = () => (
        <FacebookLogin
            appId={process.env.REACT_APP_FACEBOOK_APP_ID}
            callback={responseFacebook}
            render={(renderProps) => renderFacebookButton(renderProps, "buttonStyle")}
        />
    );
    const renderGoogle = () => {
        return (
            <CustomButton
                title={STRINGS.GOOGLE}
                Icon={Images.GoogleIcon}
                onClick={GoogleSignin}
                className="m-1 w-100 btn btn-md btn-outline-secondary"
            />
        );
    };
    const onForgotClick = () => {
        history.push(ROUTES.FORGOT_PASSWORD);
    };
    const onCloseModal = () => {
        setVisible(false);
    };
    const onLogoClick = () => {
        history.push(ROUTES.HOMEPAGE);
    };
    const handleKeyDown = (event) => {
        if (event.keyCode === 13) {
          event.preventDefault();
          onSubmit()
        }
      };
    return (
        <div className="overflow-hidden front_form min-vh-100">
            <div className="ls_form">
                <div className="text-center d-block mb-4">
                    <a className="logo_front" onClick={onLogoClick}>
                        <img src={Images.LOGO} alt="Locumbay" width={200} />
                    </a>
                    <h1 className="h6 text-capitalize">{STRINGS.LOGIN}</h1>
                </div>
                <div className="mb-4">
                    <CustomTextInput
                        id={STRINGS.EMAIL_ID}
                        icon={Images.USER}
                        iconStyle={"iconStyle"}
                        errorMessage={errorMessage.email}
                        titleText={STRINGS.EMAIL}
                        placeholder={STRINGS.ENTER_HERE}
                        onChange={onChange}
                        onKeyDown={handleKeyDown}
                    />
                </div>
                <div className="mb-4">
                    <CustomTextInput
                        id={STRINGS.PASSWORD_ID}
                        icon={!showPassword ? Images.ShowPassword : Images.HidePassword}
                        iconStyle={"iconStyle"}
                        titleText={STRINGS.PASSWORD}
                        errorMessage={errorMessage.password}
                        type={showPassword ? STRINGS.PASSWORD_ID : INPUT_TYPES.TEXT}
                        placeholder={STRINGS.ENTER_PASSWORD}
                        onChange={onChange}
                        onIconClick={onIconClick}
                        onKeyDown={handleKeyDown}
                    />
                </div>

                <div className="mb-4 d-flex flex-wrap align-items-center justify-content-between">
                    <div className="checkbox">
                        {/* // <input
                        //     type="checkbox"
                        // // className={checkbox ? "checkbox-fill" : "checkbox"}
                        // // onChange={onCheckboxPress}
                        // />
                        // <label>{STRINGS.REMEMBER}</label> */}
                    </div>

                    <span className="pointer link_text" onClick={onForgotClick}>
                        {STRINGS.FORGOT_PASSWORD}
                    </span>
                </div>

                <div className="mb-4 text-center">
                    <CustomButton
                        title="Login"
                        className="btn btn-md btn-primary"
                        onClick={onSubmit}
                    />
                </div>

                <div className="seperator mb-4">
                    <span>{STRINGS.OR}</span>
                </div>

                <div className="d-flex flex-wrap flex-md-nowrap justify-content-between text-center mb-3">
                    {renderGoogle()}
                    {renderFacebook()}
                </div>

                {/* <div>{STRINGS.RESEND_ACTIVATION}</div> */}

                <div className="text-center text-mute">
                    {STRINGS.ACCOUNT_STRING}
                    <a className="link_text ps-2" onClick={onRegisterNow}>
                        {STRINGS.REGISTER_NOW}
                    </a>
                </div>
            </div>
            <CustomModal isOpen={visible} handleToggle={onCloseModal} />
        </div>
    );
}

export default React.memo(Login);
