// libs
import React, { useEffect, useState } from "react";
import { useHistory, useLocation, matchPath } from "react-router-dom";

// widgets
import SideItem from "../widgets/SideItem.js";

// assets
import { Images } from "shared/Images";

// utils
import { isMobileDevice, toggleClasses } from "shared/utils";

// consts
import { ADMIN_SIDEBAR_OPTIONS, LOCUM_ADMIN_SIDEBAR_OPTIONS } from "shared/Constants";

// styles
import "../styles.scss";
import { useSelector } from "react-redux";

function AdminSidebar({ setSidebarToggle }) {
  const history = useHistory();
  const location = useLocation();
  const [active, setActive] = useState(0);
  const profile = useSelector((state) => state.common.profileData) || {};
  const isPharmacyUser = profile?.user_type === 1;
  
  const switchSidebar = () => {
      if (isPharmacyUser) {
          return ADMIN_SIDEBAR_OPTIONS;
      } else {
          return LOCUM_ADMIN_SIDEBAR_OPTIONS;
      }
  };
  useEffect(() => {
        // show sidebar menu selected on normal/nested routes
        const currentTab = switchSidebar()?.find(
            (route) =>
                route?.path === location?.pathname ||
                route?.nestedRoutes?.find(
                    (nestedRoute) =>
                        matchPath(location?.pathname, {
                            path: nestedRoute,
                        })?.url?.length,
                ),
        );
        setActive(currentTab?.id);
    }, [location]);

    const handleItemClick = (data) => {
        if (isMobileDevice()) handleSidebarClose();
        history.push(data?.path);
    };

    const handleSidebarClose = () => {
        toggleClasses("main", "slide-active", "remove");
        setSidebarToggle(false);
    };


    return (
        <>
            <aside className="sideBar">
                <button className="d-xl-none close_btn" onClick={handleSidebarClose}>
                    <img src={Images.CloseIcon} alt="Close" width="30" />
                </button>
                <nav className="navbar text-left">
                    <div className="navbar-brand" onClick={() => history.push("/")}>
                        <img
                            src={Images.locumLogo}
                            alt="locumlogo"
                            width="194"
                            className="img-fluid"
                        />
                    </div>
                    <ul className="navbar-nav w-100">
                        {switchSidebar().map((item, idx) => {
                            return (
                                <SideItem
                                    key={`${item.id}${idx}`}
                                    data={item}
                                    handleClick={handleItemClick}
                                    active={active}
                                />
                            );
                        })}
                    </ul>
                </nav>
            </aside>
        </>
    );
}

export default React.memo(AdminSidebar);
