import React, { useEffect, useState } from "react";
import { ADMIN_ROUTES, BREAD_CRUMB_TEXT, ROUTES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import TabNavigation from "components/CustomTabNavigator";
import Branches from "./ManageBranch";
import BranchTemplate from "../../BranchTemplate";
// import Invoices from "./Invoices";
import BreadCrumb from "components/BreadCrumb";
import { useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getBranch, getTemplate, setSelectedTab } from "redux/Actions/PharmacyAdminProfile";
import useNotification from "hooks/Notifications";
import { branchPayloadData } from "views/authenticated/constants";

export const PROFILE_TABS = [
    { id: STRINGS.BRANCHES, title: STRINGS.BRANCHES },
    // { id: STRINGS.TEMPLATE, title: STRINGS.TEMPLATE },
    // { id: STRINGS.INVOICES, title: STRINGS.INVOICES },
];

const RenderTabs = ({ activeTab, branchData, handleBranchDataChange, templateData, handleTemplateDataChange }) => {
    switch (activeTab) {
        case STRINGS.BRANCHES: {
            return <Branches branchData={branchData} handleBranchDataChange={handleBranchDataChange} />;
        }
        // case STRINGS.TEMPLATE: {
        //     return <BranchTemplate templateData={templateData} handleTemplateDataChange={handleTemplateDataChange}/>;
        // }
        // case STRINGS.INVOICES: {
        //     return <Invoices />;
        // }
        default:
            <Branches />;
    }
};

const Branch = () => {
    const history = useHistory();
    const { state } = useLocation();
    const dispatch = useDispatch();
    const templateData = useSelector(state => state.pharmacyAdmin.templateData)
    const [msg, sendNotifications] = useNotification();
    const [activeTab, setActiveTab] = useState(PROFILE_TABS[0].id);
    const [branchData, setBranchData] = useState({ ...branchPayloadData })

    const handleBranchDataChange = (field, data) => {
        if (field) {
            setBranchData(prevState => {
                let newState = { ...prevState };
                newState[field] = data;
                return newState;
            })
        }
        console.log(field, data, "main comp")
    }

    const onTabPress = (e) => {
        setActiveTab(e.target.id);
    };

    const handleBreadCrumbClick = () => {
        dispatch(setSelectedTab(STRINGS.BRANCHES))
        history.push(ADMIN_ROUTES.BRANCHES)
    };

    useEffect(() => {
        if (!state?.addNewBranch) {
            dispatch(getBranch({ id: state.data }, (response) => {
                const { data } = response;
                console.log(data, "data  dsfhhfdj")
                setBranchData({
                    address: data?.address || "",
                    branch_name: data?.branch_name || "",
                    city: data?.city || "",
                    branch_email: data?.branch_email || "",
                    pharmacy_name: data?.pharmacy_name || "",
                    postal_code: data?.postal_code || "",
                    number_of_staff: data?.number_of_staff || "",
                    country: data?.country || "",
                    state: data?.state || "",
                    phone_number: data?.phone_number || "",
                    description: data?.description || "",
                    services: data?.services ? [...data?.services] : [],
                    job_timings: data?.job_timings ? [...data.job_timings] : [],
                    manager_name: data?.manager_info?.manager_name || "",
                    manager_phone: data?.manager_info?.manager_phone || "",
                    manager_email: data?.manager_info?.manager_email || "",
                    is_select_all: data?.is_select_all ? true : false
                })
            }, (error) => {
                sendNotifications({
                    msg: error || STRINGS.SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
            }));
        } else {
            setBranchData({ ...branchData, pharmacy_name: state.pharmacyName, description: templateData?.description || "", services: templateData?.services ? [...templateData?.services] : [] })
        }

        if (!Object.keys(templateData).length) {
            dispatch(getTemplate())
        }
    }, []);

    useEffect(() => {
        if (state?.addNewBranch) {
            setBranchData({ ...branchData, pharmacy_name: state.pharmacyName, description: templateData?.description || "", services: templateData?.services ? [...templateData?.services] : [] })
        }
    }, [templateData])

    return (
        <>
            <BreadCrumb
                onClick={handleBreadCrumbClick}
                previousLink={BREAD_CRUMB_TEXT.PHARMACY_BRANCHES}
                currentLink={`${state?.addNewBranch ? "New" : "Edit"} ${BREAD_CRUMB_TEXT.BRANCH}`}
            />
            <hr className="divider mb-0" />
            <TabNavigation
                onClick={onTabPress}
                tabsData={PROFILE_TABS}
                activeTab={activeTab}
            />
            <RenderTabs activeTab={activeTab} branchData={branchData} handleBranchDataChange={handleBranchDataChange} />
        </ >
    );
};

export default Branch;
