// libs
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useHistory } from "react-router-dom";

// widgets
import StaffDetails from "../components/StaffDetails";

// hooks
import useNotification from "hooks/Notifications";

// consts
import { ADMIN_ROUTES, ROUTES_TITLES, SNACK_MESSAGES, SNACK_VARIANT, STRINGS, initialDaysData } from "shared/Constants";

// actions
import { getStaffDetailsById } from "redux/Actions/PharmacyAdminDashboard";

const ViewStaff = () => {
    const dispatch = useDispatch();
    const profile = useSelector((state) => state.common.profileData);
    const history = useHistory();
    const [msg, sendNotification] = useNotification();
    const [staffDetails, setStaffDetails] = useState({ locumDetails: { email: null, username: null, phone: null }, branch: null, status: 1, isSelectAll: false });
    const [jobTimings, setJobTimings] = useState(initialDaysData || []);

    useEffect(() => {
        const staffId =  getStaffIdByPath();
        if (staffId) {
            getStaffDataById({ id: staffId });
        } else {
            history.push(ADMIN_ROUTES.MANAGE_STAFF);
        }

    }, [history?.location]);

    const getStaffIdByPath = () => {
        const { pathname = "" } = history?.location || {};
        return pathname?.split("/")?.pop() || "";
    };

    const getStaffDataById = (payload) => {
        dispatch(
            getStaffDetailsById(payload,
                (res = {}, type) => {
                    setStaffDetails((prev) => {
                        return {
                            ...prev,
                            locumDetails: { email: res?.data?.locumUser?.email, username: res?.data?.locumUser?.username, phone: res?.data?.locumUser?.phone },
                            branch: res?.data?.branch.map(item => item?.branch_name).join(", "),
                            isSelectAll: res?.data?.branch?.is_select_all ? true : false
                        };

                    });
                    setJobTimings(res?.data?.days || []);
                    sendNotification({
                        msg: res?.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    return (
        <>
            <div className="container">
                <nav aria-label="breadcrumb" className="breadcrumb_nav">
                    <ol class="breadcrumb">
                        <li className="breadcrumb-item"><Link to={ADMIN_ROUTES.MANAGE_STAFF}>{ROUTES_TITLES.MANAGE_STAFF}</Link></li>
                        <li class="breadcrumb-item active">{STRINGS.VIEW_STAFF}</li>
                    </ol>
                </nav>

                <hr className="divider" />

                <StaffDetails
                    staffDetails={staffDetails}
                    profile={profile}
                    jobTimingsData={jobTimings}
                    disabledInputs={{
                        email: true,
                        username: true,
                        phone: true,
                        branch: true,
                        jobTimings: true
                    }}
                    isView={true}
                />
            </div>
        </>
    );
};

export default ViewStaff;
