import React from 'react'
import './style.scss'

const ToggleSwitch = ({ checked, handleToggleChange, labelText }) => {
    return (
        <div className="toggle-button">         

            <label class="toggle">
                <input class="toggle-checkbox" type="checkbox" id="switch"
                    checked={checked}
                    onChange={handleToggleChange} />
                <div class="toggle-switch"></div>
                <span class="toggle-label">{labelText}</span>
            </label>
        </div>

    )
}

export default ToggleSwitch