import HeroBanner from "components/HeroBanner";
import ReactSelect from "components/ReactSelect";
import React, { useEffect, useState } from "react";
import { Images } from "shared/Images";
import { STRINGS } from "./constants";
import CustomTextInput from "components/CustomTextInput";
import Footer from "components/Footer/Footer";
import Register from "components/OurTeam";
import { useSelector } from "react-redux";
import Newsletter from "components/Newsletter";
import JoinTeam from "./JoinTeam";
import { useHistory } from "react-router-dom";
import {
    ERROR_MESSAGES,
    INPUT_TYPES,
    LABEL,
    PLACEHOLDERS,
    ROUTES,
    SNACK_MESSAGES,
    SNACK_VARIANT,
} from "shared/Constants";
import VacancyCard from "components/VacancyCard";
import SearchBar from "components/SearchBar";
import { DayPicker } from "react-day-picker";
import moment from "moment";
import CheckClickOutside from "components/OutsideClick";
import RangeSlider from "components/RangeSlider";
import JobList from "views/authenticated/LocumAdmin/JobList";
import { useCallback } from "react";
import { useDispatch } from "react-redux";
import { applyJob, fetchJob } from "redux/Actions/LocumWebPanel";
import useNotification from "hooks/Notifications";
import { Pagination } from "components/Pagination";
import CustomModal from "components/CustomModal";
import ApplyJobform from "components/VacancyCard/ApplyJobform";
import CustomButton from "components/CustomButton";
import DateRangeSelector from "components/DateRangeSelector";
import { act } from "react-dom/test-utils";
import JobApplyModal from "components/JobApplyModal";

function VacancyListing() {
    const history = useHistory();
    const dispatch = useDispatch();
    const profile = useSelector((state) => state.common.profileData) || {};
    const token = useSelector((state) => state.auth.token);
    const [keyword, setKeyword] = useState(null);
    const [totalItems, setTotalItems] = useState(0);
    const [itemsPerPage, setItemsPerPage] = useState(15);
    const [activePage, setActivePage] = useState(1);
    const [location, setLocation] = useState("");
    const [dateRange, setDateRange] = useState("");
    const [fetchedData, setFetchedData] = useState([]);
    const [priceRange, setPriceRange] = useState([0, 10]);
    const [msg, sendNotification] = useNotification();
    const [showPicker, setShowPicker] = useState(false);
    const [showModal, setShowModal] = useState(false);
    const [selectedJob, setSelectedJob] = useState({});
    const [notesValue, setNotesValue] = useState("");
    const [priceValue, setPriceValue] = useState("");
    const [vacanyErrors, setVacancyErrors] = useState({
        rate: "",
    });

    const [filterData, setFilterData] = useState({
        country: "",
        state: "",
        selectedDate: {
            from: null,
            to: new Date(""),
        },
        rateRange: [0, 0],
    });

    useEffect(() => {
        setPriceValue("");
        setNotesValue("");
    }, [showModal]);

    const onChangeNote = (e) => {
        setNotesValue(e.target.value);
    };
    const onChangePrice = (e) => {
        const min = 0;
        const max = 1000;
        const value = e.target.value.trim();
        handleChangeError("rate", value ? "" : ERROR_MESSAGES.GENERIC_ERROR);
        if (value?.length === 0 || value === "") {
            handleChangeError("rate", ``);
        } else if (value < min || value > max) {
            handleChangeError("rate", `Please enter a price between ${min} and ${max}.`);
        } else {
            // Set the price value in the state
            handleChangeError("rate", ``);
        }
        setPriceValue(value);
    };

    const handleIconClick = () => {
        setShowPicker((prevState) => !prevState);
    };
    const aboutUs = useSelector((state) => state.dynamic.aboutUs);
    const options = [
        { value: true, label: "Activate", color: "green" },
        { value: false, label: "Deactivate", color: "red" },
    ];

    const selectedOption = options[0];

    const handleChange = (selectedOption) => {};
    const changeHandler = (value, field) => {
        setDateRange(value);
    };

    console.log(filterData.selectedDate.to);

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const fetchListing = (isResetting = false) => {
        let payload = {
            search: keyword,
            limit: itemsPerPage,

            ...(filterData.selectedDate.from && {
                start_date: isResetting
                    ? ""
                    : filterData.selectedDate.from
                    ? moment(filterData.selectedDate.from).format("YYYY-MM-DD")
                    : "",
            }),
            ...(filterData.selectedDate.from && {
                end_date: isResetting
                    ? ""
                    : filterData.selectedDate.to
                    ? moment(filterData.selectedDate.to).format("YYYY-MM-DD")
                    : "",
            }),

            start_rate: isResetting ? "" : filterData.rateRange[0] || "",
            end_rate: isResetting ? "" : filterData.rateRange[1] || "",
            page: activePage,
        };
        dispatch(
            fetchJob(payload, (res) => {
                setFetchedData(res?.data);
                console.log(res, "VACANCY LISTING");
            }),
        );
    };
    const applyForJob = (e) => {
        e.preventDefault();
        let payload = {
            job_id: selectedJob?.id,
            candidate_id: profile?.id,
            description: notesValue,
            rate: priceValue,
        };
        dispatch(
            applyJob(
                payload,
                (res) => {
                    ///onsucess
                    fetchListing();
                    setPriceValue("");
                    setNotesValue("");
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                    setPriceValue("");
                    setNotesValue("");
                },
            ),
        );
    };

    const onApply = (data) => {
        setSelectedJob(data);
        setShowModal(true);
    };

    const resetFilter = () => {
        fetchListing({ isResetting: true });
        setFilterData({
            country: "",
            state: "",
            selectedDate: {
                from: null,
                to: new Date(""),
            },
            rateRange: [0, 0],
        });
    };

    const handleChangeError = (field, message) => {
        setVacancyErrors((prevState) => {
            let errorState = { ...prevState };
            errorState[field] = message;
            return errorState;
        });
    };

    useEffect(() => {
        fetchListing();
    }, []);

    const resetPageCountAndCallAPI = (itemsCountPerPageForPayload = itemsPerPage) => {
        fetchListing({ currentPageForPayload: 1, itemsCountPerPageForPayload });
    };

    const handleSearchData = () => {
        resetPageCountAndCallAPI();
    };

    const handleChangeKeyword = (e) => {
        setKeyword(e.target.value);
    };

    const handleItemsPerPageChange = (i) => {
        setItemsPerPage(i);
        resetPageCountAndCallAPI(i);
    };

    const handlePageChange = (i) => {
        setActivePage(i?.selected + 1);
        fetchListing({ currentPageForPayload: i?.selected + 1 });
    };

    const handleChangeFilterData = (key, value) => {
        setFilterData({ ...filterData, [key]: value });
    };

    return (
        <>
            <section className="hero_banner inner_banner">
                <div className="hero_bg">
                    <img src={Images.HeroShortBg} alt="Locumbay" width="300" className="w-100" />
                </div>
                <div className="container">
                    <div className="row banner_info">
                        <div className="banner_content text-center mx-auto">
                            <h1 className="h1 text-white mb-0">{STRINGS.VACANCY_HEAD}</h1>
                        </div>
                    </div>
                </div>
            </section>
            {/*End Inner Hero Banner */}

            <div className="container">
                <div className="card job_search_filter">
                    <div className="card-body bg-dark">
                        <div className="row align-items-end">
                            <div className="col-sm-6 col-xl-3 mb-2">
                                <label className="text-white">{STRINGS.SEARCH}</label>
                                <SearchBar
                                    searchAPI={handleSearchData}
                                    keyword={keyword}
                                    handleChangeKeyword={handleChangeKeyword}
                                />
                            </div>
                            <div className="col-sm-6 col-xl-3 mb-2 text-white">
                                <label className="text-white">{LABEL.DATE}</label>
                                <DateRangeSelector
                                    dateRange={{
                                        startDate: filterData["selectedDate"].from || null,
                                        endDate: filterData["selectedDate"].to || null,
                                        key: "selection",
                                    }}
                                    setDateRange={(val) =>
                                        handleChangeFilterData("selectedDate", {
                                            from: val.startDate,
                                            to: val.endDate,
                                        })
                                    }
                                    icon={Images.calendar}
                                />
                            </div>
                            <div className="col-sm-6 col-xl-4 mb-2">
                                <label className="text-white">{STRINGS.RANGE}</label>
                                <RangeSlider
                                    state={filterData.rateRange}
                                    changeHandler={(val) =>
                                        handleChangeFilterData("rateRange", [String(val[0]), String(val[1])])
                                    }
                                />
                            </div>
                            <div className="col-sm-6 col-xl-2 mb-2">
                                <label className="d-none d-md-block">&nbsp;</label>
                                <button
                                    className="btn btn-sm btn-primary btn-lg"
                                    onClick={() => fetchListing()}
                                >
                                    {STRINGS.SEARCH}
                                </button>
                                <CustomButton
                                    Icon={Images.reset}
                                    className="btn btn-xs btn-secondary ms-2"
                                    onClick={resetFilter}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="job_listing my-4">
                <div className="container">
                    <VacancyCard
                        onCardClick={(data) => {
                            if (token) {
                                history.push(`${ROUTES.VIEW_VACANCY?.slice(0, -4)}/${data?.id}`);
                                setSelectedJob(data);
                            } else {
                                history.push(ROUTES.LOGIN);
                            }
                        }}
                        onApply={onApply}
                        data={fetchedData}
                        jobSubmit={applyForJob}
                        user_id={profile?.id}
                        userType = {profile?.user_type}
                    />
                    {fetchedData?.length ? (
                        <Pagination
                            itemsCount={totalItems}
                            itemsPerPage={itemsPerPage}
                            handlePageChange={handlePageChange}
                            handleItemsPerPageChange={handleItemsPerPageChange}
                            activePage={activePage}
                        />
                    ) : null}
                </div>
            </div>
            <JoinTeam />
            <Footer />

            <JobApplyModal
                showModal={showModal}
                handleToggleModal={() => setShowModal((prev) => !prev)}
                data={selectedJob}
                priceValue={priceValue}
                notesValue={notesValue}
                onChangeNote={onChangeNote}
                onChangePrice={onChangePrice}
                vacanyErrors={vacanyErrors.rate}
                sendRequest={applyForJob}
            />
        </>
    );
}

export default VacancyListing;
