import React, { useEffect } from "react";
import CustomTextInput from "components/CustomTextInput";
import { INPUT_TYPES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import "./styles.scss";
import CustomButton from "components/CustomButton";
import { Images } from "shared/Images";
import CustomForm from "components/CustomForm";
import { CONTACT_US_DATA } from "./constant";
import { useDispatch, useSelector } from "react-redux";
import { contactUs, getContactUsData, setContactUs } from "redux/Actions/Auth";
import useNotification from "hooks/Notifications";
import ContactForm from "./contactForm";
import Footer from "components/Footer/Footer";
import Newsletter from "components/Newsletter";
import Header from "components/Header";
import { HEADER_DATA } from "views/HomePage/constants";
import HtmlParser from "react-html-parser";

function ContactUs({}) {
    const dispatch = useDispatch();
    const contactUs = useSelector((state) => state.dynamic?.contactUs);
    const contactUsDynamic = useSelector((state) => state.dynamic?.contactUsDynamic);
    const [msg, sendNotifications] = useNotification();
    useEffect(() => {
        window.scrollTo(0, 0);
        dispatch(
            getContactUsData(
                (response) => {
                    dispatch(setContactUs(response.data));
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    }, []);
    return (
        <>
            <Header headerList={HEADER_DATA} />
            <div className="inner_pages">
                <div className="pt-4 pt-lg-5 pb-0 contact_sec">
                    <div className="container">
                        <div className="title_h2 text-center mb-4 mb-xl-5">
                            <h1 className="h1">
                                {contactUsDynamic?.sections?.[0]?.section_name
                                    ? HtmlParser(contactUsDynamic?.sections?.[0]?.section_name)
                                    : STRINGS.ALREADY_REGISTERED}
                            </h1>
                            {contactUsDynamic?.sections?.[0]?.section_content ? (
                                HtmlParser(contactUsDynamic?.sections?.[0]?.section_content)
                            ) : (
                                <p>{STRINGS.CONTACT_US_TITLE}</p>
                            )}
                        </div>
                    </div>
                    <ContactForm
                        getInTouch={contactUsDynamic?.sections?.[1]?.section_name}
                        content={contactUsDynamic?.sections?.[1]?.section_content}
                    />
                </div>
            </div>
            <Newsletter
                section_name={contactUsDynamic?.sections?.[2]?.section_name}
                section_content={contactUsDynamic?.sections?.[2]?.section_content}
            />
            <Footer />
        </>
    );
}

export default ContactUs;
