import React from "react";

function CheckBoxHeader({
  isSelectAll,
  setMultipleSelectedData = () => {},
  setIsSelectAll = () => {},
  data,
  multipleSelectedData,
  disabled = false
}) {
  return (
    <div className="checkbox">
      <input
        type="checkbox"
        disabled={disabled}
        checked={isSelectAll}
        onChange={(e) => {
          if (e.target.checked) {
            setMultipleSelectedData(data.map((value, index) => value?.id));
            setIsSelectAll(true);
          } else if (data?.length > multipleSelectedData.length) {
            setIsSelectAll(false);
          } else {
            setMultipleSelectedData([]);
            setIsSelectAll(false);
          }
        }}
      />
      <label></label>
    </div>
  );
}

export default CheckBoxHeader;
