// libs
import React from "react";
import { useHistory } from "react-router-dom";

// consts
import { ADMIN_ROUTES, ROUTES } from "shared/Constants";

// styles
import './styles.scss';

const NotFound = ({ isAdmin = false }) => {
    const history = useHistory();

    const redirectToHomepage = () => {
        history.push(isAdmin ? ADMIN_ROUTES.DASHBOARD : ROUTES.HOMEPAGE);
    };

    return <>
        <div className="notfound">
            <div className="notfound-404">
                <h1 className="h1 mb-0">404</h1>
                <h2 className="h2">Page not found</h2>
            </div>
            <button onClick={redirectToHomepage} className="mt-4 btn btn-md btn-outline-primary">Homepage</button>
        </div>
    </>
}

export default NotFound;
