import ListingModal from "components/ListingModal";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { getServices } from "redux/Actions/PharmacyAdminProfile";
import { INPUT_TYPES } from "shared/Constants";
import { onCheckboxSelection, splice, structureCSInUseData, structureData } from "shared/Utilities";
import { getSelectedData } from "views/authenticated/constants";

function Vacancy() {
    const dispatch = useDispatch();
    const [showServices, setShowServices] = useState(false);
    const [services, setServices] = useState([]);
    const [displayData, setDisplayData] = useState([]);
    const [selectedData, setSelectedData] = useState([]);
    const [isAllSelected, setisAllSelected] = useState(false);
    const [keyword, setKeyword] = useState("");
    const isMulti = false;

    useEffect(() => {
        dispatch(
            getServices((response) => {
                console.log(structureData(response.data, "service_name"));
                setServices(structureData(response.data, "service_name"));
                setDisplayData(structureData(response.data, "service_name"));
            }),
        );
    }, []);

    const handleCheckBoxSelection = (e, value) => {
        if (isMulti) {
            onCheckboxSelection(e, setServices);
            if (e.target.checked) {
                setSelectedData((prev) => [...prev, value?.id]);
                if (selectedData?.length + 1 === services?.length) {
                    setisAllSelected(true);
                }
            } else {
                setSelectedData(splice(selectedData, value?.id));
                setisAllSelected(false);
            }
        } else {
            onCheckboxSelection(e, setServices, true);
            if (e.target.checked) {
                setSelectedData([value?.id]);
            } else {
                setSelectedData([]);
            }
        }
    };

    const handleAllSelected = (e, value) => {
        if (e.target.checked) {
            setSelectedData(services.map((value, index) => value?.id));
            setisAllSelected(true);
        } else if (selectedData?.length > services.length) {
            setisAllSelected(false);
        } else {
            setSelectedData([]);
            setisAllSelected(false);
        }
        services.map((value, index) => {
            setServices((prevState) => {
                let copyPrevState = [...prevState];
                let index = copyPrevState.findIndex((item) => item.id == value.id);
                copyPrevState[index].value = e.target.checked;
                return copyPrevState;
            });
        });
    };

    const handleChangeKeyword = (e) => {
        setKeyword(e.target.value);
    };

    const handleSearchData = (e) => {
        let key = keyword.toLowerCase();

        // Use filter() method to create a new array with matching objects
        if (key?.length) {
            const searchResults = services.filter((service) => {
                // Extract the values from the object and convert them to lowercase
                const name = service.title.toLowerCase();

                // Check if the keyword is present in the name or category
                return name.includes(key);
            });
            // Return the search results
            setDisplayData(searchResults);
        } else {
            setDisplayData(services);
        }
    };

    return (
        <div>
            <button className="btn btn-secondary" onClick={() => setShowServices(true)}>
                Click Me
            </button>
            <ListingModal
                data={displayData}
                setShow={setShowServices}
                show={showServices}
                handleSelection={handleCheckBoxSelection}
                selectedData={selectedData}
                handleSelectAll={handleAllSelected}
                isAllSelect={isAllSelected}
                heading={"Select Services that user can access: "}
                multipleSelection={isMulti}
                buttonType={!isMulti ? INPUT_TYPES.RADIO : INPUT_TYPES.CHECKBOX}
                keyword={keyword}
                handleChangeKeyword={handleChangeKeyword}
                handleSearchData={handleSearchData}
            />
        </div>
    );
}

export default Vacancy;
