// libs
import React, { useState } from "react";
import { useHistory } from "react-router-dom";

// widgets
import ButtonStatus from "components/ButtonStatus";
import CheckBoxBody from "components/MultiCheckBox/CheckBoxBody";
import CheckBoxHeader from "components/MultiCheckBox/CheckBoxHeader";
import SortIcon from "components/SortIcon";

// assets
import { Images } from "shared/Images";

// consts
import { ADMIN_BASE_ROUTE, JOB_LIST_VIEW, STRINGS } from "shared/Constants";

const TableView = ({
    data = [],
    multipleSelectedData = [],
    setMultipleSelectedData,
    toggleDeleteModal,
    tableHeadings = [],
    sortData = {},
    allDataSelected = false,
    activeTab = 1,
}) => {
    const history = useHistory();
    const [isSelectAll, setIsSelectAll] = useState(allDataSelected || false);

    return (
        <>
            <thead>
                <tr>
                    <th>
                        <CheckBoxHeader
                            isSelectAll={allDataSelected || isSelectAll}
                            setMultipleSelectedData={setMultipleSelectedData}
                            setIsSelectAll={setIsSelectAll}
                            data={data}
                            multipleSelectedData={multipleSelectedData}
                            disabled={!data?.length}
                        />
                    </th>
                    {tableHeadings.map((i, idx) => (
                        <th scope="col" key={idx}>
                            <span> {i.title} </span>
                            {i.sortKey && (
                                <SortIcon
                                    toggleIcon={sortData[i.sortKey]}
                                    onClick={() => i.action(i.sortKey, sortData[i.sortKey])}
                                />
                            )}
                        </th>
                    ))}
                </tr>
            </thead>

            {[0, 2, 13, 4]?.length ? (
                [0, 2, 13, 4].map((value, index) => (
                    <tbody key={index}>
                        <tr>
                            <td width={80}>
                                <CheckBoxBody
                                    setMultipleSelectedData={setMultipleSelectedData}
                                    setIsSelectAll={setIsSelectAll}
                                    value={value}
                                    multipleSelectedData={multipleSelectedData}
                                />
                            </td>
                            <td>{index + 1}</td>
                            <td>{value?.locumUser?.first_name || "N/A"}</td>
                            <td>{value?.branch?.branch_name || "N/A"}</td>
                            <td>{value?.locumUser?.email || "N/A"}</td>
                            <td>{value?.locumUser?.email || "N/A"}</td>
                            <td>{value?.locumUser?.phone || "N/A"}</td>
                            <td>
                                <div class="d-flex align-items-center">
                                    <span class="m-1 badge-md badge-grey">Private</span>
                                </div>
                            </td>
                            <td>
                                <div class="d-flex align-items-center">
                                    <span class="m-1 badge-md badge-info">Pending</span>

                                    <span class="m-1 badge-md badge-danger">Rejected</span>

                                    <span class="m-1 badge-md badge-success">Approved</span>
                                </div>
                            </td>
                            {activeTab === JOB_LIST_VIEW.OVERVIEW.ID ? (
                                <td width={200}>
                                    <div className="d-flex btn_group">
                                        <ButtonStatus
                                            image={Images.CloseWhiteIcon}
                                            title={"Delete"}
                                            status={"danger"}
                                            onClick={() => {
                                                toggleDeleteModal(value);
                                            }}
                                        />
                                    </div>
                                </td>
                            ) : null}
                        </tr>
                    </tbody>
                ))
            ) : (
                <tbody>
                    <tr>
                        <td colSpan={activeTab === JOB_LIST_VIEW.OVERVIEW.ID ? 10 : 9} className="text-center py-5">
                            {STRINGS.NO_DATA_AVAILABLE}
                        </td>
                    </tr>
                </tbody>
            )}
        </>
    );
};

export default TableView;
