import CustomButton from "components/CustomButton";
import CustomTextInput from "components/CustomTextInput";
import useNotification from "hooks/Notifications";
import React, { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
    cvDescriptionUpload,
    deleteDocuments,
    locumProfileDetails,
    uploadDocuments,
} from "redux/Actions/Auth";
import { ERROR_MESSAGES, FIVE_MB_SIZE, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import DocumentViewerModal from "components/DocumentViewer";
import { getDocsUrl } from "shared/Utilities";
import { createCv, DOCUMENT_TYPE_NAME } from "views/authenticated/constants";
import { INPUT_TYPES } from "../../../../shared/Constants";
import { createDescriptionData } from "./constants";
import "./styles.scss";
import EducationDetails from "./widgets/EducationDetails";
import JobHistory from "./widgets/JobHistory";
import { Images } from "shared/Images";
function UploadCv({ setCvData = () => {} }) {
    const dispatch = useDispatch();
    const profileDetail = useSelector((state) => state.common.locumProfileDetails);
    const cv_data = useSelector((state) => state.common.locumProfileDetails?.cvManage);
    const [uploadedCv, setUploadedCv] = useState(cv_data?.cv || "");
    const [uploadedCvData, setUploadedCvData] = useState(getDocsUrl(cv_data?.cv) || "");
    const [description, setDescription] = useState(cv_data?.personal_description || "");
    const [msg, sendNotifications] = useNotification();
    const [viewDoc, setViewDoc] = useState(false);
    const [modalOpacity, setModalOpacity] = useState(false);
    const inputRef = useRef(null);

    const uploadCv = (files) => {
        const payload = createCv(files, DOCUMENT_TYPE_NAME.CV, "CV");
        dispatch(
            uploadDocuments(
                payload,
                (response) => {
                    setUploadedCv(response.data);
                    setUploadedCvData(response?.data || "");
                    dispatch(
                        locumProfileDetails({
                            ...profileDetail,
                            cvManage: {
                                ...cv_data,
                                cv: response?.data || "",
                            },
                        }),
                    );
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const deleteClick = () => {
        dispatch(
            deleteDocuments(
                { id: uploadedCv?.id },
                (response) => {
                    setUploadedCv("");
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                    dispatch(
                        locumProfileDetails({
                            ...profileDetail,
                            cvManage: {
                                ...cv_data,
                                cv: null,
                            },
                        }),
                    );
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const handleChange = (event) => {
        if (event.target.files[0].size > FIVE_MB_SIZE) {
            sendNotifications({
                msg: ERROR_MESSAGES.SIZE,
                variant: SNACK_VARIANT.ERROR,
            });
        } else {
            uploadCv(event.target.files[0]);
            setUploadedCv(event.target.files[0]);
        }
        event.target.value = null;
    };
    const handleDescriptionChange = (event) => {
        setDescription(event.target.value);
    };
    const onPersonalDescriptionClick = () => {
        const payload = createDescriptionData(description);
        dispatch(
            cvDescriptionUpload(
                payload,
                (response) => {
                    dispatch(
                        locumProfileDetails({
                            ...profileDetail,
                            cvManage: {
                                ...cv_data,
                                personal_description: description,
                            },
                        }),
                    );
                    // uploadCv(description);
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    return (
        <div className="tab_content">
            <div className="row">
                <div className="col-12">
                    <div className={`input_file mb-4 ${uploadedCv ? "disabled" : ""}`}>
                        <CustomTextInput
                            ref={inputRef}
                            titleText={STRINGS.UPLOAD_CV}
                            type={INPUT_TYPES.FILE}
                            disabled={!!uploadedCv ? true : false}
                            onChange={(event) => handleChange(event)}
                            accept="application/pdf"
                        />
                        <label className="data_label form-control" data-label="Choose File">
                            Choose File
                        </label>
                        <span className="error">{STRINGS.ACCEPTED}</span>
                    </div>
                </div>
                {uploadedCv && (
                    <div className="col-12">
                        <label className="mb-2 d-block">{STRINGS.CV_UPLOADED}</label>
                        <div className="upload_doc mb-4">
                            <span
                                onClick={() => {
                                    setModalOpacity(true);
                                    setViewDoc(true);
                                }}
                            >
                                {"CV.pdf"}
                            </span>
                            <button className="btn-danger" onClick={deleteClick}>
                                <img src={Images.deleteCvIcon} alt="delete"/>
                            </button>
                        </div>
                    </div>
                )}
            </div>
            <div className="card mb-4">
                <div className="card-body">
                    <label htmlFor="personal-description" className="mb-2">
                        {LABEL.PERSONAL_DESCRIPTION}
                    </label>
                    <div className="field_white">
                        <textarea
                            id="personal-description"
                            onChange={handleDescriptionChange}
                            value={description}
                            rows="5"
                            className="form-control"
                        ></textarea>
                    </div>
                </div>
            </div>
            <div className="text-end mb-4">
                <CustomButton
                    id={"personal-description"}
                    title={STRINGS.SAVE}
                    onClick={onPersonalDescriptionClick}
                    className="btn btn-sm btn-primary"
                />
            </div>
            <JobHistory jobData={cv_data?.job_history} />
            <EducationDetails educationData={cv_data?.edu_history} />

            {viewDoc && (
                <DocumentViewerModal
                    viewDoc={viewDoc}
                    modalOpacity={modalOpacity}
                    documentUrl={getDocsUrl(uploadedCv?.document)}
                    setViewDoc={setViewDoc}
                    onLoadSuccess={() => {
                        setModalOpacity(false);
                    }}
                />
            )}
        </div>
    );
}

export default UploadCv;
