// libs
import React from "react";
import { STRINGS } from "shared/Constants";

const TABLE_HEADINGS = [{ title: "#", action: () => { } },
{ title: "Page Name", action: () => { } },
{ title: "View", action: () => { }, isCheckbox: "can_view" },
{ title: "Add", action: () => { }, isCheckbox: "can_create" },
{ title: "Edit", action: () => { }, isCheckbox: "can_edit" },
{ title: "Delete", action: () => { }, isCheckbox: "can_delete" },
];


const PermisionsTable = ({ permissionsData = [], onChangeInput = () => { }, isDisabledInput = false, headerCheckboxData = {}, handleMultipleSelect = () => { } }) => {
    return <div className="card rounded mb-4">
        <div className="card-body bg-white">
            <h6 className="h6 mb-3">Permissions</h6>
            <div className="table-responsive">
                <table className="table">
                    <thead>
                        <tr>
                            {
                                TABLE_HEADINGS.map((i, idx) => (
                                    <th scope="col" id={idx} onClick={i.action}>
                                        {i?.isCheckbox &&
                                            <div className="checkbox">
                                                <input
                                                    type="checkbox"
                                                    disabled={isDisabledInput || !permissionsData?.length}
                                                    checked={headerCheckboxData[i?.isCheckbox]}
                                                    onChange={(e) => {
                                                        handleMultipleSelect(e, i?.isCheckbox);
                                                    }}
                                                />
                                                <label></label>
                                            </div>}
                                        <span> {i.title} </span>
                                    </th>
                                ))}
                        </tr>
                    </thead>

                    {permissionsData?.length ? (
                        permissionsData.map((value, index) => (
                            <tbody key={index}>
                                <tr>
                                    <td width={80}>{index + 1}</td>
                                    <td>{value.permission_name}</td>
                                    <td>
                                        <div className="checkbox">
                                            <input
                                                name="can_view"
                                                type="checkbox"
                                                checked={value?.can_view}
                                                onChange={(e) => onChangeInput(e, value?.id)}
                                                disabled={isDisabledInput}
                                            />
                                            <label></label>
                                        </div>
                                    </td>
                                    <td>
                                        <div className="checkbox">
                                            <input
                                                name="can_create"
                                                type="checkbox"
                                                checked={value?.can_create}
                                                onChange={(e) => onChangeInput(e, value?.id)}
                                                disabled={isDisabledInput}
                                            />
                                            <label></label>
                                        </div>
                                    </td>
                                    <td>
                                        <div className="checkbox">
                                            <input
                                                name="can_edit"
                                                type="checkbox"
                                                checked={value?.can_edit}
                                                onChange={(e) => onChangeInput(e, value?.id)}
                                                disabled={isDisabledInput}
                                            />
                                            <label></label>
                                        </div>
                                    </td>
                                    <td width={120}>
                                        <div className="checkbox">
                                            <input
                                                name="can_delete"
                                                type="checkbox"
                                                checked={value?.can_delete}
                                                onChange={(e) => onChangeInput(e, value?.id)}
                                                disabled={isDisabledInput}
                                            />
                                            <label></label>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        ))
                    ) : (
                        <tbody>
                            <tr>
                                <td colSpan={6} className="text-center py-5">
                                    {STRINGS.NO_DATA_AVAILABLE}
                                </td>
                            </tr>
                        </tbody>
                    )}
                </table>
            </div>
        </div>
    </div>
};

export default PermisionsTable;