import React from "react";
import { JOB_TIMING, NA, STRINGS } from "shared/Constants";
import "./style.scss";
import { convertTimeToAmPm } from "shared/Utilities";

const JobTimingsView = ({ data = [] }) => {
    return (
        <div className="card">
            <div className="card-body">
                <div className="opening_time">
                    <h6 className="h6">{STRINGS.JOB_TIMING}</h6>
                    <div className="table-responsive">
                        <table className="w-100 time_table">
                            <tr key={1000 + 1}>
                                <th width="80"></th>
                                <th width="80"></th>
                                {Object.values(JOB_TIMING.HEADER).map((i, idx) => {
                                    return (
                                        <>
                                            <th id={idx}>{i}</th>
                                        </>
                                    );
                                })}
                            </tr>
                            {data.length &&
                                data?.map((i, idx) => (
                                    <tr>
                                        <th className="text-start">
                                            {JOB_TIMING.WEEKDAYS[i?.day]}
                                        </th>
                                        <td> - </td>
                                        <td>
                                            {" "}
                                            {i?.opening_time
                                                ? convertTimeToAmPm(i?.opening_time)
                                                : NA}{" "}
                                        </td>
                                        <td>
                                            {" "}
                                            {i?.closing_time
                                                ? convertTimeToAmPm(i?.closing_time)
                                                : NA}{" "}
                                        </td>
                                        <td>
                                            {" "}
                                            {i?.lunch_start
                                                ? convertTimeToAmPm(i?.lunch_start)
                                                : NA}{" "}
                                        </td>
                                        <td>
                                            {" "}
                                            {i?.lunch_end
                                                ? convertTimeToAmPm(i?.lunch_end)
                                                : NA}{" "}
                                        </td>
                                    </tr>
                                ))}
                        </table>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default JobTimingsView;
