import "./styles.scss";
import "react-phone-input-2/lib/style.css";

import React, { useState } from "react";

import PhoneInput from "react-phone-input-2";
import { STRINGS } from "shared/Constants";

function ResendActivationCode(props) {
    const [active, setActive] = useState({
        [STRINGS.PHONE_NUMBER]: true,
        [STRINGS.EMAIL]: false,
    });
    const [phone, setPhone] = useState("");
    const toggleActive = (event) => {
        setActive((prev) => {
            if (event.target.id === STRINGS.PHONE_NUMBER) {
                return {
                    [STRINGS.EMAIL]: false,
                    [event.target.id]: true,
                };
            } else {
                return {
                    [STRINGS.PHONE_NUMBER]: false,
                    [event.target.id]: true,
                };
            }
        });
    };
    const renderWarning = () => {
        return (
            <div className="warning">
                {active[STRINGS.PHONE_NUMBER] ? (
                    <div className="warningText">{STRINGS.PHONE_NUMBER_WARNING}</div>
                ) : (
                    <div className="warningText">{STRINGS.EMAIL_WARNING}</div>
                )}
            </div>
        );
    };
    return (
        <div className="overflow-hidden front_form min-vh-100">
            <div className="ls_form">
                <div className="text-center d-block mb-4">
                    <h1 className="h5 text-capitalize">{STRINGS.VERIFY_ACCOUNT}</h1>
                </div>

                <div className="mb-4">
                    <div
                        id={STRINGS.PHONE_NUMBER}
                        className={
                            active[STRINGS.PHONE_NUMBER]
                                ? "phoneNumberActive"
                                : "phoneNumberInActive"
                        }
                        onClick={toggleActive}
                    >
                        {STRINGS.PHONE_NUMBER}
                    </div>
                    <div
                        id={STRINGS.EMAIL}
                        className={active[STRINGS.EMAIL] ? "emailActive" : "emailInActive"}
                        onClick={toggleActive}
                    >
                        {STRINGS.EMAIL}
                    </div>
                </div>
                {renderWarning()}
                <PhoneInput
                    inputProps={{
                        name: "phone",
                        required: true,
                        autoFocus: true,
                    }}
                    country={"us"}
                    buttonClass=""
                    value={phone}
                    onChange={(phone) => {
                        setPhone(phone);
                    }}
                />
            </div>
        </div>
    );
}

export default ResendActivationCode;
