import EventsCalendar from "components/Calendar";
import Cards from "components/Cards";
import useNotification from "hooks/Notifications";
import React, { useEffect } from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { getNotificationListing } from "redux/Actions/LocumAdmin";
import { DASHBOARD_CARDS, LOCUM_ADMIN_ROUTES, SNACK_MESSAGES, SNACK_VARIANT } from "shared/Constants";
import { Images } from "shared/Images";
import CustomCalender from "views/Calendar";
import { convertDateTimeToTime } from "views/authenticated/constants";

function LocumDashboard() {
    const dispatch = useDispatch();
    const history = useHistory();
    const [msg, sendNotification] = useNotification();
    const [cardData, setCardData] = useState([
        {
            title: DASHBOARD_CARDS.BOOKED,
            count: 23,
            image: Images.booked_card,
        },
        {
            title: DASHBOARD_CARDS.COMPLETED,
            count: 23,
            image: Images.completedJobs_card,
        },
        {
            title: DASHBOARD_CARDS.INVITED,
            count: 23,
            image: Images.invited_card,
        },
        {
            title: DASHBOARD_CARDS.VACANCIES,
            count: 23,
            image: Images.vacancy_card,
        },
    ]);

    const [notificationData, setNotificationData] = useState([]);

    const getNotification = () => {
        let payload = {
            page: 1,
            limit: 3,
        };

        dispatch(
            getNotificationListing(
                payload,
                (res) => {
                    console.log(res, "res");
                    setNotificationData(res.data);
                },
                (err) => {
                    //onError Message for send notifications
                    sendNotification({
                        msg: err.data?.message
                            ? err.data?.message
                            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };

    // fetch data with updated values
    // useEffect(() => {
    //     getNotification();
    // }, []);

    console.log(notificationData, "notificationData");

    return (
        <div>
            <div className="row card_block">
                {cardData?.length
                    ? cardData.map((i, idx) => <Cards key={idx} data={i} index={idx} />)
                    : null}

                <div className="d-flex">
                    <CustomCalender />
                    <div>
                        <div className="card m-4 ">
                            <div className="d-flex flex-wrap align-items-center justify-content-center dropdown-header">
                                <h6 className="h6 mb-0">Recent Messages</h6>
                            </div>

                            <ul className="list-unstyled list-body">
                                {notificationData?.map((i, idx) => {
                                    return (
                                        <li key={idx}>
                                            <h6 className="h6">{i.notif_for}</h6>
                                            <span>{i.notif_text} </span>
                                            <span>{convertDateTimeToTime(i?.updated_at)}</span>
                                        </li>
                                    );
                                })}
                            </ul>

                            <div className="dropdown-footer text-center" onClick={()=> history.push(LOCUM_ADMIN_ROUTES.MESSAGES)}>
                                <button className="btn btn-sm btn-primary">See More</button>
                            </div>
                        </div>

                        <div className="card m-4 ">
                            <div className="d-flex flex-wrap align-items-center justify-content-center dropdown-header" >
                                <h6 className="h6 mb-0">Notifications</h6>
                            </div>

                            <ul className="list-unstyled list-body">
                                {notificationData?.map((i, idx) => {
                                    return (
                                        <li key={idx}>
                                            <h6 className="h6">{i.notif_for}</h6>
                                            <span>{i.notif_text} </span>
                                            <span>{convertDateTimeToTime(i?.updated_at)}</span>
                                        </li>
                                    );
                                })}
                            </ul>

                            <div className="dropdown-footer text-center" onClick={()=> history.push(LOCUM_ADMIN_ROUTES.NOTIFICATIONS)}>
                                <button className="btn btn-sm btn-primary">See More</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default LocumDashboard;
