import CustomButton from "components/CustomButton";
import CustomTextInput from "components/CustomTextInput";
import React from "react";
import HtmlParser from "react-html-parser";
import { STRINGS } from "shared/Constants";
import "./style.scss";

function Newsletter({ section_name = "", section_content = "" }) {
    return (
        <section className="newsletter_sec">
            <div className="container">
                <div className="title_h2 text-center">
                    <h2 className="h2 text-white">
                        {section_name
                            ? HtmlParser(section_name)
                            : "Keep updated on newly added Vacancies"}
                    </h2>
                    {section_content ? (
                        HtmlParser(section_content)
                    ) : (
                        <p>
                            Be the first to know about newly added job sent directly to your email
                        </p>
                    )}
                </div>
                <form className="text-center mt-4">
                    <div className="d-flex align-items-center justify-content-center newsletter_form">
                        <CustomTextInput
                            type="email"
                            id={STRINGS.EMAIL}
                            // errorMessage={errorMessage.email}
                            placeholder={STRINGS.EMAILADDRESS}
                            // onChange={onChange}
                        />
                        <CustomButton
                            type="submit"
                            title={STRINGS.SUSBCRIBE}
                            className="btn btn-md btn-primary"
                        />
                    </div>
                </form>
            </div>
        </section>
    );
}

export default Newsletter;
