import ActivateDeactivate from 'components/ActivateDeactivate';
import ButtonStatus from 'components/ButtonStatus';
import CheckBoxBody from 'components/MultiCheckBox/CheckBoxBody';
import CheckBoxHeader from 'components/MultiCheckBox/CheckBoxHeader';
import SortIcon from 'components/SortIcon';
import React, { useState } from 'react'
import { useHistory } from 'react-router-dom';
import { ADMIN_ROUTES, ROUTES, SORTKEYS, STRINGS } from 'shared/Constants';
import { Images } from 'shared/Images';
import { BRANCHES_TABLE_HEADER } from 'views/authenticated/constants';

const TableView = ({ data = [], multipleSelectedData = [], setMultipleSelectedData, handleStatusChange, toggleDeleteModal, toggle, fetchDataAccordingToSort = ()=>{} }) => {
  const history = useHistory()
  const [isSelectAll, setIsSelectAll] = useState(false)

  const sortImage = (name) => {
    switch (name) {
      case BRANCHES_TABLE_HEADER.NAME:
        return true;
      default:
        return false;
    }
  };

  return (
    <>
      <thead>
        <tr>
          <th>
            <CheckBoxHeader
              isSelectAll={isSelectAll}
              setMultipleSelectedData={setMultipleSelectedData}
              setIsSelectAll={setIsSelectAll}
              data={data}
              multipleSelectedData={multipleSelectedData}
            />
          </th>
          {Object.values(BRANCHES_TABLE_HEADER).map((i, idx) => (
            <th scope="col" id={idx}>
              {sortImage(i) ? (
                  <SortIcon
                    toggleIcon={
                      toggle.key === i && SORTKEYS["ascending"] == toggle.direction ? true : false
                    }
                    onClick={() => {
                      fetchDataAccordingToSort(i, toggle.key === i ? SORTKEYS["ascending"] == toggle.direction ? SORTKEYS["descending"] : SORTKEYS["ascending"] : toggle.direction)
                    }}
                  />
                ) : null}
              <span> {i} </span>
            </th>
          ))}
        </tr>
      </thead>

      {data?.length ? (
        data.map((value, index) => (
          <tbody key={index}>
            <tr>
              <td width={80}>
                <CheckBoxBody
                  setMultipleSelectedData={setMultipleSelectedData}
                  setIsSelectAll={setIsSelectAll}
                  value={value}
                  multipleSelectedData={multipleSelectedData}
                />
              </td>
              <td width={80}>{index + 1}</td>
              <td>{value.branch_name}</td>
              <td>{value.phone_number ? `+${value.phone_number}` : 'N/A'}</td>
              <td>{value.number_of_staff ?? "N/A"}</td>
              <td>
                <ActivateDeactivate statusChange={handleStatusChange} value={value} />
              </td>
              <td width={200}>
                <div className="d-flex btn_group">
                  <ButtonStatus
                    image={Images.EyeIcon}
                    title={"View"}
                    onClick={() => {
                      history.push({ pathname: ADMIN_ROUTES.VIEW_BRANCH, state: { data: value.id } })
                    }}
                  />
                  <ButtonStatus
                    image={Images.EditIcon}
                    title={"Edit"}
                    onClick={() => {
                      history.push({ pathname: ADMIN_ROUTES.MANAGE_BRANCH, state: { data: value.id } })
                    }}
                  />
                  <ButtonStatus
                    image={Images.TrashIcon}
                    title={"Delete"}
                    status={"danger"}
                    onClick={() => {
                      toggleDeleteModal(value)
                    }}
                  />
                </div>
              </td>
            </tr>
          </tbody>
        ))
      ) : (
        <tbody>
          <tr>
            <td colSpan={7} className="text-center py-5">
              {STRINGS.NO_DATA_AVAILABLE}
            </td>
          </tr>
        </tbody>
      )}
    </>
  )
}

export default TableView