import {useState} from "react";
import './style.scss'

const ReadMore = ({ numberOfCharacters = 150, children }) => {
    const text = children;
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
        setIsReadMore(!isReadMore);
    };
    return (
        <p className="text">
        {isReadMore ? text.slice(0, numberOfCharacters) : text}
        <span onClick={toggleReadMore} className="read-or-hide">
            {children.length > numberOfCharacters ? isReadMore ? "...read more" : " show less" : null}
        </span>
        </p>
    );
};

export default ReadMore;