import { all, put, takeLatest } from "redux-saga/effects";
import { clearProfileData, setToken, startLoading, stopLoading } from "redux/Actions/Auth";
import { GET_CHAT_USERS_LIST, GET_USERS_LIST_BY_NAME, GET_USER_MESSAGES_LIST, UPLOAD_MEDIA } from "redux/Actions/PharmacyAdminDashboard";
import { API_URLS, STATUS_CODES } from "services/Api/Constants";
import { getRequest, postRequest, updateAuthToken } from "shared/Axios";
import { objectToFormData } from "shared/utils";

// get usersList by name
function* fetchUsersListByName({ payload, success = () => {}, fail = () => {} }) {
    const {isDebounceCall = false, search = "", page = 1, limit = 10} = payload;
    try {
        if (!isDebounceCall) {
            yield put(startLoading());
        };
        const { status, data = {} } = yield getRequest({
            API: `${API_URLS.FETCH_USERS_BY_NAME}?search=${encodeURIComponent(search)}&page=${page}&limit=${limit}`,
        });
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
};

// get chat Users list
function* fetchChatUsers({ payload, success = () => {}, fail = () => {} }) {
    const {isDebounceCall = false, page = 1, limit = 10, archive = 0, blocked = 0} = payload;
    let apiUrl = `${API_URLS.FETCH_CHAT_USERS_LIST}?page=${page}&limit=${limit}`;
    if(archive){
        apiUrl += `&archive=${archive}`
    };
    if(blocked){
        apiUrl += `&blocked=${blocked}`
    }
    try {
        if (!isDebounceCall) {
            yield put(startLoading());
        };
        const { status, data = {} } = yield getRequest({
            API: apiUrl,
        });
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
};

// get chat history of User
function* fetchChatHistory({ payload, success = () => {}, fail = () => {} }) {
    const {isDebounceCall = false, page = 1, limit = 10, room_name = "", id = null} = payload;
    try {
        if (!isDebounceCall) {
            yield put(startLoading());
        };
        let apiUrl = `${API_URLS.FETCH_USER_MESSAGES_LIST}?page=${page}&limit=${limit}&room_name=${room_name}`;
        if(id){
            apiUrl += `&id=${id}`
        }
        const { status, data = {} } = yield getRequest({
            API: apiUrl,
        });
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
};

// get chat history of User
function* uploadMedia({ payload, success = () => {}, fail = () => {} }) {
    const {isDebounceCall = false, type, media, room_name } = payload;
    try {
        if (!isDebounceCall) {
            yield put(startLoading());
        };
        const formDataObj = objectToFormData({
            type,
            media,
            room_name,
        });
        let apiUrl = `${API_URLS.UPLOAD_MEDIA}`;
        const { status, data = {} } = yield postRequest({
            API: apiUrl,
            DATA: formDataObj
        });
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
};

function* MessagesSaga() {
    yield all([
        takeLatest(GET_USERS_LIST_BY_NAME, fetchUsersListByName),
        takeLatest(GET_CHAT_USERS_LIST, fetchChatUsers),
        takeLatest(GET_USER_MESSAGES_LIST, fetchChatHistory),
        takeLatest(UPLOAD_MEDIA, uploadMedia)
    ]);
}

export default MessagesSaga;