import "./styles.scss";

import { ROUTES, STRINGS } from "../../../shared/Constants";
import React, { useState } from "react";

import LocumRegisterForm from "./widgets/locum";
import PharmacyRegisterForm from "./widgets/pharmacy";
import { useHistory } from "react-router-dom";

const Signup = (props) => {
    const history = useHistory();
    const [focus, setFocus] = useState({
        locum: true,
        pharmacy: false,
    });
    const changeFocus = (e) => {
        setFocus(() => {
            if (e.target.id === STRINGS.LOCUM_ID) {
                return { locum: true, pharmacy: false };
            } else {
                return { locum: false, pharmacy: true };
            }
        });
    };
    const responseFacebook = (response) => { };
    const onLoginClick = () => {
        history.push(ROUTES.LOGIN);
    };
    return (
        <div className="overflow-hidden front_form form_signup min-vh-100">
            <div className="ls_form">
                <div className="text-center d-block mb-4">
                    <h1 className="h5">{STRINGS.REGISTER}</h1>
                </div>

                <div className="d-flex register_tabs">
                    <span id="locum" className={focus.locum ? "active" : ""} onClick={changeFocus}>
                        {STRINGS.JOIN_AS_LOCUM}
                    </span>
                    <span
                        id="pharmacy"
                        className={focus.pharmacy ? "active" : ""}
                        onClick={changeFocus}
                    >
                        {STRINGS.JOIN_AS_PHARMACY}
                    </span>
                </div>

                {focus.locum && <LocumRegisterForm />}
                {focus.pharmacy && <PharmacyRegisterForm />}

                <div className="text-center text-mute mt-3">
                    {STRINGS.ALREADY_ACCOUNT}
                    <a className="link_text ps-2" onClick={onLoginClick}>
                        {STRINGS.LOGIN}
                    </a>
                </div>
            </div>
        </div>
    );
};

export default Signup;
