export function experienceToYears(years, months) {
    if ((years + months / 12) % 1 === 0) {
        return years + months / 12;
    } else {
        return (years + months / 12).toFixed(1);
    }
}
export function getLocalizedTimeString(dateString) {
    const format = "yyyy-MM-ddTHH:mm:ss.SSSZ";
    const date = new Date(dateString);
    if (isNaN(date.getTime())) {
        //throw new Error('Invalid date string');
    }
    const options = { year: "numeric", month: "long", day: "numeric" };
    return date.toLocaleTimeString(undefined, options);
}
export function getUniqueDocumentTypes(data = []) {
    console.log(data, "Data at document");
    const types = data?.reduce((typesObj, item) => {
        typesObj[item?.document_type] = typesObj[item?.document_type] || item;
        return typesObj;
    }, {});
    console.log(Object.values(types), "types");
    return Object.values(types);
}
export const payloadForRegister = (data, user_type) => {
    if (user_type === USER_TYPE.LOCUM) {
        return {
            email: data["email"],
            first_name: data["first-name"],
            last_name: data["last-name"],
            password: data["password"],
            // username: ,
            // dob: "1998-03-11",
            user_type,
        };
    } else {
        return {
            company_name: data["company_name"],
            email: data["email"],
            password: data["password"],
            // company_name:data["company-name"],
            // username: ,
            // dob: "1998-03-11",
            user_type,
        };
    }
};
