import CustomButton from "components/CustomButton";
import CustomCheckBox from "components/CustomCheckBox";
import CustomTextInput from "components/CustomTextInput";
import useNotification from "hooks/Notifications";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useLocation } from "react-router-dom";
import { createOrUpdatePharmacyTemplate, getServices, setTemplateData } from "redux/Actions/PharmacyAdminProfile";
import { CS_IN_USE, INPUT_TYPES, LABEL, PLACEHOLDERS, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { structureCSInUseData } from "shared/Utilities";
import { getSelectedData } from "views/authenticated/constants";

const BranchTemplate = ({ templateData, handleTemplateDataChange }) => {
    const { state } = useLocation();
    const dispatch = useDispatch()
    const [msg, sendNotifications] = useNotification();
    const [services, setServices] = useState([])
    console.log(state, templateData, "state");

    const onCheckboxSelect = (e) => {
        setServices((prevState) => {
            let copyPrevState = [...prevState];
            let index = prevState.findIndex((item) => item.id == e.target.id);
            copyPrevState[index].value = e.target.checked;
            console.log(copyPrevState, "prevState");
            return copyPrevState;
        });
    }

    const handleChangeDescription = (e) => {
        handleTemplateDataChange('description', e.target.value)
    }

    const handlePrivateVacancyChange = (e) => {
        handleTemplateDataChange('private', e.target.checked)
    }

    const handleTemplateDataSave = () => {
        let payload = { ...templateData };
        payload.services = payload?.services.map(item => item.id)
        dispatch(createOrUpdatePharmacyTemplate(payload, (response) => {
            sendNotifications({
                msg: response.message,
                variant: SNACK_VARIANT.SUCCESS,
            });
            dispatch(setTemplateData(templateData))
        },
            (error) => {
                sendNotifications({
                    msg: error || STRINGS.SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
            }))
    }

    useEffect(() => {
        dispatch(getServices(
            (response) => {
                let allData = structureCSInUseData(response.data, "service_name")
                let selectedData = getSelectedData(allData, templateData.services)
                setServices(selectedData)
            }
        ))
    }, [])

    useEffect(() => {
        handleTemplateDataChange("services", services.filter(item => item.value));
    }, [services])

    return (
        <div className="tab_content admin_view field_white">
            <h6 className="label_title mb-4">
                <span>{STRINGS.PHARMACY_DESCRIPTION}</span>
            </h6>
            <div className="row admin_view">
                <div className="col-md-6 mb-4">
                    <CustomTextInput
                        titleText={LABEL.PHARMACY_NAME}
                        type={INPUT_TYPES.TEXT}
                        placeholder={PLACEHOLDERS.ENTER_HERE}
                        value={templateData.pharmacy_name}
                        onChange={(e) => {
                            handleTemplateDataChange("pharmacy_name", e.target.value);
                        }}
                        disabled={true}
                    />
                </div>
                {/* <div className="col-md-6">
                    <CustomTextInput
                        titleText={LABEL.BRANCH_NAME}
                        type={INPUT_TYPES.TEXT}
                        placeholder={PLACEHOLDERS.ENTER_HERE}
                        value={templateData.branch_name}
                        onChange={(e) => {
                            handleTemplateDataChange("branch_name", e.target.value);
                        }}
                        disabled={!state?.addNewBranch}
                    />
                </div> */}
            </div>
            <div className="row">
                <div className="col-12 mb-4">
                    <h6 className="label_title">
                        <span>{LABEL.SERVICES}</span>
                    </h6>
                    <span className="d-block text-small">{STRINGS.SERVICES_TEXT}</span>
                    <div className="row">
                        <CustomCheckBox checkBoxData={services} onCheckboxSelect={onCheckboxSelect} />
                    </div>
                </div>
            </div>
            <h6 className="label_title mb-4">
                <span>{STRINGS.TRAVEL_ALLOWANCE}</span>
            </h6>
            <div className="row">
                <div className="col-md-6 mb-4">
                    <CustomTextInput
                        titleText={LABEL.MAX_MILES_PER_WEEK}
                        type={INPUT_TYPES.NUMBER}
                        placeholder={PLACEHOLDERS.ENTER_HERE}
                        value={templateData.max_miles_per_week}
                        onChange={(e) => {
                            handleTemplateDataChange("max_miles_per_week", e.target.value);
                        }}
                    />
                </div>
                <div className="col-md-6 mb-4">
                    <CustomTextInput
                        titleText={LABEL.AMOUNT_PAID_PER_MILE}
                        type={INPUT_TYPES.NUMBER}
                        placeholder={PLACEHOLDERS.ENTER_HERE}
                        value={templateData.amount_paid_per_miles}
                        onChange={(e) => {
                            handleTemplateDataChange("amount_paid_per_miles", e.target.value);
                        }}
                        icon={'£'}
                    />
                </div>
            </div>
            <h6 className="label_title mb-4">
                <span>{STRINGS.DESCRIPTION}</span>
            </h6>
            <div className="row mb-4">
                <CustomTextInput
                    titleText={LABEL.DESCRIPITON}
                    type={INPUT_TYPES.TEXT_AREA}
                    onChange={handleChangeDescription}
                    placeholder={PLACEHOLDERS.ENTER_HERE}
                    value={templateData.description}
                    ShowTextArea={true}
                    className="form-control height-120"
                />
            </div>
            <div className="mb-4">
                <div className="checkbox">
                    <input type="checkbox" checked={templateData.private} onChange={handlePrivateVacancyChange} />
                    <label>{STRINGS.PRIVATE_VACANCY}</label>
                </div>
            </div>
            <div className="text-center">
                <CustomButton
                    className="btn btn-md btn-primary"
                    type={INPUT_TYPES.SUBMIT}
                    title={STRINGS.UPDATE}
                    onClick={handleTemplateDataSave}
                />
            </div>
        </div>
    );
};

export default BranchTemplate;
