import { all, fork } from "redux-saga/effects";

import watchAuth from "./Auth";
import home from "./Home";
import pharmacyAdminSaga from "./PharmacyAdminSaga";
import uploadDocs from "./Upload";
import MessagesSaga from "./Messages";
import locumAdminSaga from "./LocumAdminSaga";

function* rootSaga() {
    yield all([fork(watchAuth), fork(uploadDocs), fork(home), fork(pharmacyAdminSaga), fork(locumAdminSaga), fork(MessagesSaga)]);
}

export default rootSaga;
