import "./styles.scss";

import React, { useState } from "react";

import CustomButton from "components/CustomButton";
import CustomOtpInput from "components/CustomOtpInput";
import { Images } from "shared/Images";
import { ERROR_MESSAGES, ROUTES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import { otpVerification, resendOtp } from "redux/Actions/Auth";
import { STATUS_CODES } from "services/Api/Constants";
import useNotification from "hooks/Notifications";
import CustomModal from "components/CustomModal";

function VerificationCode({ otp, verificationCodeStyle, onClick = () => { } }) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotification] = useNotification();
    const [code, setCode] = useState("");
    const [visible, setVisible] = useState(false);
    const handleChange = (otpData) => {
        setCode(otpData);
    };
    const onSubmit = () => {
        if (code.length == 4) {
            const payload = {
                email: history.location.state.email,
                otp: code,
            };
            dispatch(
                otpVerification(
                    payload,
                    (response) => {
                        history.push(ROUTES.HOMEPAGE)
                        sendNotification({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                        // setVisible(true);
                    },
                    (error) => {
                        sendNotification({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        } else {
            sendNotification({
                msg: ERROR_MESSAGES.OTP_CODE,
                variant: SNACK_VARIANT.ERROR,
            });
        }
    };
    const onResendClick = () => {
        const payload = {
            email: history.location?.state?.email,
        };
        dispatch(
            resendOtp(
                payload,
                (response) => {
                    setCode("")
                    sendNotification({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotification({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const handleProfileClick = () => {
        history.push(ROUTES.PROFILE);
    };
    return (
        <div className="overflow-hidden front_form min-vh-100">
            {visible && (
                <CustomModal isOpen={visible}>
                    <div>{STRINGS.VERIFIED}</div>
                    <CustomButton title={STRINGS.GO_TO_PROFILE} onClick={handleProfileClick} />
                </CustomModal>
            )}
            <div className="ls_form">
                <div className="text-center d-block mb-4">
                    <a className="logo_front">
                        <img src={Images.LOGO} alt="Locumbay" width={200} />
                    </a>
                    <h1 className="h6 mb-2">{STRINGS.VERIFICATION_CODE}</h1>
                    <p className="text-mute">
                        {STRINGS.EMAILED_CODE}
                        {history.location?.state?.email}
                        <br />

                        {STRINGS.ENTER_CODE}
                    </p>
                </div>

                <div className="mb-4 otp_screen">
                    <CustomOtpInput otp={code} handleChange={handleChange} />
                </div>

                <div className="mb-4 text-center">
                    <CustomButton
                        className="btn btn-md btn-primary"
                        title={STRINGS.SUBMIT}
                        onClick={onSubmit}
                    />
                </div>

                <div className="text-center text-mute mb-4">
                    {STRINGS.NOT_RECEIVE}
                    <a className="link_text ps-2" onClick={onResendClick}>
                        {STRINGS.RESEND}
                    </a>
                </div>

                <div className="text-center">
                    <a className="link_text" onClick={() => history.goBack()}>
                        {STRINGS.BACK}
                    </a>
                </div>
            </div>
        </div>
    );
}

export default VerificationCode;
