import { ROUTES, SNACK_MESSAGES, SNACK_VARIANT } from "shared/Constants";

import { useGoogleLogin } from "@react-oauth/google";
import useNotification from "hooks/Notifications";
import { useState } from "react";
import axios from "axios";
import { useDispatch } from "react-redux";
import { login } from "redux/Actions/Auth";
import { useHistory } from "react-router-dom";
import {STRINGS} from '../../shared/Constants';

export const useGoogle = () => {
    const dispatch = useDispatch();
    const [googleResponse, setGoogleResponse] = useState({});
    const [googleError, setGoogleError] = useState({});
    const [msg, sendNotification] = useNotification();
    const history = useHistory();
    const GoogleSignin = useGoogleLogin({
        onSuccess: async (tokenResponse) => {
            console.log(tokenResponse, "tokenresponse");
            const { access_token, refresh_token } = tokenResponse;
            let userInfo = await axios.get("https://www.googleapis.com/oauth2/v1/userinfo", {
                headers: {
                    Authorization: `Bearer ${access_token}`,
                },
            });
            const { given_name, family_name, email } = userInfo.data;
            const payload = {
                first_name: given_name,
                last_name: family_name,
                email,
                social_type: "google",
                social_id: access_token,
            };
            dispatch(
                login(
                    payload,
                    (response) => {
                        if (response.verified) {
                            history.push(ROUTES.HOMEPAGE);
                        } else {
                            history.push(ROUTES.VERIFICATION_CODE, { email: email });
                        }
                        sendNotification({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotification({
                            msg:error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );

            setGoogleResponse(tokenResponse);
        },
        onError: (error) => {
            setGoogleError(error);
            sendNotification({
                msg: SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                variant: SNACK_VARIANT.ERROR,
            });
        },
    });
    return { googleResponse, googleError, GoogleSignin };
};
