import CustomCheckBox from "components/CustomCheckBox";
import CustomModal from "components/CustomModal";
import SearchBar from "components/SearchBar";
import React from "react";
import { INPUT_TYPES } from "shared/Constants";
import { Images } from "shared/Images";
import "./right-slideview.scss";

const ListingModal = ({
    data = [],
    onChange = () => { },
    setShow = () => { },
    show = false,
    selectedData = [],
    handleSearchData = () => { },
    handleSelection = () => { },
    handleChangeKeyword = () => { },
    keyword,
    checkbox = "false",
    multipleSelection = true,
    disabled = false,
    isAllSelect = false,
    handleSelectAll = () => { },
    heading = "Select option user can access",
    buttonType = INPUT_TYPES.CHECKBOX,
    debounceTime = 0
}) => {
    // const checkBoxClass = checkbox ? "" : "";
    return (
        <>
        <div className={`overlay ${show ? "active" : ""}`} onClick={() => setShow(false)}></div>
        <div className={`slideView ${show ? "active" : ""}`} >
            <div className="slideView_head p-3">
                <button className="close-btn" onClick={() => setShow(false)}>
                    <img width={17} height={17} src={Images.CloseIcon} alt="Close" />
                </button>
            </div>

            <div className="d-flex align-items-center flex-md-row flex-column justify-content-lg-between mt-0 p-3 bg-note">
                <label className="mb-0">{heading}</label>
                {multipleSelection ? (
                    <div className="checkbox select_all mt-md-0 mt-2">
                        <input
                            type={buttonType}
                            disabled={disabled}
                            checked={isAllSelect}
                            onChange={(e) => handleSelectAll(e)}
                        />
                        <label>Select All</label>
                    </div>
                ) : null}
            </div>
            <div className="pt-3 card-body bg-white slideView_body">
                <div className="search_bar">
                    <SearchBar
                        searchAPI={handleSearchData}
                        keyword={keyword}
                        handleChangeKeyword={handleChangeKeyword}
                        debounceTime={debounceTime}
                    />
                </div>
                <div className="scrollBarSlide">
                    <ul className="list-unstyled m-0">
                        {data.length ? data.map((checkBoxItem, checkboxIndex) => (
                            <li key={checkboxIndex}>
                                <div key={checkboxIndex} className={`${buttonType === INPUT_TYPES.CHECKBOX ? "checkbox" : "radio-buttons"}`}>
                                    <input
                                        id={checkBoxItem.id}
                                        checked={checkBoxItem.value}
                                        type={buttonType}
                                        className={`${buttonType !== INPUT_TYPES.CHECKBOX ? "radio-button" : ""}`}
                                        name={"hello"}
                                        onChange={(e) => handleSelection(e, checkBoxItem)}
                                    />
                                    <label htmlFor="brnaches">{checkBoxItem.title}</label>
                                </div>
                            </li>
                        )) : <li><span className="text-center">No Data Found</span></li>}
                    </ul>
                </div>
            </div>
        </div>
        </>
    );
};

export default ListingModal;
