import CustomFileSelectButton from "components/CustomFileSelectButton";
import CustomTextInput from "components/CustomTextInput";
import React from "react";
import { INPUT_TYPES, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import { getDocsUrl } from "shared/Utilities";
import { getDefaultDateString, getLocalizedDateString } from "views/authenticated/constants";

function TableRow({
    value,
    index,
    handleUpload = () => {},
    onDeleteClick = () => {},
    onRowDeleteClick = () => {},
    onClick = () => {},
    onInputChange = () => { },
    onValidTillChange = () =>{},
    tableDefaultRows = 4
}) {
    return (
        <tr key={index} id={index}>
            <td>{index + 1}</td>
            <td>
                <CustomTextInput
                    value={value.title}
                    onChange={(event) => onInputChange(index, event)}
                    disabled={value.disabled}
                />
            </td>

            <td>
                <div className={`input_file ${value.uploadedFile ? "disabled" : ""}`}>
                    <CustomTextInput
                        id={index}
                        disabled={value.uploadedFile ? true : false}
                        type={INPUT_TYPES.FILE}
                        onChange={handleUpload}
                        accept="application/pdf"
                    />
                    <label
                        className="data_label form-control"
                        data-label="Choose File"
                        id={index}
                        disabled={value.uploadedFile ? true : false}
                    >
                        Choose File
                    </label>
                </div>
            </td>

            <td width={280}>
                {value.uploadedFile && (
                    <>
                        <div className="upload_doc">
                            <span onClick={() => onClick(index)}>
                                {value.uploadedFile?.name || value.title}
                            </span>
                            <span className="btn-danger" id={index} onClick={onDeleteClick}>
                                <svg
                                    width="20"
                                    height="20"
                                    viewBox="0 0 20 20"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path
                                        fill-rule="evenodd"
                                        clipRule="evenodd"
                                        d="M1.38109 19.389C1.65055 19.6584 1.91981 19.793 2.32402 19.793C2.72802 19.793 2.99747 19.6582 3.26695 19.389L10.4055 12.2504L17.5441 19.389C17.8136 19.6584 18.2176 19.793 18.4871 19.793C18.7565 19.793 19.1605 19.6582 19.43 19.389C19.9687 18.8503 19.9687 18.0421 19.43 17.5034L12.2914 10.3648L19.43 3.22618C19.9687 2.68746 19.9687 1.87927 19.43 1.34056C18.8913 0.801845 18.0831 0.801845 17.5444 1.34056L10.4058 8.47915L3.26719 1.34056C2.72848 0.801845 1.92029 0.801845 1.38157 1.34056C0.842861 1.87927 0.842861 2.68746 1.38157 3.22618L8.52016 10.3648L1.38157 17.5034C0.842861 18.0423 0.842861 18.8503 1.38157 19.3892L1.38109 19.389Z"
                                        fill="white"
                                    />
                                </svg>
                            </span>
                        </div>
                    </>
                )}
            </td>
            {value.hasOwnProperty("validTill") ? <td>
                <CustomTextInput
                    type={INPUT_TYPES.DATE}
                    value={getDefaultDateString(value.validTill) }
                    onChange={(event) => onValidTillChange(index, event)}
                    disabled={value.uploadedFile ? false:true}
                    min= {getDefaultDateString(new Date())}
                />
            </td>:<td>
            </td>}
            <td width={120}>
                <div className="btn_group">
                    {value.uploadedFile && (
                        <a
                            download
                            href={getDocsUrl(value.uploadedFile)}
                            target="_blank"
                            className="mx-2"
                        >
                            <img src={Images.Download} width="26" />
                        </a>
                    )}
                    {index >= tableDefaultRows && (
                        <button
                            className="btn-xs mx-2 btn btn-danger"
                            onClick={() => onRowDeleteClick(index)}
                        >
                            <img src={Images.TrashIcon} />
                            {/* <img id={formIndex} src={Images.TrashIcon} width="16" /> */}
                        </button>
                    )}
                </div>
            </td>
        </tr>
    );
}

export default TableRow;
