export const FETCH_JOB_LISTING = "FETCH_JOB_LISTING";
export const DELETE_JOB_POSTED = "DELETE_JOB_POSTED";
export const VIEW_JOB = "VIEW_JOB";
export const APPLY_JOB = "APPLY_JOB";

export const fetchJob = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: FETCH_JOB_LISTING,
        payload,
        success,
        fail,
    };
};
export const deleteJob = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: DELETE_JOB_POSTED,
        payload,
        success,
        fail,
    };
};
export const viewJob = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: VIEW_JOB,
        payload,
        success,
        fail,
    };
};
export const applyJob = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: APPLY_JOB,
        payload,
        success,
        fail,
    };
};
