import BreadCrumb from "components/BreadCrumb";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { BREAD_CRUMB_TEXT, LOCUM_ADMIN_ROUTES } from "shared/Constants";
import { STRINGS } from "./constants";
import { Images } from "shared/Images";

function InvoicesView() {
    const history = useHistory();
    const [toggleFilter, setToggleFilter] = useState(false);

    return (
        <div>
            <div className="d-flex align-items-center flex-wrap justify-content-between tab_full">
                <BreadCrumb
                    onClick={() => history.push(LOCUM_ADMIN_ROUTES.INVOICE)}
                    previousLink={BREAD_CRUMB_TEXT.INVOICE}
                    currentLink={`${BREAD_CRUMB_TEXT.CREATE} ${BREAD_CRUMB_TEXT.INVOICE}`}
                />

                <div className="d-flex admin_action flex-wrap">
                    <button className="btn btn-secondary">
                        {STRINGS.CREATE + " " + STRINGS.MANUAL + " " + STRINGS.INVOICE}
                    </button>
                    <button
                        className="btn btn-primary ms-2"
                        onClick={() => {
                            setToggleFilter((prev) => !prev);
                        }}
                    >
                        <img
                            src={toggleFilter ? Images.CloseWhiteIcon : Images.FilterIcon}
                            alt="filter"
                        />
                    </button>
                </div>
            </div>
        </div>
    );
}

export default InvoicesView;
