import React, { useEffect, useState } from "react";
import HtmlParser from "react-html-parser";
import { getSectionContent, getSectionTitle } from "./constants";

function PrivacyPolicy({ privacyPolicyData = {} }) {
    console.log('====================================');
    console.log(privacyPolicyData,"privacy policy data");
    console.log('====================================');
    const [sectionTitle, setSectionTitle] = useState([]);
    const [sectionContent, setSectionContent] = useState([]);
    useEffect(() => {
        setSectionTitle(getSectionTitle(privacyPolicyData?.sections))
        setSectionContent(getSectionContent(privacyPolicyData?.sections))
    },[privacyPolicyData])
     return (
        <div className="policy_sec">
            <nav className="sidebar_menu">
                <ul>
                    {sectionTitle.map((item, index) => {
                        return (
                            <li key={`title-${index}`}>
                                <a href={`#${index}`}>{HtmlParser(item)}</a>
                            </li>
                        );
                    })}
                </ul>
            </nav>

            <div className="sidebar_rht">
                {sectionContent.map((content,index) => {
                    return <section key={`section-${index}`} id={index}>
                    {content ? HtmlParser(content) : <p>
                        Lorem, ipsum dolor sit amet consectetur adipisicing elit. Officiis,
                        blanditiis expedita? Earum eligendi pariatur quaerat quos expedita ab
                        quibusdam ratione veniam in, mollitia fuga repudiandae?
                    </p>}
                </section>
                })}
            </div>
        </div>
    );
}

export default PrivacyPolicy;
