import CreateNewPassword from "components/CreateNewPassword";
import CustomButton from "components/CustomButton";
import CustomDropdown from "components/CustomDropDown";
import LogoutModal from "components/LogoutModal";
import CheckClickOutside from "components/OutsideClick";
import useNotification from "hooks/Notifications";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
    getHeader,
    logout,
    setAuthToken,
    setFooterData,
    setHeaderData,
    setToken,
} from "redux/Actions/Auth";
import {
    ADMIN_ROUTES,
    APP_VERSION,
    LABEL,
    LOCUM_ADMIN_ROUTES,
    ROUTES,
    ROUTES_TITLES,
    SNACK_VARIANT,
    STRINGS,
    USER_TYPES,
} from "shared/Constants";
import { Images } from "shared/Images";
import { getDocsUrl } from "shared/Utilities";
import "./style.scss";
import { USER_TYPE } from "shared/Constants";

function Header({ logo, headerList = [] }) {
    const dispatch = useDispatch();
    const [collapseOpen, setCollapseOpen] = useState(false);
    const [logoutVisible, setLogoutVisible] = useState(false);
    const [passwordVisible, setPasswordVisible] = useState(false);
    const profile = useSelector((state) => state.common.profileData);
    const header = useSelector((state) => state.dynamic?.header);
    const [showProfileDrop, setShowProfileDrop] = useState(false);
    const [msg, sendNotifications] = useNotification();
    const history = useHistory();
    const toggleCollapse = () => {
        document.documentElement.classList.toggle("nav-open");
        setCollapseOpen(!collapseOpen);
    };
    console.log(profile, "prifkelsjklfa");
    const onLoginClick = () => {
        history.push(ROUTES.LOGIN);
    };
    useEffect(() => {
        if (!Object.keys(header).length) {
            dispatch(
                getHeader(
                    (response) => {
                        dispatch(setHeaderData(response.data));
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        }
    }, []);
    return (
        <header className="header position-fixed">
            <div className="container d-flex align-items-center justify-content-between">
                <div className="logo" onClick={() => history.push(ROUTES.HOMEPAGE)}>
                    <a>
                        <img
                            src={
                                getDocsUrl(header?.sections?.[0]?.files?.[0]?.file) ||
                                Images.LogoFront
                            }
                            alt="Locumbay"
                            width={200}
                            className="img-fluid"
                        />
                    </a>
                    <span style={{ color: "white" }}>{APP_VERSION || ""}</span>
                </div>
                <nav className="navbar">
                    <ul>
                        {headerList.map((headerItem, index) => (
                            <li
                                key={index}
                                className={
                                    headerItem.routes == history.location.pathname ? "active" : ""
                                }
                                onClick={() => history.push(headerItem?.routes)}
                            >
                                {headerItem?.title}
                            </li>
                        ))}
                    </ul>
                </nav>

                <div className="d-flex justify-content-end align-items-center header_btns">
                    {!Object.keys(profile).length ? (
                        <div className="ls_btn">
                            <a onClick={onLoginClick}>Login</a> /{" "}
                            <a
                                onClick={() => {
                                    history.push(ROUTES.SIGNUP);
                                }}
                            >
                                Register
                            </a>
                        </div>
                    ) : (
                        <CheckClickOutside
                            onClick={() => {
                                setShowProfileDrop(false);
                            }}
                        >
                            <div
                                className="d-flex align-items-center userProfile position-relative"
                                onClick={() => {
                                    setShowProfileDrop((prevState) => !prevState);
                                }}
                                cursor="pointer"
                            >
                                <div className="avtar_info me-3" cursor="pointer">
                                    <span>
                                        {profile.user_type == USER_TYPE.PHARMACY
                                            ? profile?.owner_info?.first_name ||
                                              profile?.pharmacy_info?.company_name
                                            : profile?.profile?.first_name}
                                    </span>
                                </div>
                                <figure className="avtar avtar50 mb-0" cursor="pointer">
                                    <img
                                        src={
                                            getDocsUrl(profile.user_type == USER_TYPE.PHARMACY ? profile?.owner_info?.photo : profile?.profile?.photo) ||
                                            Images.DefaultPic
                                        }
                                        alt="Profile"
                                    />
                                </figure>

                                <CustomDropdown show={showProfileDrop}>
                                    <div className="user-body">
                                        {[
                                            {
                                                title: LABEL.MY_PROFILE,
                                                image:
                                                    getDocsUrl(profile.user_type == USER_TYPE.PHARMACY ? profile?.owner_info?.photo : profile?.profile?.photo) ||
                                                    Images.DefaultPic,
                                                onClick: () => {
                                                    setShowProfileDrop(false);
                                                    history.push(ROUTES.PROFILE);
                                                },
                                            },
                                            {
                                                title: ROUTES_TITLES.DASHBOARD,
                                                image: Images.dashboard,
                                                onClick: () => {
                                                    setShowProfileDrop(false);
                                                    profile?.user_type === 1
                                                        ? history.push(ADMIN_ROUTES.DASHBOARD)
                                                        : history.push(
                                                              LOCUM_ADMIN_ROUTES.DASHBOARD,
                                                          );
                                                },
                                            },
                                            {
                                                title: LABEL.CHANGE_PASSWORD,
                                                image: Images.ChangePassword,
                                                onClick: () => {
                                                    setPasswordVisible(true);
                                                },
                                            },
                                            {
                                                title: LABEL.LOGOUT,
                                                image: Images.logout,
                                                onClick: () => {
                                                    setLogoutVisible(true);
                                                },
                                            },
                                        ].map((i, idx) => {
                                            return (
                                                <div
                                                    key={idx}
                                                    className="dropDown-list"
                                                    onClick={() => {
                                                        i.onClick();
                                                        setShowProfileDrop((prev) => !prev);
                                                    }}
                                                >
                                                    <em>
                                                        <img
                                                            src={i?.image}
                                                            className="img-fluid"
                                                            alt="icon"
                                                            width={20}
                                                        />
                                                    </em>
                                                    <span>{i.title} </span>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </CustomDropdown>
                            </div>
                        </CheckClickOutside>
                    )}

                    <button className="navbar-toggler" onClick={toggleCollapse}>
                        <span className="bar"></span>
                        <span className="bar"></span>
                        <span className="bar"></span>
                    </button>
                </div>
            </div>
            {passwordVisible && (
                <CreateNewPassword
                    showNewPassword={passwordVisible}
                    setPasswordVisible={setPasswordVisible}
                />
            )}
            {logoutVisible && (
                <LogoutModal
                    onLogout={() => {
                        history.push(ROUTES.HOMEPAGE);
                        dispatch(
                            logout(
                                (response) => {
                                    console.log(response, "response");
                                    sendNotifications({
                                        msg: response.data.message,
                                        variant: SNACK_VARIANT.SUCCESS,
                                    });
                                    localStorage.removeItem("persist:root");
                                },
                                (error) => {
                                    console.log(error, "error");
                                    sendNotifications({
                                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                                        variant: SNACK_VARIANT.ERROR,
                                    });
                                },
                            ),
                        );
                    }}
                    showLogout={logoutVisible}
                    setShowLogout={setLogoutVisible}
                />
            )}
        </header>
    );
}

export default Header;
