import useNotification from "hooks/Notifications";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getCookiePolicy, getPrivacyPolicy, getTermsAndConditions, setCookiePolicyData, setPrivacyPolicyData, setTermsAndConditionData } from "redux/Actions/Auth";
import { LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import TabNavigation from "components/CustomTabNavigator";
import { HEADER_DATA } from "views/HomePage/constants";
import "./styles.scss";
import Header from "components/Header";
import Footer from "components/Footer/Footer";
import { TERMS_AND_CONDITIONS, TERMS_AND_CONDITIONS_TABS } from "./constants";
import TermsAndConditions from "./termsAndConditions";
import PrivacyPolicy from "./PrivacyPolicy";
import CookiePolicy from "./CookiePolicy";
import TermsAndConditionsPage from "./termsAndConditions";
function TermsAndCondition(props) {
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [activeTab, setActiveTab] = useState(LABEL.TERMS_AND_CONDITIONS);
    const termsAndCondition = useSelector(state => state.dynamic?.termsAndCondition)
    const privacyPolicy = useSelector(state => state.dynamic?.privacyPolicy)
    const cookiePolicy = useSelector(state => state.dynamic?.cookiePolicy)
    useEffect(() => {
        if (!Object.keys(termsAndCondition).length) {
            dispatch(
                getTermsAndConditions(
                    (response) => {
                        dispatch(setTermsAndConditionData(response.data))
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );    
        }
        if (!Object.keys(privacyPolicy).length) {
            dispatch(
                getPrivacyPolicy(
                    (response) => {
                        dispatch(setPrivacyPolicyData(response.data))
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );    
        }
        if (!Object.keys(cookiePolicy).length) {
            dispatch(
                getCookiePolicy(
                    (response) => {
                        dispatch(setCookiePolicyData(response.data))
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );    
        }
    }, []);
    const onTabPress = (event) => {
        setActiveTab(event.target.id);
    };
    return (
        <>
            <Header headerList={HEADER_DATA} />
            <div className="inner_pages">
                <div className="container pt-4">
                    <TabNavigation
                        onClick={onTabPress}
                        tabsData={TERMS_AND_CONDITIONS_TABS}
                        activeTab={activeTab}
                    />
                    <RenderTab
                        activeTab={activeTab}
                        termsAndConditionData={termsAndCondition}
                        privacyPolicyData={privacyPolicy}
                        cookiePolicyData ={cookiePolicy}
                    />
                </div>
            </div>
            <Footer />
        </>
    );
}

export default TermsAndCondition;
const RenderTab = ({ activeTab, termsAndConditionData, privacyPolicyData,cookiePolicyData }) => {
    switch (activeTab) {
        case LABEL.TERMS_AND_CONDITIONS:
            return <TermsAndConditionsPage termsAndConditionData={termsAndConditionData} />;
        case LABEL.PRIVACY_POLICY:
            return <PrivacyPolicy privacyPolicyData={privacyPolicyData} />;
        case LABEL.COOKIE_POLICY:
            return <CookiePolicy cookiePolicyData={cookiePolicyData}/>;
        default:
            return <TermsAndConditionsPage termsAndConditionData={termsAndConditionData} />;
    }
};
