// libs
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

// hooks
import useNotification from "hooks/Notifications";

// custom components
import CustomButton from "components/CustomButton";
import CustomCheckBox from "components/CustomCheckBox";
import CustomTable from "components/CustomTable";
import CustomTextInput from "components/CustomTextInput";
import DocumentViewerModal from "components/DocumentViewer";

// constants
import {
    ERROR_MESSAGES,
    FIVE_MB_SIZE,
    INPUT_TYPES,
    LABEL,
    SERVICES,
    SNACK_VARIANT,
    STRINGS,
} from "shared/Constants";
import {
    createCsValue,
    createProfileVerification,
    createUploadPayload,
    DOCUMENT_TYPE_NAME,
    getComputerSystemsData,
    getDefaultDateString,
    getInititalValueDocuments,
    getTableData,
} from "views/authenticated/constants";
import { Images } from "shared/Images";
import { getDocsUrl } from "shared/Utilities";
import {
    deleteDocuments,
    locumProfileDetails,
    saveProfileVerification,
    uploadDocuments,
} from "redux/Actions/Auth";

// styles
import "./styles.scss";


function ProfileVerification({
    csInUse = [],
}) {
    const dispatch = useDispatch();
    const profileDetails = useSelector((state) => state.common.locumProfileDetails);
    const verificationData = useSelector(
        (state) => state.common.locumProfileDetails?.profileVerification,
    );
    const [computerSystems, setComputerSystems] = useState(
        getComputerSystemsData(csInUse, verificationData?.cs),
    );
    const [tableData, setTableData] = useState(getTableData(verificationData?.documents));
    const [viewDoc, setViewDoc] = useState(false);
    const [documentUrl, setDocumentUrl] = useState("");
    const [regNumber, setRegNumber] = useState(verificationData?.reg_num);
    const [modalOpacity, setModalOpacity] = useState(false);
    const [validTill, setValidTill] = useState(
        getDefaultDateString(verificationData?.valid_till) || "",
    );
    // setIdentityValidTill
    const [identityValidTill,setIdentityValidTill] = useState(getDefaultDateString(verificationData?.identity_valid_till) || "",)
    const [errorMessage, setErrorMessage] = useState({
        reg_number: "",
        valid_till: "",
    });
    const [msg, sendNotifications] = useNotification();
    useEffect(() => {
        getComputerSystemsData(csInUse, verificationData?.cs);
    }, [verificationData, csInUse]);
    const onSubmit = () => {
        if (!regNumber) {
            setErrorMessage((prev) => {
                return {
                    ...prev,
                    reg_number: ERROR_MESSAGES.REGISTRATION_NO,
                };
            });
            return;
        }
        // if (validTill && !tableData[1].uploadedFile) {
        //     sendNotifications({
        //         msg: ERROR_MESSAGES.INSURANCE_DOCUMENT,
        //         variant: SNACK_VARIANT.ERROR,
        //     });
        // } else {
            const payload = createProfileVerification(
                { reg_number: regNumber, valid_till: tableData[1].validTill, rtw_valid_till:tableData[0].validTill },
                computerSystems,
            );
            dispatch(
                saveProfileVerification(
                    payload,
                    (response) => {
                        dispatch(
                            locumProfileDetails({
                                ...profileDetails,
                                profileVerification: {
                                    ...verificationData,
                                    valid_till: validTill,
                                    identity_valid_till:identityValidTill,
                                    reg_num: regNumber,
                                    cs: [...createCsValue(computerSystems)],
                                },
                            }),
                        );
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        // }
    };
    const onCheckboxSelect = (event) => {
        const itemIndex = computerSystems.findIndex((item, index) => {
            return item.id == event.target.id;
        });
        const newArr = [...computerSystems];
        newArr[itemIndex].value = event.target.checked;
        setComputerSystems(newArr);
    };
    const handleUpload = (event) => {
        if (event.target.files[0].size < FIVE_MB_SIZE) {
            const title = tableData[event.target.id].title;
            const payload = createUploadPayload(event.target.files[0], event.target.id, title);
            dispatch(
                uploadDocuments(
                    payload,
                    (response) => {
                        const tempArr = [...tableData];
                        const tempData = tempArr[event.target.id];
                        tempData.uploadedFile = response.data.document;
                        tempData.docId = response.data.id;
                        tempData.document_type = response.data.document_type;
                        tempData.disabled = true;
                        tempArr[event.target.id] = tempData;
                        dispatch(
                            locumProfileDetails({
                                ...profileDetails,
                                profileVerification: {
                                    ...verificationData,
                                    documents: [...getInititalValueDocuments(tempArr)],
                                },
                            }),
                        );
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                        setTableData(tempArr);
                        event.target.value = null;
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        } else {
            sendNotifications({
                msg: ERROR_MESSAGES.SIZE,
                variant: SNACK_VARIANT.ERROR,
            });
        }
        event.target.value = null;
    };
    const onAddServiceClick = () => {
        window.scrollTo(0, document.body.scrollHeight);
        setTableData((prev) => [
            ...prev,
            {
                id: prev.length + 1,
                title: SERVICES.OTHER,
                uploadedFile: null,
                document_type: 4,
                disabled: false,
            },
        ]);
    };
    const handleDeleteClick = (itemId,splice=false) => {
        dispatch(
            deleteDocuments(
                { id: tableData[itemId].docId },
                (response) => {
                    const tempArr = [...tableData];
                    const tempData = tempArr[itemId];
                    tempData.uploadedFile = null;
                    tempData.validTill = ""
                    if (tableData[itemId].document_type == DOCUMENT_TYPE_NAME.OTHER) {
                        tempData.disabled = false
                    }
                    tempArr[itemId] = tempData;
                    if (splice) {
                        tempArr.splice(itemId,1)
                    }
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                    dispatch(
                        locumProfileDetails({
                            ...profileDetails,
                            profileVerification: {
                                ...verificationData,
                                documents: [...getInititalValueDocuments(tempArr)],
                            },
                        }),
                    );
                    setTableData(tempArr);
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };

    const onDeleteClick = (event) => {
        const itemId = event.target.id;
        handleDeleteClick(itemId,false);
    };
    const onRowDeleteClick = (index) => {
        if (tableData[index].uploadedFile) {
            handleDeleteClick(index,true);
        } else {
            tableData.splice(index, 1);
            setTableData([...tableData]);
        }
    };
    const onRowClick = (index) => {
        if (tableData[index].uploadedFile) {
            setModalOpacity(true);
            setDocumentUrl(getDocsUrl(tableData[index].uploadedFile));
            setViewDoc(true);
        }
    };
    const onInputChange = (index, event) => {
        tableData[index].title = event.target.value;
        setTableData([...tableData]);
    };
    const onValidTillChange = (index, event) => {
        tableData[index].validTill = event.target.value;
        setTableData([...tableData])
    }
    console.log(tableData,"table data");
    return (
        <div className="tab_content">
            <div className="row">
                <div className="col-md-6 mb-4">
                    <CustomTextInput
                        id={"reg_number"}
                        type={INPUT_TYPES.NUMBER}
                        errorMessage={errorMessage.reg_number}
                        value={regNumber}
                        onChange={(event) => {
                            if (!event.target.value) {
                                setErrorMessage((prev) => {
                                    return {
                                        ...prev,
                                        reg_number: ERROR_MESSAGES.VALID_REGISTRATION_NO,
                                    };
                                });
                            } else {
                                setErrorMessage((prev) => {
                                    return {
                                        ...prev,
                                        reg_number: "",
                                    };
                                });
                            }
                            event.target.value.length <= 16 && setRegNumber(event.target.value);
                        }}
                        titleText={LABEL.REGISTRATION_NO}
                        placeholder={STRINGS.ENTER_HERE}
                        // onChange={onChange}
                    />
                </div>
                {/* <div className="col-md-6 mb-4">
                    <CustomTextInput
                        titleText={LABEL.VALID_INSURANCE}
                        type={INPUT_TYPES.DATE}
                        value={validTill}
                        onChange={(event) => {
                            setValidTill(event.target.value);
                        }}
                        placeholder={STRINGS.ENTER_HERE}
                    />
                </div>
                <div className="col-md-6 mb-4">
                    <CustomTextInput
                        titleText={LABEL.VALID_IDENTITY}
                        type={INPUT_TYPES.DATE}
                        value={identityValidTill}
                        onChange={(event) => {
                            setIdentityValidTill(event.target.value);
                        }}
                        placeholder={STRINGS.ENTER_HERE}
                    />
                </div> */}
            </div>
            <div className="row">
                <div className="col-12 mb-3">
                    <label className="mb-3">{LABEL.COMPUTER_SYSTEMS}</label>
                    <div className="row">
                        <CustomCheckBox
                            checkBoxData={computerSystems}
                            onCheckboxSelect={onCheckboxSelect}
                        />
                    </div>
                </div>
                <div className="col-12">
                    <div className="d-flex align-items-center justify-content-between mb-2">
                        <label className="mb-0">{LABEL.PROFILE_DOCUMENTS}</label>
                        <CustomButton
                            title={STRINGS.ADD_MORE_SERVICES}
                            onClick={onAddServiceClick}
                            className="btn btn-sm btn-outline-secondary add_btn"
                            Icon={Images.PlusIcon}
                        />
                    </div>
                </div>
                <div className="col-12">
                    <div className="table-responsive">
                        <CustomTable
                            data={tableData}
                            activeTab={STRINGS.PROFILE_TABLE}
                            handleUpload={handleUpload}
                            onClick={onRowClick}
                            onDeleteClick={onDeleteClick}
                            onRowDeleteClick={onRowDeleteClick}
                            onInputChange={onInputChange}
                            onValidTillChange={onValidTillChange}
                            tableDefaultRows={4}
                        />
                    </div>
                    {viewDoc && (
                        <DocumentViewerModal
                            viewDoc={viewDoc}
                            modalOpacity={modalOpacity}
                            documentUrl={documentUrl}
                            setViewDoc={setViewDoc}
                            onLoadSuccess={() => {
                                setModalOpacity(false);
                            }}
                        />
                    )}
                </div>
                <div className="col-12 text-center mt-2">
                    <span className="error text-start">{STRINGS.DOCUMENT_INFORMATION}</span>
                </div>
                <div className="col-12 text-center mt-4">
                    <CustomButton
                        title={STRINGS.SAVE}
                        onClick={onSubmit}
                        className="btn btn-md btn-primary"
                    />
                </div>
            </div>
        </div>
    );
}

export default ProfileVerification;
