import React, { useState } from "react";
import ReactPaginate from "react-paginate";
import "./style.scss";

import DialogView from "components/DialogView";
import CheckClickOutside from "components/OutsideClick";
import { PAGE_COUNT } from "shared/Constants";
import { Images } from "shared/Images";

export function Pagination({
  itemsCount,
  itemsPerPage,
  handlePageChange,
  handleItemsPerPageChange,
  activePage
}) {

  const pageCount = Math.ceil(itemsCount / itemsPerPage);
  const [pageCountShow, setPageCountShow] = useState(false);

  const togglePageCountShow = () => {
    setPageCountShow(prevState => !prevState)
  }

  return (
    <div className="row">
      <div className="col-sm-12 col-lg-5 my-2">
        <div className="data_table_info d-flex align-items-center">
          <span className="me-2">Entries</span>
          <CheckClickOutside onClick={() => setPageCountShow(false)}>
            <button
              className="white_control"
              onClick={togglePageCountShow}
            >
              <label>{itemsPerPage}</label>
              <em><img src={Images.ArrowDown} alt="" width={20} /></em>
            </button>
            <div className="list-unstyled dropdown_entries" hidden={!pageCountShow}>
              <DialogView>
                {PAGE_COUNT.map((i, idx) => {
                  return (
                    <div
                      key={idx}
                      className="list"
                      onClick={() => {
                        togglePageCountShow();
                        handleItemsPerPageChange(i)
                      }}
                    >
                      {i}
                    </div>
                  );
                })}
              </DialogView>
            </div>
          </CheckClickOutside>
          <span className="ms-2">{`Showing 1 to ${itemsPerPage} of ${itemsCount} Entries`}</span>
        </div>
      </div>
      <div className="col-sm-12 col-lg-7 my-2">
        <ReactPaginate
          breakLabel="..."
          nextLabel="next >"
          onPageChange={handlePageChange}
          pageRangeDisplayed={5}
          pageCount={pageCount}
          previousLabel="< previous"
          renderOnZeroPageCount={null}
          className="d-flex justify-content-end pagination"
          forcePage={activePage - 1}
        />
      </div>
    </div>
  );
}
