import useNotification from "hooks/Notifications";
import React from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import { VACANCY } from "../constants";
import BreadCrumb from "components/BreadCrumb";
import { ADMIN_ROUTES, ROUTES, SNACK_MESSAGES, SNACK_VARIANT } from "shared/Constants";
import JobView from "components/JobView";
import DeleteModal from "components/DeleteModal";
import { deleteVacancies } from "redux/Actions/PharmacyAdminDashboard";
import useGetIdFromPath from "hooks/Utilities/useGetIdFromPath";

const ViewVacancy = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    const id = useGetIdFromPath();
    const [msg, sendNotification] = useNotification();
    const [deleteModal, setDeleteModal] = useState(false);

    const onDelete = () => {
        let data = {
          vacancies: { vacancy_list: [id] },
        };

        dispatch(
          deleteVacancies(data, (res) => {
            setDeleteModal(false);
            history.push(ADMIN_ROUTES.VACANCIES);
            sendNotification({
              msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
              variant: SNACK_VARIANT.SUCCESS,
            });
          }),
          (err) => {
            setDeleteModal(false);
            sendNotification({
              msg: err?.data?.message
                ? err.data.message
                : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        );
      };

    const handleEditVacancy = () => {
        history.push(`${ADMIN_ROUTES.EDIT_VACANCY.slice(0,-4)}/${id}`);
    }

    const handleDeleteVacancyClick = () => {
        setDeleteModal(true)
    }

    const toggleDeleteModal = () => {
        setDeleteModal(prevState => !prevState)
    }

    return (
        <>
            <div className="d-flex flex-wrap align-items-center justify-content-between">
                <BreadCrumb
                    onClick={() => history.push(ADMIN_ROUTES.VACANCIES)}
                    previousLink={VACANCY.VACANCIES}
                    currentLink={VACANCY.DETAILS}
                />
                <div className="btn_group">
                    <button className="btn btn-sm btn-secondary" onClick={handleEditVacancy}>Edit</button>
                    <button className="btn btn-sm btn-danger" onClick={handleDeleteVacancyClick}>Delete</button>
                </div>
            </div>
            <hr className="divider mt-0" />
            <JobView
                isCandidateView={true}
            />
            <DeleteModal
                show={deleteModal}
                toggleModal={toggleDeleteModal}
                onClick={onDelete}
                medium={true}
            />
        </>
    );
};

export default ViewVacancy;
