export const getMaxDate = () => {
    const validDate = new Date().getDate();
    const validMonth = `0${new Date().getMonth() + 1}`;
    const validYear = new Date().getFullYear() - 18;
    return `${validYear}-${validMonth}-${validDate}`;
};
export const loginClick = (email, password) => {
    const emailValid = emailValidation(email);
    const passwordValid = passwordValidation(password);
    let emailMsg = "";
    let passwordMsg = "";
    if (!emailValid) {
        if (!email) {
            emailMsg = ERROR_MESSAGES.EMAIL_EMPTY;
        } else {
            emailMsg = ERROR_MESSAGES.EMAIL_VALID;
        }
    } else {
        emailMsg = "";
    }
    if (!passwordValid) {
        if (!password) {
            passwordMsg = ERROR_MESSAGES.PASSWORD_EMPTY;
        } else {
            passwordMsg = ERROR_MESSAGES.PASSWORD_VALID;
        }
    } else {
        passwordMsg = "";
    }
    return { emailMsg, passwordMsg };
};
export const validateConfirmPassword = (val, registerName, watch) => {
    if (registerName === STRINGS.CONFIRM_PASSWORD) {
        if (watch(INPUT_TYPES.PASSWORD) != val) {
            return `${ERROR_MESSAGES.PASSWORD_VALID}`;
        }
    }
};
export const typeSelection = (data, setTypeToggle) => {
    if (PASSWORD_TYPES.includes(data)) {
        setTypeToggle((prev) => {
            if (prev[data] === INPUT_TYPES.TEXT) {
                return {
                    ...prev,
                    [data]: INPUT_TYPES.PASSWORD,
                };
            } else {
                return {
                    ...prev,
                    [data]: INPUT_TYPES.TEXT,
                };
            }
        });
    } else {
        return INPUT_TYPES.TEXT;
    }
};

