  export const makeJobDaysListing = (startDate, endDate) => {
    let data = [];
    let diff = moment(endDate).diff(startDate, 'days');
    for (let i = 0; i <= diff; i++) {
      let day = moment(startDate).add(i, 'days').day();
      let newData = {
        day: day == 0 ? 6 : day - 1,
        opening_time: '',
        closing_time: '',
        lunch_start: '',
        lunch_end: '',
        branch: '',
        isSelected: false,
        date: moment(startDate).add(i, 'days').format('DD MMM YYYY'),
      }
      data.push(newData);
    }
    return data;
  };

  export const makeJobDaysListingForEdit = (startDate, endDate, selectedDays) => {
    let data = [];
    let diff = moment(endDate).diff(startDate, 'days');
    for (let i = 0; i <= diff; i++) {
      let day = moment(startDate).add(i, 'days').day();
      let dayData = selectedDays.find(item => item.day == (day == 0 ? 6 : day - 1))
      data.push({
        day: day == 0 ? 6 : day - 1,
        opening_time: dayData?.start_time || '',
        closing_time: dayData?.end_time || '',
        lunch_start: dayData?.lunch_start_time || '',
        lunch_end: dayData?.lunch_end_time || '',
        branch: '',
        isSelected: dayData ? true : false,
        date: moment(startDate).add(i, 'days').format('DD MMM YYYY'),
      });
    }
    return data;
  };
  
  

export const generateMockData = (date) => {
    const events = [];
    for (let i = 0; i < 10; i++) {
        const randomDate = generateRandomDate(
            moment(date).startOf("month").toDate(),
            moment(date).endOf("month").toDate(),
        );
        events.push({
            start: randomDate.toDate(),
            end: randomDate.add(1, "hour").toDate(),
            title: randomEventName[parseInt(Math.random() * randomEventName.length - 1)],
        });
    }
    return events;
};

const generateRandomDate = (start, end) => {
    return moment(new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime())));
};

//function to get times ago from current time. eg: 30 mins ago
export function timeAgo(created_at) {
    const now = new Date();
    const created = new Date(created_at);
    const elapsed = now - created;

    // Get the elapsed time in seconds
    const seconds = Math.floor(elapsed / 1000);

    // Convert seconds to the appropriate unit of time
    if (seconds < 60) {
        return `${seconds} seconds ago`;
    } else if (seconds < 3600) {
        const minutes = Math.floor(seconds / 60);
        return `${minutes} ${minutes === 1 ? "minute" : "minutes"} ago`;
    } else if (seconds < 86400) {
        const hours = Math.floor(seconds / 3600);
        return `${hours} ${hours === 1 ? "hour" : "hours"} ago`;
    } else {
        const days = Math.floor(seconds / 86400);
        return `${days} ${days === 1 ? "day" : "days"} ago`;
    }
}

// This function returns the first 'maxCharacter' characters of a given string, or "U" if the string is empty or null.
// The function takes two arguments:
//    - 'str' : The input string for which to extract the first characters.
//    - 'maxCharacter': The number of characters to extract from the start of the string.
export function firstCharacter(str, maxCharacter) {
    return str?.length ? str?.substring(0, maxCharacter) : "U";
}

export const onCheckboxSelection = (e, setState, isSingleSelection = false) => {
    setState((prevState) => {
        let copyPrevState = deepCopy(prevState);
        let index = copyPrevState.findIndex((item) => item.id == e.target.id);
        if (isSingleSelection) {
            copyPrevState.forEach((item) => {
                if (isSingleSelection) {
                    if (item.id !== e.target.id) {
                        // For single selection case, uncheck all other items
                        item.value = false;
                    }
                    copyPrevState[index].value = e.target.checked;
                }
            });
        } else {
            // For multiple selection case, toggle the checked value of the clicked item
            copyPrevState[index].value = e.target.checked;
        }
        return copyPrevState;
    });
};

