export const createVacancyPayload = (vacancyData, jobTimingData) => {
    let shift_details = [];
    jobTimingData.forEach(item => {
        if(item.isSelected){
            shift_details.push({
                day: item.day,
                start_time: item.opening_time,
                end_time: item.closing_time,
                lunch_start_time: item.lunch_start,
                lunch_end_time: item.lunch_end
            })
        }
    })
    return {
        branch_id: vacancyData.branch_id.id,
        job_name: vacancyData.job_name,
        start_date: moment(vacancyData.selectedDay.from).format("YYYY-MM-DD"),
        end_date: moment(vacancyData.selectedDay.to).format("YYYY-MM-DD"),
        description: vacancyData.description,
        start_rate: vacancyData.start_rate,
        end_rate: vacancyData.end_rate,
        vacancy_services: vacancyData.vacancy_services.map(item => item.id),
        shift_details
    }
}


export function dateWithWeekDay(dateString) {
    const formattedDate = new Date(dateString);
    const options = {weekday: 'long', day: 'numeric', month: 'short', year: 'numeric'};
    return formattedDate.toLocaleDateString("en-US", options);
  }

  export function dateWithMonthString(dateString) {
    const formattedDate = new Date(dateString);
    const options = {day: 'numeric', month: 'long', year: 'numeric'};
    return formattedDate.toLocaleDateString("en-US", options);
  }
  
  export const getDatefromDayOfWeek = (startDate, day) => {
    let date = moment(startDate).add(day, "days").day();  
    return moment(startDate).add(date, "days").format("DD MMM YYYY");
  };
  
  export function convertTimeToAmPm(timeString) {
    const [hour, minute] = timeString.split(":");
    const amOrPm = hour < 12 ? "AM" : "PM";
    let hourInAmPmFormat = hour % 12;
    if (hourInAmPmFormat === 0) {
      hourInAmPmFormat = 12;
    }
  
    return `${hourInAmPmFormat}:${minute} ${amOrPm}`;
  }
  
  
  
