import CustomButton from "components/CustomButton";
import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
    deleteEducationDetails,
    deleteJobDetails,
    locumProfileDetails,
    saveEducationData,
    saveJobHistoryData,
} from "redux/Actions/Auth";
import {
    ERROR_MESSAGES,
    INPUT_TYPES,
    LABEL,
    SNACK_MESSAGES,
    SNACK_VARIANT,
    STRINGS,
} from "shared/Constants";
import { Images } from "shared/Images";
import {
    addEducationDetails,
    addJob,
    checkAndDelete,
    checkAndUpdate,
    createDefaultData,
    createEducationDefaultData,
    createEducationHistory,
    createEducationHistoryList,
    createEducationList,
    createEducationListClick,
    createEducationPayload,
    createHistoryDefault,
    createItemData,
    createJobHistoryData,
    createJobHistoryList,
    createPayloadData,
} from "../../constants";
import CustomEducationForm from "./widget/CustomEducationHistory";

function EducationDetails({}) {
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const profileDetails = useSelector((state) => state.common.locumProfileDetails);
    const cvManage = useSelector((state) => state.common.locumProfileDetails?.cvManage) || {};
    const educationData =
        useSelector((state) => state.common.locumProfileDetails?.cvManage?.edu_history) || [];
    const [educationHistoryList, setEducationHistoryList] = useState([
        ...createEducationList(educationData.length, educationData),
    ]);
    // const [educationAddClick, setEducationAddClick] = useState(false);
    const educationAddClick = useRef(false);
    const [historyData, setHistoryData] = useState(createEducationDefaultData(educationData));
    const onSubmit = (data, event) => {
        const payload = createEducationPayload(data);
        dispatch(
            saveEducationData(
                { edu_history: JSON.stringify(payload) },
                (response) => {
                    dispatch(
                        locumProfileDetails({
                            ...profileDetails,
                            cvManage: {
                                ...cvManage,
                                edu_history: [...response.data],
                            },
                        }),
                    );
                    if (educationAddClick) {
                        setEducationHistoryList([
                            ...createEducationListClick(educationHistoryList, true),
                            addEducationDetails(educationHistoryList.length),
                        ]);
                        // setEducationAddClick(false);
                        educationAddClick.current = false;
                    } else {
                        setEducationHistoryList(
                            createEducationListClick(educationHistoryList, true),
                        );
                    }
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const onEditClick = () => {
        setEducationHistoryList(createEducationListClick(educationHistoryList, false));
    };
    const onDeleteClick = (formItemIndex) => {
        const index = formItemIndex.split("_")[2];
        if (historyData[index]) {
            let api_payload = [...historyData];
            (() => {
                api_payload = api_payload.map((item, index) => {
                    return {
                        institution_name: item[`institution_name_${index}`],
                        qualification: item[`qualification_${index}`],
                        start_date: item[`start_date_${index}`],
                        end_date: item[`end_date_${index}`],
                        description: item[`description_${index}`],
                    };
                });
                api_payload.splice(index, 1);
            })();
            dispatch(
                saveEducationData(
                    { edu_history: JSON.stringify(api_payload) },
                    (response) => {
                        dispatch(
                            locumProfileDetails({
                                ...profileDetails,
                                cvManage: {
                                    ...cvManage,
                                    edu_history: [...response.data],
                                },
                            }),
                        );
                        if (educationAddClick) {
                            setEducationHistoryList([
                                ...createEducationListClick(educationHistoryList, true),
                                addEducationDetails(educationHistoryList.length),
                            ]);
                            // setEducationAddClick(false);
                            educationAddClick.current = false;
                        } else {
                            setEducationHistoryList(
                                createEducationListClick(educationHistoryList, true),
                            );
                        }
                        sendNotifications({
                            msg: SNACK_MESSAGES.DELETED_SUCCESS,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
            // dispatch(
            //     deleteEducationDetails(
            //         { id: educationData[educationHistoryIndex].id },
            //         (response) => {
            //             // educationData.splice(educationHistoryIndex, 1);
            //             // educationHistoryList.splice(educationHistoryIndex, 1);
            //             // historyData.splice(educationHistoryIndex, 1);
            //             // setHistoryData([...historyData]);
            //             // setEducationHistoryList([...educationHistoryList]);
            //             dispatch(
            //                 locumProfileDetails({
            //                     ...profileDetails,
            //                     cvManage: {
            //                         ...cvManage,
            //                         edu_history: [...educationData],
            //                     },
            //                 }),
            //             );
            //             sendNotifications({
            //                 msg: response.message,
            //                 variant: SNACK_VARIANT.SUCCESS,
            //             });
            //         },
            //         (error) => {
            //             sendNotifications({
            //                 msg: error || STRINGS.SOMETHING_WENT_WRONG,
            //                 variant: SNACK_VARIANT.ERROR,
            //             });
            //         },
            //     ),
            // );
        } else {
            educationHistoryList.splice(index, 1);
            historyData.splice(index, 1);
            setHistoryData([...historyData]);
            setEducationHistoryList([...educationHistoryList]);
        }
    };
    useEffect(() => {
        setHistoryData([...createEducationDefaultData(educationData)]);
        if (!educationAddClick.current) {
            setEducationHistoryList([...createEducationList(educationData.length, educationData)]);
        }
    }, [JSON.stringify(educationData), educationData.length]);
    return (
        <div className="card mb-4">
            <div className="card-body">
                <div className="title_icon d-flex mb-3">
                    <h6 className="h6 mb-0">{STRINGS.EDUCATIONDETAIL}</h6>
                    <em className="ms-2" onClick={onEditClick}>
                        <img src={Images.Pencil} width="16" />
                    </em>
                </div>
                <CustomEducationForm
                    id="educationHistory"
                    formData={educationHistoryList}
                    onSubmit={onSubmit}
                    onDeleteClick={onDeleteClick}
                    defaultValues={createHistoryDefault(historyData)}
                />
                <div className="btn_group text-center">
                    <button
                        type={"submit"}
                        form={"educationHistory"}
                        onClick={() => {
                            // setEducationAddClick(true);
                            educationAddClick.current = true;
                        }}
                        className="btn btn-md btn-outline-secondary"
                    >
                        <svg
                            className="me-2"
                            width="18"
                            height="19"
                            viewBox="0 0 29 28"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                d="M14.9512 0C13.9337 0 13.1091 0.824671 13.1091 1.84211V12.1579H2.79328C1.77584 12.1579 0.951172 12.9826 0.951172 14C0.951172 15.0174 1.77584 15.8421 2.79328 15.8421H13.1091V26.1579C13.1091 27.1753 13.9337 28 14.9512 28C15.9686 28 16.7933 27.1753 16.7933 26.1579V15.8421H27.1091C28.1265 15.8421 28.9512 15.0174 28.9512 14C28.9512 12.9826 28.1265 12.1579 27.1091 12.1579H16.7933V1.84211C16.7933 0.824671 15.9686 0 14.9512 0Z"
                                fill="#104651"
                            />
                        </svg>
                        {STRINGS.ADD_JOB}
                    </button>
                    <button
                        type={"submit"}
                        form={"educationHistory"}
                        className="btn btn-md btn-primary"
                    >
                        {STRINGS.SAVE}
                    </button>
                </div>
            </div>
        </div>
    );
}

export default EducationDetails;
