// libs
import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import ReactSelect from "components/ReactSelect";

// widgets
import { Pagination } from "components/Pagination";
import CommonTable from "components/CommonTable";
import CustomButton from "components/CustomButton";
import DeleteModal from "components/DeleteModal";
import SearchBar from "components/SearchBar";
import TableView from "./TableView";
import './style.scss';

// hooks
import useNotification from "hooks/Notifications";

// consts
import { ADMIN_ROUTES, ROUTES_TITLES, SNACK_MESSAGES, SNACK_VARIANT, STRINGS } from "shared/Constants";

// actions
import { deleteMultipleStaff, deleteStaff, getBranchListing, getStaffListing } from "redux/Actions/PharmacyAdminDashboard";
import { Images } from "shared/Images";

const ManageStaff = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotification] = useNotification();
    const profile = useSelector((state) => state.common.profileData);
    const [multipleSelectedData, setMultipleSelectedData] = useState([]);
    const [data, setData] = useState([]);
    const [totalItems, setTotalItems] = useState(0);
    const [itemsPerPage, setItemsPerPage] = useState(15);
    const [activePage, setActivePage] = useState(1)
    const [keyword, setKeyword] = useState(null);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const [deleteStaffData, setDeleteStaffData] = useState({});
    const [branchesData, setBranchesData] = useState([]);
    const [sortData, setSortData] = useState({ name: false, email: false});
    const [appliedFilter, setAppliedFilter] = useState({ branch : ""});
    const [toggleFilter, setToggleFilter] = useState(false);

    // fetch groups & branches
    useEffect(() => {
        fetchData();
        fetchBranchListingCall();
    }, []);

    const handleAddNewStaff = () => {
        history.push({
            pathname: ADMIN_ROUTES.ADD_STAFF
        });
    };

    const toggleDeleteModal = (value) => {
        setShowDeleteModal(prevState => !prevState);
        if (value && !showDeleteModal) {
            setDeleteStaffData(value)
        } else {
            setDeleteStaffData({})
        }
    }

    // fetch staff listing
    const fetchData = useCallback(({ currentPageForPayload = activePage, itemsCountPerPageForPayload = itemsPerPage, orderBy = false, filterType = "", branch = 0 } = {}) => {
        let payload = {
            page: currentPageForPayload,
            search: keyword ? keyword : "",
            limit: itemsCountPerPageForPayload,
            orderBy: orderBy ? 1 : 0,
            filterType,
            branch: branch ?? appliedFilter?.branch?.value
        };
        dispatch(
            getStaffListing(
                payload,
                (res = {}, type) => {
                    setData(res.data);
                    setTotalItems(res.count);
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    }, [dispatch, itemsPerPage, activePage, keyword, sendNotification, appliedFilter]);

    // fetch branch listing
    const fetchBranchListingCall = () => {
        let payload = {
            isJobTimingsRequired : 0
        };
        dispatch(
            getBranchListing(
                payload,
                (res = {}) => {
                    const branchOptions = res?.data?.length ? res?.data.map((branch = {}) => {
                        return { label: branch.branch_name, value: branch.id }
                    }) : [];
                    setBranchesData([...branchOptions]);
                },
                (err) => {
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    }

    // delete staff call
    const deleteStaffCall = (payload) => {
        dispatch(deleteStaff(
            payload,
            (res = {}, type) => {
                setData(prevState => prevState.filter((staff) => staff?.id !== payload?.staffId));
                setTotalItems(prevState => prevState - 1);
                sendNotification({
                    msg: res?.message,
                    variant: SNACK_VARIANT.SUCCESS,
                });
                setShowDeleteModal(false);
                setMultipleSelectedData([]);
            },
            (err) => {
                sendNotification({
                    msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
            },
        ),
        )
    };

    // delete multiple staff call
    const deleteMultipleStaffCall = (payload) => {
        const transformDataToBeDeleted = payload?.map((staff) => staff?.id);
        dispatch(deleteMultipleStaff(
            {ids: transformDataToBeDeleted},
            (res = {}, type) => {
                setData(prevState => prevState.filter((staff) => !transformDataToBeDeleted?.includes(staff?.id)));
                setTotalItems(prevState => prevState - payload?.length);
                sendNotification({
                    msg: res?.message,
                    variant: SNACK_VARIANT.SUCCESS,
                });
                setShowDeleteModal(false);
                setMultipleSelectedData([]);
            },
            (err) => {
                sendNotification({
                    msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
            },
        ),
        )
    };

    const handleChangeKeyword = (e) => {
        setKeyword(e.target.value);
    };

    const handleDelete = () => {
        let arr = [];
        if (multipleSelectedData.length && !deleteStaffData.id) {
            arr = multipleSelectedData.map((i, idx) => {
                return {
                    id: i,
                };
            });
        } else {
            arr = [
                {
                    id: deleteStaffData.id,
                },
            ];
        }
        if(!arr.length) return;
        if (arr.length === 1){
            deleteStaffCall({ staffId: arr[0]?.id });
        }else{
            deleteMultipleStaffCall(arr);
        }
    }

    // set actve page to 1 and call api for searches items
    const resetPageCountAndCallAPI = (itemsCountPerPageForPayload = itemsPerPage) => {
        fetchData({ currentPageForPayload: 1, itemsCountPerPageForPayload })
        setActivePage(1);
        setSortData({});
    }

    const handleItemsPerPageChange = (i) => {
        setItemsPerPage(i);
        resetPageCountAndCallAPI(i)
    }

    const handlePageChange = (i) => {
        setActivePage(i?.selected + 1);
        fetchData({ currentPageForPayload: i?.selected + 1 })
    }

    const handleSearchData = () => {
        resetPageCountAndCallAPI()
    }

    // to sort data server side asc/desc on table heading click
    const handleSortData = (type, value) => {
        fetchData({filterType: type, orderBy: !value });
        setActivePage(1);
        // for dynamic sort up/down icon on table headings
        setSortData((prev) => {
            return {[type]: !value }
        });

    };

    const handleBranchFilterChange = ({ type, option }) => {
        fetchData({ branch: option?.value});
        setSortData({});
        setAppliedFilter((prev) => {
            return { ...prev, [type]: option }
        })
    };

    const handleToggleFilter = (filterState) => {
        if(filterState){
            handleBranchFilterChange({ type: "branch", option: null });
        }
        setToggleFilter((prev) => !prev);
    };

    const tableHeadings = [
        { title: "#", action: () => { } },
        { title: "Staff Name", action: (key, value) => { handleSortData(key, value) }, sortKey : "name" },
        { title: "Branch Name", action: () => { } },
        { title: "Email", action: (key, value) => { handleSortData(key, value) }, sortKey : "email" },
        { title: "Phone Number", action: () => { } },
        { title: "Action ", action: () => { } }
    ];

    return (
            <>
                <div className="d-flex align-items-center flex-wrap position-relative justify-content-between nav-tabs">
                    <h1 className="h6 mb-0">{ROUTES_TITLES.MANAGE_STAFF}</h1>
                    <div className="d-flex justify-content-end admin_action flex-wrap mt-0">
                        {multipleSelectedData.length ? (
                            <>
                                <button
                                    className="btn btn-sm btn-danger ms-2"
                                    onClick={() => { toggleDeleteModal() }}
                                >
                                    {STRINGS.DELETE}
                                </button>
                            </>
                        ) : null}
                        <SearchBar
                            searchAPI={handleSearchData}
                            keyword={keyword}
                            handleChangeKeyword={handleChangeKeyword}
                        />
                        <CustomButton
                            className="ms-2 btn btn-sm btn-secondary"
                            type="button"
                            title={STRINGS.ADD_NEW}
                            onClick={handleAddNewStaff}
                        />
                        <button className="btn btn-xs btn-secondary ms-2" onClick={() => handleToggleFilter(toggleFilter)}>
                            {toggleFilter ?  <img src={Images.CloseWhiteIcon} alt="Filter" width={20} /> :  <img src={Images.FilterIcon} alt="Filter" width={20} />}
                        </button>
                    </div>
                </div>
                <hr className="divider mt-0"/>
                <div className="tab_content pt-0">
                    {toggleFilter && <div className="card filter_btn"><div className="card-body bg-white">
                        <div className="row">
                            <div className="col-md-4 col-sm-6">
                                <ReactSelect
                                    selectedOption={appliedFilter?.branch}
                                    handleChange={(option) => handleBranchFilterChange({ type: "branch", option })}
                                    options={branchesData}
                                    title={"Filter By Branch"}
                                    className="react-select"
                                />
                            </div>
                        </div>
                    </div></div>
                    }
                    <CommonTable>
                        <TableView
                            data={data}
                            multipleSelectedData={multipleSelectedData}
                            setMultipleSelectedData={setMultipleSelectedData}
                            toggleDeleteModal={toggleDeleteModal}
                            sortData={sortData}
                            tableHeadings={tableHeadings}
                            allDataSelected={multipleSelectedData?.length ? multipleSelectedData?.length == data?.length : false}
                        />
                    </CommonTable>
                    <Pagination
                        itemsCount={totalItems}
                        itemsPerPage={itemsPerPage}
                        handlePageChange={handlePageChange}
                        handleItemsPerPageChange={handleItemsPerPageChange}
                        activePage={activePage}
                    />
                </div>

            <DeleteModal
                show={showDeleteModal}
                toggleModal={toggleDeleteModal}
                onClick={handleDelete}
                medium={true}
            />
        </>
    );
};

export default ManageStaff;
