const EditProfile = ({ handleProfileDetailsChange, EditProfileDetails }) => {
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [image, setImage] = useState(EditProfileDetails?.photo ?? Images.DefaultPic)
    const [phoneNumberError, setPhoneNumberError] = useState(false);
    const [imageFile, setImageFile] = useState("");

    const handleImageChange = (e) => {
        setImageFile(e.target.files[0]);
        setImage(URL.createObjectURL(e.target.files[0]))
        handleProfileDetailsChange("photo", e.target.files[0])
    }

    const onSubmit = (data) => {
        console.log(data);
        let payload = { ...EditProfileDetails }
        // if (imageFile) {
        //     payload.photo = imageFile
        // } else {
        //     payload.photo = EditProfileDetails?.photo
        // }
        dispatch(postEditProfileData(
            payload,
            (response) => {
                console.log(response, "responsee");
                dispatch(getPharmacyAdminData())
                sendNotifications({
                    msg: response.message,
                    variant: SNACK_VARIANT.SUCCESS,
                });
            },
            (error) => {
                sendNotifications({
                    msg: error || STRINGS.SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
            }
        ))
    }
    console.log(EditProfileDetails, "EditProfileDetails", typeof EditProfileDetails?.photo, getDocsUrl(EditProfileDetails?.photo))
    useEffect(() => {
        setImage((typeof EditProfileDetails?.photo == "object" ? URL.createObjectURL(EditProfileDetails?.photo) : getDocsUrl(EditProfileDetails?.photo)) || Images.DefaultPic)
    }, [EditProfileDetails.photo])

    return (
        <div className="tab_content">
            <CustomImageUpload image={image} handleImageChange={handleImageChange} />
            <CustomForm
                id={"edit-profile"}
                onSubmit={onSubmit}
                reCaptcha={false}
                formData={REGISTRATION_DETAILS_DATA}
                // phone={EditProfileDetails.phone_number}
                phoneNumberError={phoneNumberError}
                renderSubmitButton={true}
                defaultValues={{ ...EditProfileDetails }}
                handleStateDataChange={handleProfileDetailsChange}
            />
        </div>
    );
};

export default EditProfile;
