import React from "react";
import { STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import ReactHtmlParser from "react-html-parser";
import "./style.scss";
import { getDocsUrl } from "shared/Utilities";

function HeroBanner({ bannerSection = {} }) {
    return (
        <section className="hero_banner">
            <div className="hero_bg">
                <img src={Images.HeroBg} alt="Locumbay" width="300" className="w-100" />
            </div>
            <div className="container">
                <div className="row align-items-center banner_info">
                    <div className="col-md-6">
                        <div className="banner_content">
                            <h1 className="h1">
                                {bannerSection?.section_content
                                    ? ReactHtmlParser(bannerSection?.section_content)
                                    : STRINGS.HEAD_TITLE}
                            </h1>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <figure>
                            <img
                                src={
                                    getDocsUrl(bannerSection?.files?.[0]?.file) || Images.HeroBanner
                                }
                                alt="Locumbay"
                                width={400}
                                className="w-100"
                            />
                        </figure>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default HeroBanner;
