import React from 'react';
import TableHeadItem from './TableHeadItem';
import TableRow from './TableRow';
import { STRINGS } from 'shared/Constants';

//theadData : ["Field1", "Field2", "Field3"];
// tbodyData : [{id: "0", items: [{value: "value1"}, {value: "value2"}, {value: "value3}"]}, {id: "1", items: ["value1", "value2, "value3]}]
// type :  "Super Admin" | "User" | "Pharmasy Admin"
// Note: Data of head and body items should be in same order

const FunctionalTable = ({
  theadData = [],
  tbodyData = [],
  type,
  handleDocumentClick,
  handleActionClick = () => {},
}) => {
    console.log(tbodyData, "tbodyData")
  return (
    <div className="table-responsive">
      <table className="table">
        <thead>
          <tr>
            {theadData?.map((h, i) => {
              return <TableHeadItem key={i} item={h} />;
            })}
          </tr>
        </thead>

        {tbodyData.length ? (
          tbodyData?.map((item) => {
            return (
              <TableRow
                key={item.id}
                data={item.items}
                type={type}
                handleDocumentClick={handleDocumentClick}
                handleActionClick={handleActionClick}
              />
            );
          })
        ) : (
          <tbody>
            <tr>
              <td colspan="6">
                <div className="d-flex align-items-center justify-content-center text-center no_result">
                  {STRINGS.NO_DATA_AVAILABLE}
                </div>
              </td>
            </tr>
          </tbody>
        )}
      </table>
    </div>
  );
};

export default FunctionalTable;
