// libs
import React, { useState } from "react";
import { useHistory } from "react-router-dom";

// widgets
import ButtonStatus from "components/ButtonStatus";
import CheckBoxBody from "components/MultiCheckBox/CheckBoxBody";
import CheckBoxHeader from "components/MultiCheckBox/CheckBoxHeader";
import SortIcon from "components/SortIcon";

// assets
import { Images } from "shared/Images";

// consts
import { ADMIN_BASE_ROUTE, JOB_LIST_VIEW, LOCUM_ADMIN_ROUTES, NA, SORTKEYS, STRINGS, VACANCY_STATUS } from "shared/Constants";
import moment from "moment";
import { VACANCY_TABS } from "../Vacancies/constants";

const TableView = ({
    data = [],
    multipleSelectedData = [],
    setMultipleSelectedData,
    toggleDeleteModal,
    tableHeadings = [],
    allDataSelected = false,
    sortData,
    activeTab = 1,
    onViewClick=()=>{},
    onEditClick=()=>{}
}) => {
    const history = useHistory();
    const [isSelectAll, setIsSelectAll] = useState(allDataSelected || false);

    return (
        <>
            <thead>
                <tr>
                    <th>
                        <CheckBoxHeader
                            isSelectAll={allDataSelected || isSelectAll}
                            setMultipleSelectedData={setMultipleSelectedData}
                            setIsSelectAll={setIsSelectAll}
                            data={data}
                            multipleSelectedData={multipleSelectedData}
                            disabled={!data?.length}
                        />
                    </th>
                    {tableHeadings.map((i, idx) => (
                        <th scope="col" key={idx}>
                            <span> {i.title} </span>
                            {i.sortKey && (
                                <SortIcon
                                    toggleIcon={sortData.key === i.sortKey && SORTKEYS["ascending"] == sortData.direction ? true : false}
                                    onClick={() => i.action(i.sortKey, sortData.key === i.sortKey ? SORTKEYS["ascending"] == sortData.direction ? SORTKEYS["descending"] : SORTKEYS["ascending"] : sortData.direction)}
                                />
                            )}
                        </th>
                    ))}
                </tr>
            </thead>

            {data?.length ? (
                data?.map((value, index) => (
                    <tbody key={index}>
                        <tr>
                            <td width={80}>
                                <CheckBoxBody
                                    setMultipleSelectedData={setMultipleSelectedData}
                                    setIsSelectAll={setIsSelectAll}
                                    value={value}
                                    multipleSelectedData={multipleSelectedData}
                                />
                            </td>
                            <td>{value.id}</td>
                            <td>{value?.branch_name || NA}</td>
                            <td>{value?.job_name || NA}</td>
                            <td>{moment(value?.start_date).format("DD MMM YYYY") || NA}</td>
                            <td>{moment(value?.end_date).format("DD MMM YYYY") || NA}</td>
                            {activeTab !== VACANCY_STATUS.OPEN ? <td>{value?.locum || NA}</td> : ""}
                            <td>{`£${value?.start_rate} - £${value.end_rate}` || NA}</td>
                            {activeTab === VACANCY_STATUS.OPEN ? <td>{value?.offered ?? NA}</td> : ""}
                            <td width={200}>
                                <div className="d-flex btn_group">
                                    <ButtonStatus
                                        image={Images.EyeIcon}
                                        title={"Delete"}
                                        status={"secondary"}
                                        onClick={() => {
                                            onViewClick(value)
                                        }}
                                    />

                                    {activeTab === VACANCY_STATUS.OPEN ?
                                        <ButtonStatus
                                            image={Images.EditIcon}
                                            title={""}
                                            // status={"Verified"}
                                            onClick={() => {
                                                onEditClick(value);
                                            }}
                                        /> : ""
                                    }
                                    { (activeTab === VACANCY_STATUS.OPEN || activeTab === VACANCY_STATUS.ONGOING) ? <ButtonStatus
                                        image={Images.TrashIcon}
                                        title={"Delete"}
                                        status={"danger"}
                                        onClick={() => {
                                            toggleDeleteModal(value);
                                        }}
                                    /> : ""
                                    }
                                </div>
                            </td>
                        </tr>
                    </tbody>
                ))
            ) : (
                <tbody>
                    <tr>
                        <td
                            colSpan={tableHeadings.length + 1}
                            className="text-center py-5"
                        >
                            {STRINGS.NO_DATA_AVAILABLE}
                        </td>
                    </tr>
                </tbody>
            )}
        </>
    );
};

export default TableView;
