import "./styles.scss";

function CustomLoader({ visible = false, children }) {
    return (
        <>
            <div className={`${visible ? "loader_main" : ""}`}>
                {visible ? (
                    <>
                        <span className="loader"></span>
                    </>
                ) : null}
            </div>
            {children}
        </>
    );
}

export default CustomLoader;
