import "./styles.scss";

import { Images } from "shared/Images";
import Modal from "react-modal";
import React from "react";

function CustomModal(
    {
        children,
        className = "",
        handleToggle = () => { },
        isOpen = false,
        large = false,
        medium = false,
        small = false,
        avarge = false,
        xLarge = false,
        style = {},
        clearHandler = () => { },
        modalOpacity = false,
        document = false,
        bodyClassname = "modal-body"
    },
    props,
) {
    return (
        <>
            <Modal
                isOpen={isOpen}
                onRequestClose={handleToggle}
                className={`Modal react_modal_main`}
                overlayClassName="Overlay"
                ariaHideApp={false}
            >
                <div
                    className={`modal-dialog modal-dialog-centered ${large
                            ? `modal-lg`
                            : medium
                                ? `modal-md`
                                : avarge
                                    ? `modal-mds`
                                    : small
                                        ? `modal-xs`
                                        : xLarge
                                            ? `modal-xl`
                                            : "modal-md"
                        }`}
                >
                    <div className="modal-content" hidden={modalOpacity ? modalOpacity : false}>
                        {document && <div className="modal-header"><h6 className="h6 mb-0">Document</h6></div>}
                        <button
                            className="btn-close btn-transparent"
                            onClick={() => {
                                handleToggle();
                                clearHandler();
                            }}
                        >
                            <img src={Images.CloseIcon} alt="Close" width="24" />
                        </button>
                        <div className={`${bodyClassname}`}>{children}</div>
                    </div>
                </div>
            </Modal>
        </>
    );
}

export default CustomModal;
