import React from "react";
import { INPUT_TYPES, STRINGS } from "shared/Constants";
import "./styles.scss";
function CustomCheckBox({ checkBoxData = [], onCheckboxSelect = () => {}, checkbox = "false" }) {
    const checkBoxClass = checkbox ? "col-md-3 mt-2" : "col-md-3 mt-2";
    return (
        <>
            {checkBoxData.length ? (
                checkBoxData.map((checkBoxItem, checkboxIndex) => (
                    <div className={checkBoxClass} key={checkboxIndex}>
                        <div key={checkboxIndex} className="checkbox mb-3">
                            <input
                                id={checkBoxItem.id}
                                checked={checkBoxItem.value}
                                type={INPUT_TYPES.CHECKBOX}
                                onChange={onCheckboxSelect}
                            />
                            <label>{checkBoxItem.title}</label>
                        </div>
                    </div>
                ))
            ) : (
                <div className="d-flex align-items-center justify-content-center text-center no_result">
                    {STRINGS.NOT_FOUND}
                </div>
            )}
        </>
    );
}

export default React.memo(CustomCheckBox);
