import React from 'react'
import TableHeadItem from './TableHeadItem';
import TableRow from './TableRow';
import "./style.scss"

//theadData : ["Field1", "Field2", "Field3"];
// tbodyData : [{id: "0", items: ["value1", "value2", "value3"]}, {id: "1", items: ["value1", "value2, "value3]}]
// Note: Data of head and body items should be in same order

const BasicTable = ({ theadData, tbodyData }) => {
    return (
        <div className='table-responsive'>
            <table className="table mb-0">
                <thead>
                    <tr>
                        {theadData.map((h, i) => {
                            return <TableHeadItem key={i} item={h} />;
                        })}
                    </tr>
                </thead>

                {tbodyData.map((item) => {
                    return <TableRow key={item.id} data={item.items} />;
                })}

            </table>
        </div>
    );
}

export default BasicTable