
import React from "react";
import { splice } from "shared/Utilities";

function CheckBoxBody({
  multipleSelectedData,
  value,
  setMultipleSelectedData = () => {},
  setIsSelectAll = () => {},
}) {
  return (
    <div className="checkbox">
      <input
        type="checkbox"
        checked={multipleSelectedData.includes(value?.id)}
        onChange={(e) => {
          if (e.target.checked) {
            setMultipleSelectedData((prev) => [...prev, value?.id]);
          } else {
            setMultipleSelectedData(splice(multipleSelectedData, value?.id));
            setIsSelectAll(false);
          }
        }}
      />
      <label></label>
    </div>
  );
}

export default CheckBoxBody;
