import React, { useEffect, useState } from "react";
import { STRINGS } from "shared/Constants";
import TabNavigation from "components/CustomTabNavigator";
import EditProfile from "./EditProfile";
import PharmacyInfo from "./PharamacyInfo";
import { getDefaultDateString } from "../constants";
import { useSelector } from "react-redux";

export const PROFILE_TABS = [
    { id: STRINGS.EDIT_PROFILE, title: STRINGS.EDIT_PROFILE },
    { id: STRINGS.PHARMACY_INFO, title: STRINGS.PHARMACY_INFO },
];

const RenderTabs = ({ activeTab, handleProfileDetailsChange, EditProfileDetails, handlePharmacyDetailsChange, pharmacyInfoData, templateData, handleTemplateDataChange }) => {
    switch (activeTab) {
        case (STRINGS.EDIT_PROFILE): {
            return <EditProfile EditProfileDetails={EditProfileDetails} handleProfileDetailsChange={handleProfileDetailsChange} />
        }
        case (STRINGS.PHARMACY_INFO): {
            return <PharmacyInfo pharmacyInfoData={pharmacyInfoData} handlePharmacyDetailsChange={handlePharmacyDetailsChange} />
        }
        default: <EditProfile />
    }
}

const AdminManageProfile = () => {
    const [activeTab, setActiveTab] = useState(PROFILE_TABS[0].id)
    const profile = useSelector(state => state.common.profileData)
    console.log(profile, "proffiellee")
    const [EditProfileDetails, setEditProfileDetails] = useState({
        first_name: "",
        last_name: "",
        email: "",
        phone_number: "",
        description: ""
    })
    const [pharmacyInfoData, setPharmacyInfoData] = useState({
        pharmacy_name: "",
        pharmacy_email: "",
        license_number: "",
        address: "",
        country: "",
        city: "",
        description: "",
        csInUse: []
    })

    const handleProfileDetailsChange = (field, data) => {
        if (field) {
            setEditProfileDetails(prevState => {
                let newState = { ...prevState };
                newState[field] = data;
                return newState;
            })
        }
    }

    const handlePharmacyDetailsChange = (field, data) => {
        if (field) {
            setPharmacyInfoData(prevState => {
                let newState = { ...prevState };
                newState[field] = data;
                return newState;
            })
        }
    }

    const onTabPress = (e) => {
        setActiveTab(e.target.id);
    };

    useEffect(() => {
        setEditProfileDetails({
            first_name: profile?.owner_info?.first_name || "",
            last_name: profile?.owner_info?.last_name || "",
            email: profile?.owner_info?.email || "",
            phone_number: profile?.owner_info?.phone_number || "",
            description: profile?.owner_info?.description || "",
            photo: profile?.owner_info?.photo || ""
        })
        setPharmacyInfoData({
            pharmacy_name: profile?.pharmacy_info?.pharmacy_name || "",
            pharmacy_email: profile?.pharmacy_info?.basic_info?.pharmacy_email || "",
            license_number: profile?.pharmacy_info?.basic_info?.license_number || "",
            address: profile?.pharmacy_info?.basic_info?.address || "",
            country: profile?.pharmacy_info?.basic_info?.country || "",
            city: profile?.pharmacy_info?.basic_info?.city || "",
            description: profile?.pharmacy_info?.basic_info?.description || "",
            postal_code: profile?.pharmacy_info?.basic_info?.postal_code || "",
            csInUse: profile?.pharmacy_info?.csInUse || [],
            documents: profile?.pharmacy_info?.documents || [],
            license_valid_till: getDefaultDateString(profile?.pharmacy_info?.license_valid_till) || "",
            insurance_valid_till: getDefaultDateString(profile?.pharmacy_info?.insurance_valid_till) || "",
        })
    }, [])

    return (
        <main className="main">
            <div className="inner_content">
                <div className="container">
                    <div className="position-relative">
                        <TabNavigation
                            onClick={onTabPress}
                            tabsData={PROFILE_TABS}
                            activeTab={activeTab}
                        />
                        <RenderTabs
                            activeTab={activeTab}
                            EditProfileDetails={EditProfileDetails}
                            handleProfileDetailsChange={handleProfileDetailsChange}
                            pharmacyInfoData={pharmacyInfoData}
                            handlePharmacyDetailsChange={handlePharmacyDetailsChange}
                        />
                    </div>
                </div>
            </div>
        </main>
    );
};

export default AdminManageProfile;
