import {
    FROM_VERIFICATION_CODE,
    LOGIN,
    SET_DATA,
    START_LOADING,
    STOP_LOADING,
    CLEAR_DATA,
    LOCUM_PROFILE_DETAILS,
} from "../Actions/Auth";

import { ACTION_STATES } from "../Actions/ActionStates";

const initialState = {
    loading: false,
    fromVerficationCode: false,
    profileData: {},
    locumProfileDetails:{}
};
const commonReducer = (state = initialState, action) => {
    switch (action.type) {
        case START_LOADING: {
            return {
                ...state,
                loading: true,
            };
        }
        case STOP_LOADING: {
            return {
                ...state,
                loading: false,
            };
        }
        case FROM_VERIFICATION_CODE: {
            return {
                ...state,
                fromVerficationCode: action.value,
            };
        }
        case LOCUM_PROFILE_DETAILS: {
            return {
                ...state,
                locumProfileDetails: {...action.locumProfileDetails},
            };
        }   
        case SET_DATA: {
            return {
                ...state,
                profileData: { ...action.profileData },
            };
        }
        case CLEAR_DATA: {
            return {
                ...state,
                profileData: {},
            };
        }
        default: {
            return state;
        }
    }
};

export default commonReducer;
