import React, { useCallback, useEffect } from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import "./style.scss";

import Ratingstars from "components/Rating";
import useNotification from "hooks/Notifications";
import {
    ADMIN_ROUTES,
    NA,
    PHARMACY_USER_VIEW,
    ROUTES,
    SNACK_MESSAGES,
    SNACK_VARIANT,
    STRINGS,
} from "shared/Constants";
import { Images } from "shared/Images";
import GoogleMap from "components/googleMap";
import { getVacancyDetails } from "redux/Actions/PharmacyAdminDashboard";
import { convertTimeToAmPm, getDatefromDayOfWeek, timeAgo } from "shared/Utilities";
import useGetIdFromPath from "hooks/Utilities/useGetIdFromPath";
import CustomButton from "components/CustomButton";
import { viewJob } from "redux/Actions/LocumWebPanel";
import { USER_TYPE } from "shared/Constants";
import ReadMore from "components/ReadMore";

function JobView({ onSucess = () => {}, type = USER_TYPE.PHARMACY  , refreshData =false}) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [viewData, setViewData] = useState({});
    const [msg, sendNotification] = useNotification();
    const id = useGetIdFromPath();

    const ratingData = [
        {
            title: PHARMACY_USER_VIEW.JOB_VIEW.STAFF_LEVELS,
            rating: 2,
        },
        {
            title: PHARMACY_USER_VIEW.JOB_VIEW.KNOWLEDGE,
            rating: 4,
        },
        {
            title: PHARMACY_USER_VIEW.JOB_VIEW.SUPPORT,
            rating: 2,
        },
        {
            title: PHARMACY_USER_VIEW.JOB_VIEW.ACCESSIBILITY,
            rating: 3,
        },
        {
            title: PHARMACY_USER_VIEW.JOB_VIEW.PAYMENT,
            rating: 5,
        },
        {
            title: PHARMACY_USER_VIEW.JOB_VIEW.OVERALL,
            rating: 4,
        },
    ];

    const viewUser = () => {
        // Creating an object with the id of the current invoice to be retrieved.
        let data = { id: id };
        // Dispatching an API call to retrieve the invoice details from the database.
        dispatch(
            viewJob(data, (res) => {
                // If the retrieval is successful, updating the state variable with the invoice details.
                onSucess(res?.data);
                setViewData(res?.data);
            }),
            (err) => {
                // If the retrieval is unsuccessful, displaying an error notification message and navigating back to the previous page.
                sendNotification({
                    msg: err?.data?.message
                        ? err.data.message
                        : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                    variant: SNACK_VARIANT.ERROR,
                });
                history.goBack();
            },
        );
    };

    const handleViewMoreClick = () => {
        history.push(ADMIN_ROUTES.CANDIDATE_LIST);
    };

    // useEffect hook to retrieve the invoice details on page load.
    useEffect(() => {
        if (id) {
            viewUser();
        }
    }, [id,refreshData]);

    return (
        <>
            <div className="row justify-content-center DetailRow">
                <div className="col-xxl-8 col-lg-7 left_card">
                    <div className="DetailRow_card">
                        <div className="card mb-4 job_detail">
                            <div className={`card-body ${type === USER_TYPE.LOCUM ? "bg-light-green" : "bg-white"}`}>
                                <h2 className="h5">
                                    {`${viewData?.job_name} Job Vacancy in ${viewData?.pharmacy_name}`}
                                </h2>
                                <small className="text-small">
                                    {PHARMACY_USER_VIEW.JOB_VIEW.JOB_ID}
                                    {` - #${viewData?.id} -  `}
                                    {`${
                                        PHARMACY_USER_VIEW.JOB_VIEW.POSTED
                                    } ${timeAgo(viewData?.created_at)}`}
                                </small>

                                <span className="d-block job_location mt-2 mb-3">
                                    <em className="me-2">
                                        <img
                                            width={21}
                                            height={27}
                                            style={{ fill: "Green" }}
                                            src={Images.LocationIcon}
                                            alt="Location"
                                        />
                                    </em>
                                    {`${viewData?.branch_address?.branch_name} ${viewData?.branch_address?.address} ${viewData?.branch_address?.city}, ${viewData?.branch_address?.state} ${viewData?.branch_address?.country} ${viewData?.branch_address?.postal_code}`}
                                </span>

                                {viewData?.description ? (
                                    <ReadMore numberOfCharacters={580}>
                                        {viewData?.description}
                                    </ReadMore>
                                ) : null}
                            </div>
                        </div>

                       <div className="card DetailRow_card_block my-3">
                            <div className={`job_timing card-body ${type === USER_TYPE.LOCUM ? "bg-light-green" : "bg-white"}`}>
                                <h5 className="h5 text-capitalize">Job Timing</h5>
                                <div className="common_collapse_card mt-3">
                                    {viewData?.shift_details?.map((value, index) => {
                                        return (
                                            <div id={index} class={`card ${type === USER_TYPE.LOCUM ? "card_white" : "card_light_green"} mb-3`}>
                                                <div
                                                    class="card-header border-bottom-0"
                                                    id="headingOne"
                                                >
                                                    <div className="d-flex flex-md-row flex-column align-items-md-center align-items-start">
                                                        <h6 class="mb-0 h6 col-md-2 shift_day">
                                                            {getDatefromDayOfWeek(
                                                                viewData?.start_date,
                                                                value?.day,
                                                            )}
                                                        </h6>
                                                        <div className="d-flex flex-sm-row flex-column align-items-md-center align-items-start col-md-10 shift_time">
                                                            <span className="me-md-5 me-3">
                                                                {`Time: ${
                                                                    value?.start_time
                                                                        ? convertTimeToAmPm(
                                                                              value?.start_time,
                                                                          )
                                                                        : NA
                                                                } - ${
                                                                    value?.end_time
                                                                        ? convertTimeToAmPm(
                                                                              value?.end_time,
                                                                          )
                                                                        : NA
                                                                }`}
                                                            </span>
                                                            <span>
                                                                {`Lunch: ${
                                                                    value?.lunch_start_time
                                                                        ? convertTimeToAmPm(
                                                                              value?.lunch_start_time,
                                                                          )
                                                                        : NA
                                                                } - ${
                                                                    value?.lunch_end_time
                                                                        ? convertTimeToAmPm(
                                                                              value?.lunch_end_time,
                                                                          )
                                                                        : NA
                                                                }`}
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        </div>

                         {/* Map */}
                        <div className="google_map my-4">
                            <GoogleMap
                            // address={location.address}
                            // lat={location.lat}
                            // lng={location.lng}
                            />
                        </div>
                    </div>
                </div>
                {/* left ends */}
                {/* right begins */}
                <div className="col-xxl-4 col-lg-5 right_card mt-lg-0 my-3">
                    <div className="DetailRow_card">
                        <div className="card mb-4 DetailRow_card_block ">
                            <div className="card-body job_body bg-white">
                                <div className="card card_light_green mb-3">
                                    <h5 className="card-title h6">Rate</h5>
                                    <div className="card-body ">
                                        <h6>{`£${viewData?.start_rate}-£${viewData?.end_rate} / hr`}</h6>
                                    </div>
                                </div>

                                <div className="card card_light_green mb-3">
                                    <h5 className="card-title h6">
                                        {PHARMACY_USER_VIEW.JOB_VIEW.PHARMACIST}
                                    </h5>
                                    <div className="card-body">
                                        <h6>{viewData?.branch_address?.branch_name}</h6>
                                        <span>{viewData?.branch_address?.postal_code}</span>
                                    </div>
                                </div>

                                <div className="card DetailRow_card_block">
                                    <h5 className="card-title h6">
                                        {PHARMACY_USER_VIEW.JOB_VIEW.PROFESSIONAL_DETAILS}
                                    </h5>
                                    <div className="card-body">
                                        <>
                                            {ratingData?.map((i, idx) => {
                                                return (
                                                    <div className="d-flex justify-content-md-between align-items-md-center align-items-start ratingBlock">
                                                        <span className="h6 ratingBlock_title">
                                                            {i.title}{" "}
                                                        </span>
                                                        <span className="ratingBlock_value">
                                                            <Ratingstars rating={i?.rating || 3} />
                                                        </span>
                                                    </div>
                                                );
                                            })}
                                        </>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {type === USER_TYPE.PHARMACY ? (
                            <div className="card">
                                <div className="card-body DetailRow_card_block bg-white">
                                    <h5 className="card-title h6">
                                        {PHARMACY_USER_VIEW.JOB_VIEW.JOB_ACTIVITIES}
                                    </h5>
                                    <div className="card-body">
                                        <>
                                            {PHARMACY_USER_VIEW.JOB_ACTIVITIES?.map((i, idx) => {
                                                return (
                                                    <div className="d-flex justify-content-md-between align-items-md-center align-items-start ratingBlock">
                                                        <span className="h6 ratingBlock_title">
                                                            {i.LABEL}{" "}
                                                        </span>
                                                        <span className="ratingBlock_value">
                                                            {viewData?.jobs_activates?.[i.KEY] ?? 0}
                                                        </span>
                                                    </div>
                                                );
                                            })}
                                            <CustomButton
                                                title={STRINGS.VIEW_MORE}
                                                className="btn btn-md w-100 btn-secondary m-1 text-center"
                                                onClick={handleViewMoreClick}
                                            />
                                        </>
                                    </div>
                                </div>
                            </div>
                        ) : null}
                    </div>
                </div>
                {/* right ends */}
            </div>
        </>
    );
}

export default JobView;
