import React, { useEffect, useState } from "react";
import Header from "components/Header";
import Invoice from "./Invoice";
import { PROFILE_TABS } from "../constants";
import ProfileVerification from "./ProfileVerification";
import RegistrationDetails from "./RegistrationDetails";
import { SNACK_VARIANT, STRINGS } from "shared/Constants";
import TabNavigation from "components/CustomTabNavigator";
import UploadCv from "./CV";
import "./styles.scss";
import { HEADER_DATA } from "views/HomePage/constants";
import Footer from "components/Footer/Footer";
import {
    fromVerificationCode,
    getAccountDetails,
    getCsInUse,
    getCvData,
    getProfile,
    getProfileVerification,
} from "redux/Actions/Auth";
import useNotification from "hooks/Notifications";
import { useDispatch } from "react-redux";
import { STATUS_CODES } from "services/Api/Constants";

function ManageProfile(props) {
    const [msg, sendNotifications] = useNotification();
    const dispatch = useDispatch();
    const [activeTab, setActiveTab] = useState(STRINGS.REGISTRATION);
    const [defaultDataCS, setDefaultDataCS] = useState([]);
    const [verificationData, setVerificationData] = useState({});
    const [accountDetails, setAccountDetails] = useState({});
    const [userDetails, setUserDetails] = useState({});
    const [cvData, setCvData] = useState({});
    const onTabPress = (e) => {
        setActiveTab(e.target.id);
    };
    const getCsInUseTab = () => {
        dispatch(
            getCsInUse(
                (response) => {
                    setDefaultDataCS(response.data);
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const getAccountDetailsTab = () => {
        dispatch(
            getAccountDetails(
                (response) => {
                    setAccountDetails(response.data);
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const getProfileVerificationTab = () => {
        dispatch(
            getProfileVerification(
                (response) => {
                    setVerificationData(response.data);
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const getManageCvData = () => {
        dispatch(
            getCvData(
                (response) => {
                    //    console.log(response);
                    setCvData(response.data);
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const getProfileData = () => {
        dispatch(
            getProfile(
                (response) => {
                    setUserDetails(response.data);
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    useEffect(() => {
        dispatch(fromVerificationCode(false));
        getCsInUseTab();
    }, []);
    // Renders active tab component to the screen
    return (
        <main className="main">
            <div className="container">
                <div className="inner_content">
                    <div className="profile_sec">
                        <h1 className="h6 mb-4">{STRINGS.MANAGE_PROFILE}</h1>
                        <TabNavigation
                            onClick={onTabPress}
                            tabsData={PROFILE_TABS}
                            activeTab={activeTab}
                        />
                        <RenderTab
                            activeTab={activeTab}
                            userDetails={userDetails}
                            accountDetails={accountDetails}
                            verificationData={verificationData}
                            csInUse={defaultDataCS}
                            cv_data={cvData}
                            setUserDetails={setUserDetails}
                            setAccountDetails={setAccountDetails}
                            setVerificationData={setVerificationData}
                            setDefaultDataCS={setDefaultDataCS}
                            setCvData={setCvData}
                        />
                    </div>
                </div>
            </div>
            <Footer />
        </main>
    );
}

export default ManageProfile;
const RenderTab = ({
    activeTab,
    userDetails,
    accountDetails,
    verificationData,
    csInUse,
    cv_data,
    setUserDetails = () => { },
    setAccountDetails = () => { },
    setVerificationData = () => { },
    setDefaultDataCS = () => { },
    setCvData = () => { },
}) => {
    switch (activeTab) {
        case STRINGS.REGISTRATION:
            return (
                <RegistrationDetails userDetails={userDetails} setUserDetails={setUserDetails} />
            );
        case STRINGS.INVOICE:
            return (
                <Invoice accountDetails={accountDetails} setAccountDetails={setAccountDetails} />
            );
        case STRINGS.PROFILE_VERIFICATION:
            return (
                <ProfileVerification
                    csInUse={csInUse}
                    verificationData={verificationData}
                    setVerificationData={setVerificationData}
                    setDefaultDataCS={setDefaultDataCS}
                />
            );
        case STRINGS.CV:
            return <UploadCv cv_data={cv_data} setCvData={setCvData} />;
        default:
            return (
                <RegistrationDetails userDetails={userDetails} setUserDetails={setUserDetails} />
            );
    }
};
