import React, { useCallback, useEffect, useState } from "react";
import { BUTTON_TEXT, VERIFY_TOOLTIP, STRINGS, PROFILE_TABLE } from "shared/Constants";
import { LOCUM_USER, PHARMACY_USER } from "shared/TableData";
import { Tooltip, Fade } from "@mui/material";
import CustomFileSelectButton from "components/CustomFileSelectButton";
import TableRow from "./TableRow";

function CustomTable({
    data = [],
    activeTab,
    handleUpload = () => {},
    onDeleteClick = () => {},
    onClick = () => {},
    onRowDeleteClick = () => { },
    onInputChange = () => { },
    onValidTillChange = () =>{},
    tableDefaultRows={}
}) {
    const [statusView, setStatusview] = useState(false);
    const [headerLabel, setHeaderLabel] = useState({});

    // Change heading title for table according to tabs
    const onTabChange = useCallback(() => {
        if (activeTab === STRINGS.PROFILE_TABLE) {
            setHeaderLabel(PROFILE_TABLE);
        } else {
            setHeaderLabel(LOCUM_USER);
        }
    }, [setHeaderLabel, activeTab]);

    useEffect(() => {
        onTabChange();
    }, [activeTab, onTabChange]);

    return (
        <table className="table">
            <thead>
                <tr key={1000 + 1}>
                    {Object.values(headerLabel).map((i, idx) => (
                        <th scope="col" id={idx} key={idx}>
                            {i}
                        </th>
                    ))}
                </tr>
            </thead>
            {data.map((value, index) => (
                <tbody key={index}>
                    <TableRow
                        value={value}
                        index={index}
                        handleUpload={handleUpload}
                        onDeleteClick={onDeleteClick}
                        onClick={onClick}
                        onRowDeleteClick={onRowDeleteClick}
                        onInputChange={onInputChange}
                        tableDefaultRows={tableDefaultRows}
                        onValidTillChange={onValidTillChange}
                    />
                </tbody>
            ))}
        </table>
    );
}

export default CustomTable;
